<template>
  <div class="subVideo">
    <video ref="subVideo" src=""></video>
    <div class="controls">
      <v-btn icon> <v-icon>mdi-microphone</v-icon></v-btn>
      <v-btn icon> <v-icon>mdi-pause</v-icon></v-btn>
      <v-btn icon> <v-icon>mdi-stop</v-icon></v-btn>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    stream: {
      required: true,
    },
  },
  methods: {
    Initialize() {
      this.$refs.subVideo.srcObject = this.stream;
      this.$refs.subVideo.addEventListener("loadedmetadata", () => {
        this.$refs.subVideo.play();
      });
    },
  },
  mounted() {
    this.Initialize();
  },
};
</script>

