<template>
  <div class="">
    <div class="h-100" id="">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            My courses /
            <span class="main--text"
              ><v-icon class="main--text text-h4 ml-3"
                >mdi-book-open-variant</v-icon
              ></span
            >
          </h3>
          <div class="">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 d-block d-md-inline my-3 py-3 mr-md-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
          </div>
        </div>
        <div class="mt-5">
          <v-card>
            <v-card-title>
              Courses
              <v-spacer></v-spacer>
              <v-text-field
                v-model="data.search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :loading="data.loading"
              :loading-text="'chargement...'"
              :headers="headers"
              :items="ProfessorCoursesState"
              :search="data.search"
            >
              <template v-slot:item.departments="{ item }">
                <td>
                  <ul>
                    <li v-for="i in item.other_informations.departments" :key="i._id">
                      {{ i.name }}
                    </li>
                  </ul>
                </td>
              </template>
              <template v-slot:item.action="{ item }">
                <td>
                  <router-link
                    :to="'/professor/course/' + item._id"
                    class="rounded-circle white--text px-2 py-3 mr-3"
                    ><v-icon class="green--text">mdi-arrow-right</v-icon>
                  </router-link>
                </td>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { defaultData } from "../../utils/data";

export default {
  computed: {
    ...mapGetters(["ProfessorCoursesState", "AdminProfilState", "ErrorBag"]),
  },
  methods: {
    ...mapActions(["getProfessorCourses"]),

    async Refresh() {
      this.data.loading = true;
      try {
        await this.getProfessorCourses();
        this.$store.dispatch("setSnackbar", {
          message: "Courses refresh successfully",
          showing: true,
        });
      } catch (error) {
        this.$store.dispatch("setSnackbar", {
          message: this.ErrorBag.default,
          showing: true,
        });
      }
      this.data.loading = false;
    },
    async Initialize() {
      this.data.loading = true;
      try {
        await this.getProfessorCourses();
      } catch (error) {
        this.$store.dispatch("setSnackbar", {
          message: this.ErrorBag.default,
          showing: true,
        });
      }
      this.data.loading = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      data: defaultData,

      headers: [
        {
          text: "Name",
          value: "name",
        },
        { text: "Code", value: "code" },
        { text: "Level", value: "level" },
        { text: "Departments", value: "departments" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
