<template>
  <div v-if="link">
    <v-tabs
      v-model="tab"
      class="mb-3"
      color="blue"
      background-color="white"
      centered
      icons-and-text
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab v-if="file.media" href="#stream-video">
        Stream video
        <v-icon>mdi-view-stream-outline</v-icon>
      </v-tab>

      <v-tab v-if="file.media" href="#change-video">
        Change video
        <v-icon>mdi-video-plus-outline</v-icon>
      </v-tab>

      <v-tab v-if="!file.media" href="#change-video">
        Upload video
        <v-icon>mdi-video-plus-outline</v-icon>
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item :value="'stream-video'">
        <v-col cols="12" class="video">
          <video controls :src="file.media"></video>
        </v-col>
      </v-tab-item>
      <v-tab-item :value="'change-video'">
        <v-col class="bg-tkp rounded uploadVideoContainer" cols="12">
          <div class="uploadVideo" v-if="!inProgress">
            <input type="file" ref="dropzone" @change="uploadVideo" class="" />
            <div class="callToAction d-flex justify-center align-center">
              <div class="text-center">
                <div class="red--text mb-5">{{ errors }}</div>
                <v-icon class="text-h4">mdi-file-video-outline</v-icon>
                <p class="mb-0 mt-4">Choose or Drag your video here</p>
              </div>
            </div>
          </div>
          <div v-else class="d-flex justify-center align-center">
            <div>
              <v-progress-circular
                :rotate="-90"
                :size="100"
                :width="15"
                :value="progress"
                color="primary"
              >
                {{ progress }}%
              </v-progress-circular>
              <div class="black--text text-center mt-2 main--text">
                {{ progressMessage }}
              </div>
            </div>
          </div>
        </v-col>
      </v-tab-item>
    </v-tabs-items>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: {
    link: {},
    file: {},
  },
  methods: {
    async uploadVideo() {
      const video = this.$refs.dropzone.files[0];
      const formData = new FormData();
      if (video.type != "video/mp4")
        return (this.errors = "Only mp4 video are supported");
      formData.append("video", video);
      this.inProgress = true;
      this.progressMessage = "In progress";
      try {
        await axios.put(`${this.link}`, formData, {
          onUploadProgress: (e) =>
            (this.progress = Math.round((e.loaded * 100) / e.total)),
        });
      } catch (err) {
        this.progress = 0;
        this.inProgress = false;
        this.message = "failed to uploade this video";
        this.snackbar = true;
        return (this.errors = "Failed to upload this video");
      }
      this.progressMessage = "Done";
      setTimeout(() => {
        this.inProgress = false;
        this.message = "Video uploaded successfully";
        this.snackbar = true;
        this.$emit("refresh");
        this.tab = "stream-video";
      }, 1000);
    },
  },
  data() {
    return {
      tab: null,
      snackbar: false,
      message: "",

      errors: "",
      chapiter: {},
      progress: 0,
      inProgress: false,
      progressMessage: "",
    };
  },
};
</script>

<style></style>
