<template>
  <div class="h-100 bg-tkp">
    <div v-if="DepartmentsState.list">
      <div class="container">
        <!-- {{ ProgramsState }} -->
        <div class="d-flex justify-space-between my-4">
          <h3 class="mb-3">School Times Table</h3>
          <v-btn class="main white--text rounded" @click="addProgram"> Add</v-btn>
        </div>
        <div class="d-flex">
          <v-select
            class="mx-3"
            :items="levels"
            v-model="level"
            @change="collectProgram"
            dense
            label="Levels"
            outlined
          ></v-select>
          <v-select
            dense
            class="mx-3"
            :items="DepartmentsState.list"
            @click="Initialize, getEvent"
            @change="collectProgram"
            v-model="department"
            label="Department"
            outlined
          ></v-select>
          <v-btn @click="collectProgram">Refresh</v-btn>
        </div>
      </div>

      <v-row v-if="loading == false && department" class="fill-height px-md-6 rounded">
        <v-col>
          <v-sheet height="64">
            <v-toolbar flat>
              <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
                Today
              </v-btn>
              <v-btn fab text small color="grey darken-2" @click="prev">
                <v-icon small> mdi-chevron-left </v-icon>
              </v-btn>
              <v-btn fab text small color="grey darken-2" @click="next">
                <v-icon small> mdi-chevron-right </v-icon>
              </v-btn>
              <v-toolbar-title v-if="$refs.calendar">
                {{ $refs.calendar.title }}
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-menu bottom right>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn outlined color="grey darken-2" v-bind="attrs" v-on="on">
                    <span>{{ typeToLabel[type] }}</span>
                    <v-icon right> mdi-menu-down </v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item @click="type = 'day'">
                    <v-list-item-title>Day</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="type = 'week'">
                    <v-list-item-title>Week</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="type = 'month'">
                    <v-list-item-title>Month</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="type = '4day'">
                    <v-list-item-title>4 days</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </v-toolbar>
          </v-sheet>
          <v-sheet height="600">
            <v-calendar
              ref="calendar"
              v-model="focus"
              color="primary"
              :events="events"
              :event-color="getEventColor"
              :type="type"
              @click:event="showEvent"
              @click:more="viewDay"
              @click:date="viewDay"
            ></v-calendar>
          </v-sheet>
        </v-col>
      </v-row>
      <circulate-load v-else-if="loading" />
      <div class="text-center text-h5" v-else>
        <p>Please select department to continue</p>
      </div>
    </div>

    <v-dialog class="" v-model="Dialog" max-width="50rem">
      <div>
        <v-form ref="form" v-model="valid" lazy-validation class="white container">
          <v-row>
            <v-col cols="12" md="6">
              <h3 class="mt-3">{{ ModalText.title }}</h3>
              <p class="red--text">{{ errors }}</p>
              <v-radio-group
                class=""
                v-if="!program._id"
                row
                v-model="program.type"
                mandatory
              >
                <v-radio label="Course" value="course"></v-radio>
                <v-radio label="Exams" value="exam"></v-radio>
              </v-radio-group>
              <v-select
                v-model="program.course"
                :items="CoursesState.list"
                label="Select the course"
                :rules="[(v) => !!v || 'courses is required']"
                dense
                @click="getCoursesList"
                outlined
              ></v-select>
              <v-menu
                v-if="program.type == 'exam'"
                ref="timeMenu"
                v-model="timeMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="program.time"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="program.time"
                    label="Time"
                    prepend-icon="mdi-clock-time-four-outline"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="timeMenu"
                  v-model="program.time"
                  full-width
                  @click:minute="$refs.timeMenu.save(program.time)"
                ></v-time-picker>
              </v-menu>
              <v-color-picker
                v-model="program.color"
                v-if="program.type == 'course'"
                class="ma-2"
                hide-canvas
              ></v-color-picker>
            </v-col>
            <v-col cols="12" md="5">
              <v-date-picker
                v-if="program.type == 'course'"
                v-model="program.dates"
                range
              ></v-date-picker>
              <v-date-picker v-else v-model="program.date"></v-date-picker>
            </v-col>
          </v-row>

          <v-btn
            @click="saveProgram"
            :disabled="!valid"
            :loading="loading"
            class="main white--text"
            >{{ ModalText.btn }}</v-btn
          >
          <v-btn
            @click="deleteProgram(program._id)"
            :disabled="!valid"
            v-if="program._id"
            :loading="loading"
            class="red ml-3 white--text"
            >{{ "Delete" }}</v-btn
          >
          <v-btn class="ml-3" @click="Dialog = false">Cancel</v-btn>
        </v-form>
      </div>
    </v-dialog>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
import circulateLoad from "../../components/universal/circulateLoad.vue";
export default {
  components: { circulateLoad },
  computed: {
    ...mapGetters(["DepartmentsState", "ProgramsState", "CoursesState"]),
  },
  mounted() {
    // this.$refs.calendar.checkChange();
    this.Initialize();
  },
  watch: {},
  methods: {
    ...mapActions(["getDepartmentsList", "getPrograms", "getCoursesList"]),
    async collectProgram() {
      this.loading = true;
      try {
        await this.getPrograms({
          department: this.getDptId(this.department),
          level: this.level,
        });
      } catch (error) {
        this.message = "failed to get school times table";
        this.snackbar = true;
      }
      this.loading = false;
      this.getEvent();
    },
    getEvent() {
      this.events = [];
      for (let i = 0; i < this.ProgramsState.length; i++) {
        const program = this.ProgramsState[i];
        if (program.type == "course") {
          this.events.push({
            name: program.name,
            start: new Date(program.dates[0]),
            end: new Date(program.dates[1]),
            color: program.color,
            timed: program.timed ?? false,
            _id: program._id,
          });
        } else if (program.type == "exam") {
          this.events.push({
            name: program.name,
            start: new Date(`${program.day.date} ${program.day.time}`),
            end: new Date(`${program.day.date} ${program.day.time}`),
            color: program.color,
            timed: true,
            _id: program._id,
          });
        }
      }
    },
    async getEventById(id) {
      for (let i = 0; i < this.ProgramsState.length; i++) {
        const program = this.ProgramsState[i];
        if (id == program._id) {
          console.log(program);
          if (program.timed == true) {
            return {
              color: program.color,
              date: program.day.date,
              time: program.day.time,
              course: await this.getCourseName(program.course),
              type: program.type,
              _id: program._id,
            };
          } else {
            return {
              color: program.color,
              dates: program.dates,
              course: await this.getCourseName(program.course),
              type: program.type,
              _id: program._id,
            };
          }
        }
      }
    },
    async getDepartments() {
      const interval = setInterval(() => {
        this.getDepartmentsList();
        if (this.DepartmentsState.list) {
          this.collectProgram();
          clearInterval(interval);
        }
      }, 500);
    },
    async Initialize() {
      this.getDepartments();
    },
    addProgram() {
      this.program = {};
      this.Dialog = true;
      this.ModalText = {
        btn: "Add",
        title: "Add one",
      };
    },
    showEvent({ nativeEvent, event }) {
      const edit = async () => {
        this.selectedEvent = event;
        this.program = await this.getEventById(event._id);
        this.ModalText = {
          btn: "update",
          title: `Update ${this.program.type}`,
        };
        requestAnimationFrame(() => requestAnimationFrame(() => (this.Dialog = true)));
      };

      if (this.Dialog) {
        this.Dialog = false;
        this.selectedEvent = undefined;
        this.selectedElement = undefined;
        requestAnimationFrame(() => requestAnimationFrame(() => edit()));
      } else {
        edit();
      }

      nativeEvent.stopPropagation();
    },
    async saveProgram() {
      this.errors = "";
      if (this.$refs.form.validate()) {
        if (this.program.type == "course") {
          if (!this.program.dates || this.program.dates.length < 2) {
            return (this.errors = "You need to seleted the start and the end date");
          }
        } else {
          if (!this.program.date) return (this.errors = "You need to seleted the date");
          if (!this.program.time) return (this.errors = "You need to seleted the time");
        }

        this.loading = true;
        let program = {
          color: this.program.color.hexa ?? this.program.color,
          course: this.getCourseId(this.program.course),
          type: this.program.type,
        };
        if (this.program.type == "course") {
          program.dates = this.program.dates;
        } else {
          program.day = {
            date: this.program.date,
            time: this.program.time,
          };
        }
        try {
          if (this.program._id)
            await axios.put("/admin/program/" + this.program._id, program);
          else await axios.post("/admin/program", program);
          this.message = "Program added successfully";
          this.collectProgram();
        } catch (error) {
          this.message = "Failed to add this program";
        }
        this.loading = false;
        this.Dialog = false;
        this.snackbar = true;
      }
    },
    async deleteProgram(id) {
      try {
        await axios.delete("/admin/program/" + id);
        this.message = "Program deleted successfully";
      } catch (error) {
        this.message = "failed to delete this program";
      }
      this.snackbar = true;
      this.Dialog = false;
      this.collectProgram();
    },
    getDptId(name) {
      for (let i = 0; i < this.DepartmentsState.departments.length; i++) {
        const department = this.DepartmentsState.departments[i];
        if (department.name == name) return department._id;
      }
    },
    getCourseId(name) {
      for (let i = 0; i < this.CoursesState.courses.length; i++) {
        const course = this.CoursesState.courses[i];
        if (course.name == name) return course._id;
      }
    },
    async getCourseName(id) {
      await this.getCoursesList();
      for (let i = 0; i < this.CoursesState.courses.length; i++) {
        const course = this.CoursesState.courses[i];
        if (course._id == id) return course.name;
      }
    },
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
  },
  data() {
    return {
      focus: "",
      type: "month",
      Dialog: false,
      valid: true,
      typeToLabel: {
        month: "Month",
        week: "Week",
        day: "Day",
        "4day": "4 Days",
      },
      errors: "",
      ModalText: {},
      loading: true,
      snackbar: false,
      message: "",
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
      program: {},
      departments: [],
      items: [],
      levels: ["100", "200", "300", "400", "500", "600", "700"],
      level: "100",
      department: "",
      timeMenu: false,
    };
  },
};
</script>
