<template>
  <div class="h-100 pt-5 bg-tkp">
    <div>
      <div class="d-flex container justify-space-between">
        <h3>Times table</h3>
        <div>
          <a
            @click.prevent="collectProgram"
            href=""
            class="px-3 py-3 rounded elevation transparent-bg white--text"
            >Refresh</a
          >
        </div>
      </div>
      <v-container v-if="loading == false && ProgramsState">
        <v-row class="fill-height px-md-6 rounded">
          <v-col>
            <v-sheet height="64">
              <v-toolbar flat>
                <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
                  Today
                </v-btn>
                <v-btn fab text small color="grey darken-2" @click="prev">
                  <v-icon small> mdi-chevron-left </v-icon>
                </v-btn>
                <v-btn fab text small color="grey darken-2" @click="next">
                  <v-icon small> mdi-chevron-right </v-icon>
                </v-btn>
                <v-toolbar-title v-if="$refs.calendar">
                  {{ $refs.calendar.title }}
                </v-toolbar-title>
                <v-spacer></v-spacer>
                <v-menu bottom right>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn outlined color="grey darken-2" v-bind="attrs" v-on="on">
                      <span>{{ typeToLabel[type] }}</span>
                      <v-icon right> mdi-menu-down </v-icon>
                    </v-btn>
                  </template>
                  <v-list>
                    <v-list-item @click="type = 'day'">
                      <v-list-item-title>Day</v-list-item-title>
                    </v-list-item>
                    <v-list-item @click="type = 'week'">
                      <v-list-item-title>Week</v-list-item-title>
                    </v-list-item>
                    <v-list-item @click="type = 'month'">
                      <v-list-item-title>Month</v-list-item-title>
                    </v-list-item>
                    <v-list-item @click="type = '4day'">
                      <v-list-item-title>4 days</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>
              </v-toolbar>
            </v-sheet>
            <v-sheet height="600">
              <v-calendar
                ref="calendar"
                v-model="focus"
                color="primary"
                :events="events"
                :event-color="getEventColor"
                :type="type"
                @click:more="viewDay"
                @click:date="viewDay"
              ></v-calendar>
              <v-menu
                v-model="selectedOpen"
                :close-on-content-click="false"
                :activator="selectedElement"
                offset-x
              >
                <v-card color="grey lighten-4" min-width="350px" flat>
                  <v-toolbar :color="selectedEvent.color" dark>
                    <v-btn icon>
                      <v-icon>mdi-pencil</v-icon>
                    </v-btn>
                    <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn icon>
                      <v-icon>mdi-heart</v-icon>
                    </v-btn>
                    <v-btn icon>
                      <v-icon>mdi-dots-vertical</v-icon>
                    </v-btn>
                  </v-toolbar>
                  <v-card-text>
                    <span v-html="selectedEvent.details"></span>
                  </v-card-text>
                  <v-card-actions>
                    <v-btn text color="secondary" @click="selectedOpen = false">
                      Cancel
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-menu>
            </v-sheet>
          </v-col>
        </v-row>
      </v-container>
      <circulate-load v-else-if="loading" />
      <div class="text-center text-h5" v-else>
        <p>Please select department to continue</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import circulateLoad from "../../components/universal/circulateLoad.vue";
export default {
  components: { circulateLoad },
  computed: {
    ...mapGetters(["StudentProfilState", "ProgramsState", "CoursesState"]),
  },
  mounted() {
    this.Initialize();
  },
  methods: {
    ...mapActions(["getDepartmentsList", "getPrograms", "getCoursesList"]),
    Initialize() {
      this.loading = true;
      const interval = setInterval(async () => {
        if (this.StudentProfilState.degre) {
          this.collectProgram();
          clearInterval(interval);
        }
      }, 100);
    },
    async collectProgram() {
      this.loading = true;
      try {
        await this.getPrograms({
          department: this.StudentProfilState.department,
          level: this.StudentProfilState.degre,
        });
      } catch (error) {
        this.message = "failed to get school times table";
        this.snackbar = true;
      }
      this.loading = false;
      this.getEvent();
    },
    getEvent() {
      this.events = [];
      for (let i = 0; i < this.ProgramsState.length; i++) {
        const program = this.ProgramsState[i];
        if (program.type == "course") {
          this.events.push({
            name: program.name,
            start: new Date(program.dates[0]),
            end: new Date(program.dates[1]),
            color: program.color,
            timed: program.timed ?? false,
            _id: program._id,
          });
        } else if (program.type == "exam") {
          this.events.push({
            name: program.name,
            start: new Date(`${program.day.date} ${program.day.time}`),
            end: new Date(`${program.day.date} ${program.day.time}`),
            color: program.color,
            timed: true,
            _id: program._id,
          });
        }
      }
    },
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
  },
  data() {
    return {
      focus: "",
      type: "month",
      Dialog: false,
      valid: true,
      typeToLabel: {
        month: "Month",
        week: "Week",
        day: "Day",
        "4day": "4 Days",
      },
      errors: "",
      ModalText: {},
      loading: true,
      snackbar: false,
      message: "",
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      events: [],
      program: {},
      departments: [],
      items: [],
      department: "",
    };
  },
};
</script>

<style></style>
