<template>
  <div class="">
    <div class="h-100" id="">
      <div class="container px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            My courses /

            <span class="main--text"
              ><v-icon class="main--text text-h4 ml-3"
                >mdi-book-open-variant</v-icon
              ></span
            >
          </h3>
          <div class="">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 d-block d-md-inline my-3 py-3 mr-md-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
          </div>
        </div>
        <div class="mt-5">
          <v-card>
            <v-card-title>
              Courses
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :loading="loading"
              :loading-text="'chargement...'"
              :headers="headers"
              :items="StudentCoursesState"
              :search="search"
            >
              <template v-slot:item.departments="{ item }">
                <td>
                  <ul>
                    <li v-for="i in item.other_informations.departments" :key="i._id">
                      {{ i.name }}
                    </li>
                  </ul>
                </td>
              </template>
              <template v-slot:item.professor="{ item }">
                <td>
                  {{ item.other_informations.professor.grade }}
                  {{ item.other_informations.professor.firstname }}
                  {{ item.other_informations.professor.lastname }}
                </td>
              </template>
              <template v-slot:item.action="{ item }">
                <td>
                  <router-link
                    :to="'/student/course/' + item._id"
                    class="rounded-circle white--text px-2 py-3 mr-3"
                    ><v-btn icon color="green"
                      ><v-icon class="">mdi-arrow-right</v-icon></v-btn
                    >
                  </router-link>
                </td>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </div>
    </div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>

    <load v-if="loader" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Load from "../../components/universal/load.vue";

export default {
  components: { Load },
  computed: {
    ...mapGetters(["StudentCoursesState"]),
  },
  methods: {
    ...mapActions(["getStudentCourses"]),

    Refresh() {
      this.Initialize();
      this.message = "Courses refresh successfully";
      this.snackbar = true;
    },
    Initialize() {
      this.loading = true;
      this.loader = true;
      this.getStudentCourses().then(
        () => {
          this.loading = false;
          this.loader = false;
        },
        () => {
          this.loading = false;
          this.loader = false;
        }
      );
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      errors: "",

      snackbar: false,
      message: "",
      loading: false,
      loader: true,

      search: "",
      headers: [
        {
          text: "Name",
          value: "name",
        },
        { text: "Professor", value: "professor" },
        // { text: "Level", value: "level" },
        // { text: "Departments", value: "departments" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
