<template>
  <div>
    <div class="">
      <v-tabs
        v-model="tab"
        class="mb-3"
        color="blue"
        background-color="white"
        centered
        icons-and-text
      >
        <v-tabs-slider></v-tabs-slider>

        <v-tab @click="(exerciceUpdate = {}), (isCreated = true)" href="#show">
          Show Exercices
          <v-icon>mdi-clipboard-list-outline</v-icon>
        </v-tab>

        <v-tab @click="AnswerType = 'Text'" v-if="isCreated" href="#treated">
          Treated Exercice
          <v-icon>mdi-head-question-outline</v-icon>
        </v-tab>
      </v-tabs>
      <!-- <div>{{ exercices }}</div> -->
    </div>

    <v-tabs-items v-model="tab">
      <v-tab-item :value="'show'">
        <div class="container">
          <div
            class="my-2 d-flex justify-space-between"
            v-for="(exercice, i) in exercices"
            :key="exercice._id"
          >
            <a
              class="main--text"
              v-if="isAvailable(exercice)"
              @click.prevent="treated(exercice)"
              href=""
              :class="exercice.treated ? 'finish' : ''"
            >
              <span> Exercice {{ i + 1 }}</span>
            </a>
            <div>
              <v-icon class="black--text" v-if="isAvailable(exercice) == 'time out'"
                >mdi-timer-off-outline</v-icon
              >
            </div>
          </div>
        </div>
      </v-tab-item>
      <v-tab-item :value="'treated'">
        <div>
          <treated-exercice
            :link="link"
            ref="treated"
            :quiz="exercice"
            :timeOut="timeOut"
          />
        </div>
      </v-tab-item>
    </v-tabs-items>

    <v-slide-y-transition>
      <v-dialog v-model="DeleteDialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this exercice ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every thing about this exercice will be deleted too
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="bg-secondaire white--text"
              text
              @click="(exerciceDeleteId = ''), (DeleteDialog = false)"
            >
              Cancel
            </v-btn>
            <v-btn class="red white--text" text @click="remove(exerciceDeleteId)">
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
// import createArticle from "../universal/createArticle.vue";
// import axios from "axios";
import treatedExercice from "./treatedExercice.vue";
export default {
  components: { treatedExercice },
  //   components: { createArticle },
  props: {
    exercices: {},
  },
  methods: {
    successCreated() {
      this.tab = "show";
      this.$emit("refresh");
      if (this.$refs.createArticle) this.$refs.createArticle.Initialize();
      this.message = "Exercice saved successfully";
      this.snackbar = true;
    },
    treated(exercice) {
      this.exercice = exercice;
      this.link = `student/answer/${exercice._id}`;
      this.isAvailable(this.exercice) == "time out"
        ? (this.timeOut = true)
        : (this.timeOut = false);

      this.tab = "treated";
      setTimeout(() => {
        this.$refs.treated.Initialize();
      }, 500);
    },
    isAvailable(exercice) {
      if (exercice.online == false) return false;
      if (exercice.timeLimite != undefined && exercice.timeLimite != false) {
        const start = `${exercice.timeLimite.date.start} ${exercice.timeLimite.time.start}`;
        const end = `${exercice.timeLimite.date.end} ${exercice.timeLimite.time.end}`;
        const startDate = new Date(start);
        const endDate = new Date(end);
        const today = new Date();
        if (today.getTime() < startDate.getTime() || today.getTime() > endDate.getTime())
          return "time out";
      }
      return true;
    },
  },
  data() {
    return {
      tab: null,
      snackbar: false,
      timeOut: false,
      link: "",
      message: "",
      exerciceDeleteId: "",
      DeleteDialog: false,
      errors: "",
      chapiter: {},
      progress: 0,
      inProgress: false,
      progressMessage: "",
      applicationAnswerType: ["Text", "Pdf", "Multiple Choice"],
      AnswerType: "Text",
      exerciceUpdate: {},
      isCreated: true,
      exercice: {},
    };
  },
};
</script>

<style></style>
