import axios from 'axios'

const state ={
    Chapiter :{},
}

const getters = {
    ChapiterState : (state)=>state.Chapiter,

}

const mutations ={
    
    GET_CHAPITER(state,data){
        state.Chapiter = data
    },  
    
  
    
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
  
    getProfessorChapiter({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/chapiter/${id}`)
                .then(res=>{
                    commit('GET_CHAPITER',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getStudentChapiter({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/student/chapiter/${id}`)
                .then(res=>{
                    commit('GET_CHAPITER',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
     
    updateChapiterGoal({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .put(`/professor/course/${schema.courseId}/chapiter/${schema.chapiterId}/goal`,schema.data)
                .then(res=>{
                    commit('LOGIN',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
}

export default {
    state,
    getters,
    mutations,
    actions
}