import axios from "axios"

/**
 * Utils function + vuex function 
 */ 
const InCoursePeriod = (day,dates) => {
    const startDate = new Date(dates[0])            
    const endDate = new Date(dates[1])
    if (day.getTime() < endDate.getTime() && day.getTime() >= startDate.getTime()) return true
    return false
}


const state = {
    error: {
        default : "Something went wrong"
    },
    accademicYears: [],
    levels: [],
    collapse: false,
    quillOption: {
        default: {
            // Some Quill options...
            theme: "snow",
            modules: {
              toolbar: [
                ["bold", "italic", "underline", "strike"],
                ["blockquote", "code-block"],
                [{ header: 1 }, { header: 2 }],
                [{ list: "ordered" }, { list: "bullet" }],
                [{ script: "sub" }, { script: "super" }],
                [{ indent: "-1" }, { indent: "+1" }],
                [{ direction: "rtl" }],
                [{ size: ["small", false, "large", "huge"] }],
                [{ header: [1, 2, 3, 4, 5, 6, false] }],
                [{ font: [] }],
                [{ color: [] }, { background: [] }],
                [{ align: [] }],
                ["clean"],
                ["link", "image", "video"],
              ],
            },
          }
    },
    courseShortInfo :{}
}

const getters = {
    ErrorBag: (state) => state.error,
    AccademicYears: (state) => {
        const today = new Date();
        let year = today.getFullYear();
        year += 20;
        let year2 = year + 1;
        let sessions = []
        for (let c = 0; c < 40; c++) {
            sessions.push(`${year - c} - ${year2 - c}`);
        }
        return state.accademicYears = sessions
    },
    Levels: (state) => {
        let levels = []
        for (let c = 1; c <= 7; c++){
            levels.push(100 * c)
        }
        return state.levels = levels
    },
    Collapse :(state) => state.collapse,
    QuillOption :(state) => state.quillOption,
    CourseShortInfo : (state)=> state.courseShortInfo,
    InCoursePeriod: (state) => {
        if (state.courseShortInfo.program) {
            const dates = state.courseShortInfo.program.dates
            const today = new Date()
            return InCoursePeriod(today,dates)
           
        }   
        return false
    },
    isInCoursePeriod: (state) => (today) => {
        console.log(today);
        if (state.courseShortInfo.program) {
            const dates = state.courseShortInfo.program.dates
            return  InCoursePeriod(today, dates)
        }
    }
}

const mutations = {
    TOGGLE_COLLAPSE(state) {
        if (state.collapse == true) state.collapse = false
        else state.collapse= true
    },
    GET_COURSE_INFO(state,data) {
        state.courseShortInfo = data
    }
}

const actions = {
    toggleCollapse({commit}) {
        commit('TOGGLE_COLLAPSE')
    },
    getCourseShortInfo({commit},id){
        return new Promise((resolve, reject) => {
            axios.get(`/course/${id}/short-info`)
                .then((res) => {
                    resolve(res.data)
                    commit("GET_COURSE_INFO",res.data)
                }, err => {
                    reject(err)
                })
        })
    }
}

export default {
    state,
    getters,
    mutations,
    actions
}