<template>
  <div class="bg-tkp h-100">
    <v-container v-if="Notifications && loader == false">
      <div class="white rounded px-3 py-3">
        <div class="d-flex justify-space-between">
          <h1 class="my-3">Notifications <v-icon color="red">mdi-bell</v-icon></h1>
          <div class="py-5">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs" v-on="on">
                  <v-badge
                    :content="2"
                    :value="professor"
                    color="red"
                    overlap
                    class="mr-5"
                  >
                    <!-- <v-icon>mdi-bell-alert</v-icon> -->
                    <v-icon color="black" class="ml-2">mdi-account</v-icon>
                  </v-badge>
                </span>
              </template>
              <span>Professor unread notifications</span>
            </v-tooltip>

            <a href="" @click.prevent="refresh()" class="main--text">Refresh</a>
          </div>
        </div>

        <div>
          <notification :Notifications="Notifications.notifications" />
        </div>
      </div>
    </v-container>
    <circulate-load v-else />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Notification from "../../components/universal/Notification.vue";

export default {
  components: { Notification, CirculateLoad },
  computed: {
    ...mapGetters(["Notifications"]),
    computed: {
      //professorNotification unread
      professor() {
        if (this.Notifications.other_informations)
          return this.Notifications.other_informations.professors.length;
        return 0;
      },
    },
  },
  methods: {
    ...mapActions(["adminGetNotifications"]),
    refresh() {
      this.Initialize();
    },
    async Initialize() {
      this.loader = true;
      try {
        await this.adminGetNotifications();
      } catch (error) {
        console.log(error);
      }
      this.loader = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      loader: false,
    };
  },
};
</script>

<style></style>
