<template>
  <div>
    <div class="white" id="admin-profil">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div>
          <div class="head px-md-6">
            <div class="">
              <h3 class="text-h5">Dashbord / <span class="primaire">Profil</span></h3>
            </div>
            <div class="infos">
              <v-row>
                <v-col cols="12" md="4">
                  <div class="profil-img">
                    <img
                      :src="AdminProfilState.profilPicture"
                      class="rounded mr"
                      alt=""
                    />
                  </div>
                </v-col>

                <v-col cols="12" md="8">
                  <div class="mt-5 ml-3">
                    <div>
                      <h3 class="black--text mb-3">
                        <span class="primaire lighter">{{ AdminProfilState.grade }}</span>
                        {{ AdminProfilState.firstname + " " + AdminProfilState.lastname }}
                      </h3>
                      <p class="black--text">
                        <v-icon color="black" class="mr-">mdi-gmail</v-icon>
                        {{ AdminProfilState.email }}
                      </p>
                      <p class="black--text">
                        <span class="bold">Function :</span>
                        {{ AdminProfilState.function }}
                      </p>
                    </div>
                    <div class="d-flex justify-space-between white-part">
                      <div class="mt-3">
                        <div>
                          <span class="green mx-2 rounded-circle py-1 px-3"></span>
                          <span class="yellow mx-2 rounded-circle py-1 px-3"></span>
                          <span class="red rounded-circle mx-2 py-1 px-3"></span>
                          <span class="bg-primaire rounded-circle mx-2 py-1 px-3"></span>
                        </div>
                        <div class="mt-7">
                          <a
                            href=""
                            @click.prevent="data.modal = true"
                            class="bg-primaire white--text px-3 py-3 rounded"
                            ><v-icon color="white">mdi-key</v-icon> Change password
                          </a>
                        </div>
                        <!-- <span class=""><v-icon class="red--text">mdi-gmail</v-icon> </span> -->
                      </div>
                    </div>
                  </div>
                </v-col>
              </v-row>
            </div>
          </div>
        </div>
      </div>
    </div>

    <v-slide-x-transition>
      <v-dialog v-model="data.modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="data.modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="form"
              v-model="data.valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h1>{{ `Change password` }}</h1>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>

              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="data.ModalModel.password"
                  :type="data.passwordVisibility.type"
                  :rules="data.passwordRules"
                  label="Password"
                  required
                ></v-text-field>
                <span class="mt-5 ml-2">
                  <v-icon
                    @click="method.managePasswordVisibility(data.passwordVisibility)"
                  >
                    {{ data.passwordVisibility.icon }}
                  </v-icon>
                </span>
              </div>

              <div class="d-flex justify-space-between">
                <v-text-field
                  :rules="passwordConfirmationRules"
                  v-model="data.ModalModel.password_confirmation"
                  :type="data.confirmPasswordVisibility.type"
                  label="Confirmer le mots de passe"
                  required
                ></v-text-field>
                <span class="mt-5 ml-2">
                  <v-icon
                    @click="
                      method.managePasswordVisibility(data.confirmPasswordVisibility)
                    "
                  >
                    {{ data.confirmPasswordVisibility.icon }}
                  </v-icon>
                </span>
              </div>

              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!data.valid"
                  color=""
                  class="mr-4 white--text main"
                  @click="validates"
                  width="100%"
                >
                  {{ "Change" }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { defaultData, defaultMethod } from "../../utils/data";
export default {
  components: {},
  computed: {
    ...mapGetters(["admissionApplicationsGetters"]),
    ...mapGetters(["AdminProfilState"]),
  },
  methods: {
    ...mapActions(["getAdmissionApplications"]),

    changePassword() {
      // call map actions change my password universal pour tout les comptes
    },
  },
  mounted() {},
  data() {
    return {
      data: defaultData,
      method: defaultMethod,
      passwordConfirmationRules: [
        (v) => !!v || "Password confirmation is required",
        (v) => (v && v == this.data.ModalModel.password) || "Password doesn't match",
      ],
    };
  },
};
</script>

<style>
#admin-profil .profil-img img {
  height: 20rem !important;
}
</style>
