<template>
  <div class="bg-tkp h-100">
    <student-mark-collection
      v-if="TranscriptState.student && !loading"
      :Transcript="TranscriptState"
    />
    <circulate-load v-else-if="loading" />
    <can-not-find v-else :what="`This transcript`" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import StudentMarkCollection from "../../components/universal/StudentMarkCollection.vue";
export default {
  components: { StudentMarkCollection, CirculateLoad, CanNotFind },
  computed: {
    ...mapGetters(["ErrorBag", "TranscriptState"]),
  },
  methods: {
    ...mapActions(["adminGetStudentTranscript"]),
    async Initialize() {
      this.loading = true;
      try {
        const schema = {
          studentId: this.$route.params.studentId,
          courseId: this.$route.params.courseId,
        };
        await this.adminGetStudentTranscript(schema);
      } catch (error) {
        console.log(error);
      }
      this.loading = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      loading: false,
    };
  },
};
</script>

