<template>
  <div class="">
    <div class="h-100" id="">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">Dashbord / <span class="main--text">Courses</span></h3>
          <div class="">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 d-block d-md-inline my-3 py-3 mr-md-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
            <a
              href=""
              v-if="
                AdminProfilState.function == 'Director' ||
                AdminProfilState.function == 'Main Professor' ||
                AdminProfilState.function == 'Staff'
              "
              @click.prevent="Add()"
              class="px-3 d-block d-md-inline my-3 py-3 white elevation-1 rounded-pill blue-grey--text"
              >Add Course</a
            >
          </div>
        </div>
        <div class="mt-5">
          <v-card>
            <v-card-title>
              Courses
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :loading="loader"
              :loading-text="'chargement...'"
              :headers="headers"
              :items="CoursesState"
              :search="search"
            >
              <template v-slot:item.departments="{ item }">
                <td>
                  <ul>
                    <li v-for="i in item.other_informations.departments" :key="i._id">
                      {{ i.name }}
                    </li>
                  </ul>
                </td>
              </template>
              <template v-slot:item.action="{ item }">
                <td>
                  <router-link
                    :to="'course/' + item._id"
                    class="rounded-circle white--text px-2 py-3 mr-3"
                    ><v-icon class="green--text">mdi-arrow-right</v-icon>
                  </router-link>
                  <a
                    href=""
                    v-if="
                      AdminProfilState.function == 'Director' ||
                      AdminProfilState.function == 'Main Professor' ||
                      AdminProfilState.function == 'Staff'
                    "
                    @click.prevent="Edit(item)"
                    class="rounded-circle white--text px-2 py-3 mr-3"
                    ><v-icon class="main--text">mdi-circle-edit-outline</v-icon>
                  </a>
                  <a
                    href=""
                    v-if="
                      AdminProfilState.function == 'Director' ||
                      AdminProfilState.function == 'Main Professor' ||
                      AdminProfilState.function == 'Staff'
                    "
                    @click.prevent="Delete(item._id)"
                    class="rounded-pill white--text px-2 py-3"
                  >
                    <v-icon color="red">mdi-trash-can-outline</v-icon>
                  </a>
                </td>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </div>
    </div>
    <v-slide-x-transition>
      <v-dialog v-model="Modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="Modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h1>{{ ModalText.title }}</h1>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.name"
                  :rules="nameRules"
                  label="Name"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.code"
                  :rules="codeRules"
                  label="Code"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-select
                  :items="Levels"
                  color="red darken-3"
                  :rules="levelRules"
                  v-model="ModalModel.level"
                  label="Level"
                  class=""
                  required
                ></v-select>
              </div>
              <div>
                <v-card
                  class="py-3 mt-3 mb-12 elevation-0"
                  v-if="
                    AdminProfilState.function == 'Director' ||
                    AdminProfilState.function == 'Main Professor' ||
                    AdminProfilState.function == 'Staff'
                  "
                  color="grey lighten-4"
                >
                  <v-toolbar flat class="rounded" color="transparent">
                    <p class="text-h5 mt-3">Departments</p>
                    <v-spacer></v-spacer>
                    <v-btn
                      v-if="!allSelected"
                      icon
                      @click="(addDepartment = true), getDepartments()"
                    >
                      <v-icon color="blue">mdi-plus-circle-outline</v-icon>
                    </v-btn>
                  </v-toolbar>

                  <v-container class="py-0">
                    <v-row align="center" justify="start">
                      <v-col
                        v-for="(selection, i) in selections"
                        :key="selection"
                        class="shrink"
                      >
                        <v-chip
                          :disabled="loading"
                          close
                          @click:close="selected.splice(i, 1)"
                        >
                          <!-- <v-icon left v-text="selection"></v-icon> -->
                          {{ selection }}
                        </v-chip>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card>
              </div>

              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="validates"
                  width="100%"
                >
                  {{ ModalText.btn }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>

    <v-slide-y-transition>
      <v-dialog v-model="Dialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this course ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every exercice ,exams ... about this course will be deleted
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="bg-secondaire white--text" text @click="CancelDelete()">
              Cancel
            </v-btn>
            <v-btn class="red white--text" text @click="ConfirmDelete(deleteId)">
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>
    <v-slide-y-transition>
      <v-dialog max-width="45rem" v-model="addDepartment">
        <v-list class="px-md-12 px-3 py-md-12">
          <div class="d-flex justify-space-between">
            <span class="text-h5 black--text d-block my-2">Choose departments</span>
            <a href="" class="main--text text-h5" @click.prevent="addDepartment = false"
              >Ok</a
            >
          </div>
          <div class="mt-5" v-for="item in departments" :key="item">
            <div class="">
              <v-list-item
                v-if="!selected.includes(item)"
                :key="item"
                :disabled="loading"
                @click="selected.push(item)"
              >
                <v-list-item-title v-text="item"></v-list-item-title>
              </v-list-item>
            </div>
          </div>
          <div v-if="allSelected" class="text-center">
            Every department is already selected
          </div>
        </v-list>
      </v-dialog>
    </v-slide-y-transition>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>

    <function ref="function" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Function from "../../components/universal/function.vue";

export default {
  components: { Function },
  computed: {
    ...mapGetters([
      "CoursesState",
      "DepartmentsState",
      "AdminProfilState",
      "ErrorBag",
      "Levels",
    ]),
    allSelected() {
      console.log(this.departments);
      return this.selected.length === this.departments.length;
    },
    categories() {
      const search = this.search.toLowerCase();

      if (!search) return this.deparmtents.names;

      return this.departments.filter((item) => {
        const text = item.toLowerCase();

        return text.indexOf(search) > -1;
      });
    },
    selections() {
      const selections = [];

      for (const selection of this.selected) {
        selections.push(selection);
      }

      return selections;
    },
  },
  methods: {
    ...mapActions([
      "getCourses",
      "addCourse",
      "updateCourse",
      "deleteCourse",
      "getDepartments",
    ]),
    async setDepartment() {
      const departments = [];
      await this.getDepartments();
      this.DepartmentsState.forEach((item) => {
        departments.push(item.name);
      });
      this.departments = departments;
    },
    async Refresh() {
      this.loader = true;
      try {
        await this.getCourses();
        this.message = "Courses refresh successfully";
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.snackbar = true;
      this.loader = false;
    },
    async Initialize() {
      this.ModalText.title = "Add new Course";
      this.ModalText.btn = "Add";
      this.ModalModel = {};
      this.selected = [];
      this.loader = true;
      try {
        await this.getCourses();
        await this.setDepartment();
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.loader = false;
    },
    Add() {
      this.Modal = true;
      this.Initialize();
    },
    Edit(item) {
      item.other_informations.departments.forEach((department) => {
        this.selected.push(department.name);
      });
      this.Modal = true;
      this.ModalModel = item;
      this.ModalText.title = "Edit Course";
      this.ModalText.btn = "Update";
    },
    Delete(id) {
      this.Dialog = true;
      this.deleteId = id;
    },
    CancelDelete() {
      this.deleteId = "";
      this.Dialog = false;
    },
    async ConfirmDelete(id) {
      try {
        await this.deleteCourse(id);
        this.message = "Course deleted successfully";
      } catch (error) {
        this.message = "Failed to delete this course";
      }
      this.snackbar = true;
      this.CancelDelete();
      this.Initialize();
      this.getDepartments();
    },
    getDepartmentName(id) {
      for (let i = 0; i < this.DepartmentsState.length; i++) {
        const deparment = this.DepartmentsState;
        if (deparment[i]._id == id) {
          return deparment[i].name;
        }
      }
    },
    getDepartmentId(names) {
      this.departments_id = [];
      names.forEach((name) => {
        this.DepartmentsState.forEach((item) => {
          if (item.name == name) {
            this.departments_id.push(item._id);
          }
        });
      });
    },
    async validates() {
      if (this.$refs.form.validate()) {
        if (this.selected.length < 1)
          return (this.errors = "Department is required to create a course ");
        this.loading = true;
        await this.getDepartmentId(this.selected);
        const ModelAdd = {
          name: this.ModalModel.name,
          code: this.ModalModel.code,
          level: this.ModalModel.level,
          departments: this.departments_id,
        };

        if (this.ModalModel._id) {
          // update
          const ModalModelUpdate = {
            data: ModelAdd,
            _id: this.ModalModel._id,
          };
          try {
            await this.updateCourse(ModalModelUpdate);
            this.message = "Course updated successfully";
          } catch (error) {
            this.message = "Failed to edit this course";
          }
        } else {
          try {
            await this.addCourse(ModelAdd);
            this.message = "Course added successfully";
          } catch (error) {
            this.message = "Failed to add course";
          }
        }

        this.loading = false;
        this.Modal = false;
        this.snackbar = true;
        this.getDepartments();
        this.Initialize();
      }
    },
    isAuthorized(value, array) {
      if (this.AdminProfilState.function == "Director") return true;
      if (this.$refs.function.inArray(value, array)) return true;

      return false;
    },
  },
  mounted() {
    this.getDepartments();
    this.Initialize();
  },
  data() {
    return {
      deleteId: "",
      errors: "",
      departments: [],
      departments_id: [],
      Dialog: "",
      snackbar: false,
      message: "",
      loading: false,
      loader: true,
      selected: [],
      chips: [],
      addDepartment: false,
      valid: true,
      nameRules: [(v) => !!v || "name is required"],
      codeRules: [(v) => !!v || "course code  is required"],
      departmentRules: [(v) => !!v || "department is required"],
      levelRules: [(v) => !!v || "level is required"],
      Modal: false,
      ModalText: {},
      ModalModel: {},
      search: "",
      headers: [
        {
          text: "Name",
          value: "name",
        },
        { text: "Code", value: "code" },
        { text: "Level", value: "level" },
        { text: "Departments", value: "departments" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
