<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {},
  computed: mapGetters(["panierState", "drawerState", "StudentProfilState"]),

  methods: {
    ...mapActions(["showDrawer", "getStudentProfil"]),
    cancel() {
      this.navSubmitClass = false;
      this.searchForm = false;
    },
    submitTkp() {
      this.cancel();
    },

    search() {
      if (this.searchForm == true) {
        this.submitTkp();
      } else {
        this.searchForm = true;
        this.navSubmitClass = true;
      }
    },
    isActive(name) {
      name = name.split(",");
      for (let i = 0; i < name.length; i++) {
        if (this.$route.name == name[i]) return "active-nav";
      }
    },
  },
  mounted() {
    this.getStudentProfil();
  },
  data() {
    return {
      drawer: false,
    };
  },
};
</script>

