<template>
  <div class="bg-tkp h-100">
    <div class="py-5 px-md-5" id="" v-if="StudentCourseState.name && loader == false">
      <!-- {{ StudentCourseState.other_informations.chapiters }} -->
      <v-row>
        <v-col cols="12" md="4" lg="3">
          <div>
            <short-course-info :id="$route.params.id" />
          </div>
        </v-col>
        <v-col cols="12" md="8" lg="9">
          <v-container class="white rounded h-100 px-md-12 py-12">
            <div>
              <div class="mb-8 border-b">
                <!-- <h3>Professor :</h3> -->
                <div class="d-flex align-center">
                  <v-avatar size="90"
                    ><v-img
                      :src="StudentCourseState.other_informations.professor.profilPicture"
                      aspect-ratio="1"
                  /></v-avatar>
                  <div class="ml-2">
                    <div class="bold">
                      <span class="main--text mx-3 d-block">{{
                        StudentCourseState.other_informations.professor.grade
                      }}</span>
                      <span class="mx-3 text-sm">{{
                        StudentCourseState.other_informations.professor.firstname
                      }}</span>
                      <span class="text-sm">{{
                        StudentCourseState.other_informations.professor.lastname
                      }}</span>
                    </div>
                    <div class="ml-3">
                      <span class="text-sm">{{
                        StudentCourseState.other_informations.professor.email
                      }}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="d-flex border-t pt-5 justify-space-between">
                <h1 class="text-h3">{{ StudentCourseState.name }}</h1>
                <div>
                  <router-link
                    :to="`/student/course/${StudentCourseState._id}/collect-notes`"
                    class="rounded-lg elevation-1 main white--text px-2 py-1 mr-3"
                    >Collect of notes</router-link
                  >
                  <a
                    href=""
                    @click.prevent="Refresh()"
                    class="rounded-lg elevation-1 px-2 py-1 main--text"
                    >Refrech</a
                  >
                </div>
              </div>

              <div class="d-flex justify-space-between">
                <div v-if="StudentCourseState.description" class="mt-3">
                  <article v-html="StudentCourseState.description"></article>
                  <p class="bold">Objectif(s) of this course:</p>

                  <ul>
                    <li v-for="(objectif, i) in StudentCourseState.objectifs" :key="i">
                      {{ objectif }}
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div class="mt-10 pt-6 border-t">
              <div class="d-flex justify-space-between">
                <h3 class="bold">Chapiter(s)</h3>
              </div>
              <div
                v-if="StudentCourseState.other_informations.chapiters.length > 0"
                class="mt-8"
              >
                <div
                  v-for="chapiter in StudentCourseState.other_informations.chapiters"
                  :key="chapiter._id"
                  class="d-flex justify-space-between"
                >
                  <router-link
                    :class="chapiter.treated ? 'finish' : ''"
                    :to="`/student/chapiter/${chapiter._id}`"
                    >Chapiter {{ chapiter.number }} : {{ chapiter.title }} ({{
                      chapiter.type
                    }})</router-link
                  >
                </div>
              </div>
              <div class="mt-8" v-else>
                <p>This course does'nt have chapiter now</p>
              </div>
            </div>
            <div class="mt-10 pt-6 border-t">
              <div class="d-flex justify-space-between">
                <h3 class="bold">Exam</h3>
              </div>
              <div v-if="CourseExamState._id" class="mt-8">
                <div class="d-flex justify-space-between">
                  <router-link :to="`/student/exam/${CourseExamState._id}`"
                    >Exam
                    <v-icon v-if="CourseExamState.online == true" color="green"
                      >mdi-dot</v-icon
                    ></router-link
                  >
                </div>
              </div>
              <div class="mt-8" v-else>
                <p>This course does'nt have exam now</p>
              </div>
            </div>
            <div class="mt-10 rounded pt-6 border-t">
              <div class="d-flex justify-space-between">
                <h3 class="bold">Comming Test(s)</h3>
              </div>
              <v-row v-if="StudentCourseState.other_informations.tests.length > 0">
                <v-col
                  v-for="(test, i) in StudentCourseState.other_informations.tests"
                  :key="test._id"
                  class="col-12 col-md-4"
                >
                  <p class="main--text">Test {{ i + 1 }} ({{ test.answerType }})</p>
                  <div>
                    <span class="bold">Date : </span>
                    <span>{{ test.day.date }} {{ test.day.time }}</span>
                  </div>
                  <div>
                    <span class="bold">Duration : </span>
                    <span>{{ test.duration }} min</span>
                  </div>
                  <div v-if="isItTime(test.day, test.duration)" class="mt-5">
                    <router-link
                      :to="`/student/test/${test._id}`"
                      class="main px-4 bold py-2 rounded-pill white--text"
                      >Write it</router-link
                    >
                  </div>
                </v-col>
              </v-row>
              <div v-else class="mt-8">
                <p>This course does'nt have exam now</p>
              </div>
            </div>
          </v-container>
        </v-col>
      </v-row>
    </div>
    <div v-else-if="loader == false && !StudentCourseState.name">
      <can-not-find what="this Courses" />
    </div>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import ShortCourseInfo from "../../components/universal/shortCourseInfo.vue";

export default {
  components: {
    CanNotFind,
    CirculateLoad,
    ShortCourseInfo,
  },
  computed: {
    ...mapGetters(["StudentCourseState", "CourseExamState", "isInCoursePeriod"]),
  },
  methods: {
    ...mapActions(["getStudentCourse", "getCourseExam"]),
    Refresh() {
      this.Initialize();
      this.message = "Course refresh successfully";
      this.snackbar = true;
    },
    async Initialize() {
      this.loading = true;
      this.loader = true;
      this.chapiter = {};
      this.ChapiterModal = false;
      this.DeleteDialog = false;
      this.Modal = false;
      try {
        await this.getStudentCourse(this.$route.params.id);
        await this.getCourseExam(this.$route.params.id);
      } catch (error) {
        this.message = "Something went wrong";
        this.snackbar = true;
      }
      this.loading = false;
      this.loader = false;
    },
    isItTime(day, duration) {
      const today = new Date();
      const startDate = new Date(`${day.date} ${day.time}`);
      let endDate = new Date(startDate);
      endDate.setMinutes(startDate.getMinutes() + Number(duration));
      //getTime() is in  milisecond
      if (
        today.getTime() >= startDate.getTime() &&
        today.getTime() <= endDate.getTime()
      ) {
        return true;
      }
      return false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      circulateLoader: false,
      chapiterNumberLoading: false,
      objectifs: [],
      items: [],
      chapiter: {},
    };
  },
};
</script>

<style>
.finish {
  text-decoration: line-through;
}
</style>
