<template>
  <div class="bg-tkp h-100">
    <div class=" " id="admin-profil" v-if="TranscriptState.course && loader == false">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-m-flex justify-space-between">
          <h3 class="text-h5 mb-4">
            Transcript /
            <router-link
              :to="`/professor/course/${TranscriptState.course._id}`"
              class="main--text"
              >{{ TranscriptState.course.name }}</router-link
            >
          </h3>
          <v-col cols="12" md="12">
            <div class="d-flex flex-column flex-md-row">
              <div>
                <v-select
                  class="mx-2 my-4 my-md-0"
                  :items="AccademicYears"
                  label="Session"
                  v-model="session"
                  @change="Initialize"
                  dense
                  outlined
                ></v-select>
              </div>
              <div>
                <v-select
                  class="mx-2 my-4 my-md-0"
                  :items="[1, 2, 3]"
                  label="Test number"
                  v-model="testNumber"
                  @change="manageTestNumber"
                  dense
                  outlined
                ></v-select>
              </div>
              <div class="" v-if="TranscriptState.course.other_informations">
                <v-btn
                  v-if="
                    TranscriptState.course.other_informations.exam &&
                    TranscriptState.course.other_informations.tests > 0
                  "
                  :loading="loading"
                  @click="generateTranscript()"
                  class="py- d-block d-md-inline mr-md-3 elevation-1 white rounded-lg secondary--text"
                >
                  <span v-if="!TranscriptState.transcript"> Generate Transcript </span>
                  <span v-else> Regenerate Transcript </span>
                </v-btn>
              </div>
              <div class="mt-md-2">
                <a
                  href=""
                  @click.prevent="Refresh()"
                  class="px-3 py-2 d-block d-md-inline mr-md-3 elevation-1 transparent-bg rounded-lg white--text"
                  >Refrech</a
                >
              </div>
            </div>
          </v-col>
        </div>
        <div class="d-flex justify-end">
          <div></div>
        </div>
      </div>
      <transcript ref="transcript" :Transcript="TranscriptState" />
    </div>
    <div v-else-if="loader == false && !TranscriptState._id">
      <can-not-find what="this transcript" />
    </div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
    <function ref="function" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Function from "../../components/universal/function.vue";

import axios from "axios";
import Transcript from "../../components/universal/Transcript.vue";
export default {
  components: {
    CanNotFind,
    Function,
    CirculateLoad,
    Transcript,
  },
  computed: {
    ...mapGetters(["TranscriptState", "AdminProfilState", "ErrorBag", "AccademicYears"]),
  },
  methods: {
    ...mapActions(["professorGetTranscript"]),
    Refresh() {
      this.Initialize();
    },

    async Initialize() {
      if (!this.session) return;
      this.loading = true;
      this.loader = true;
      try {
        await this.professorGetTranscript({
          id: this.$route.params.id,
          session: this.session,
        });
        if (this.TranscriptState.course)
          this.testNumber = this.TranscriptState.course.testNumber;
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loading = false;
      this.loader = false;
    },
    async manageTestNumber() {
      this.loading = true;
      try {
        await axios.post(`/professor/course/${this.$route.params.id}/test-number`, {
          number: this.testNumber,
        });
        this.message = "test note define successfully";
        this.Initialize();
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.snackbar = true;
      this.loading = false;
    },

    async generateTranscript() {
      this.loading = true;
      const transcript = this.$refs.transcript.getTranscript();

      try {
        await axios.post(
          `/professor/course/${this.$route.params.id}/transcript/${this.session}`,
          {
            transcript: transcript,
          }
        );
        this.message = "Transcript generated successfully";
        this.Initialize();
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.snackbar = true;
      this.loading = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  watch: {},
  data() {
    return {
      testNumber: 2,
      valid: true,
      snackbar: false,
      session: "2021 - 2022",
      message: "",
      errors: "",
      loading: false,
      loader: true,
      circulateLoader: false,
    };
  },
};
</script>

<style></style>
