import axios from 'axios'

const state ={
    notifications :[],
    snackbar: {
    }
}

const getters = {
    Notifications : (state)=>state.notifications,
    Snackbar : (state)=>state.snackbar,
}

const mutations ={
    
    GET_NOTIFICATIONS(state,data){
        state.notifications = data
    },
    SET_SNACKBAR(state,data){
        state.snackbar = data
    },
    
    
}

const actions = {
  
    professorGetNotifications({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/notification`)
                .then(res=>{
                    commit('GET_NOTIFICATIONS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    adminGetNotifications({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/admin/notification`)
                .then(res=>{
                    commit('GET_NOTIFICATIONS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
    setSnackbar({commit},schema) {
        commit('SET_SNACKBAR',schema)
    }
    
}

export default {
    state,
    getters,
    mutations,
    actions
}