import axios from 'axios'

const state ={
    Programs:[]   
}

const getters = {
    ProgramsState : (state)=>state.Programs,
    
}

const mutations ={
    
    GET_PROGRAMS(state,data){
        state.Programs = data
    },  
    
 
    
    
}

const actions = {
    getPrograms({commit},data){
        
        return new Promise((resolve,reject)=>{
            axios
                .get(`/admin/program/${data.department}/${data.level}`)
                    .then(res=>{
                        commit('GET_PROGRAMS',res.data)
                        resolve(res.data)
                    })
                    .catch(err=>{
                        reject(err)
                    })
                })
    },
}

export default {
    state,
    getters,
    mutations,
    actions
}