<template>
  <div class="m-0 relative bg-tkp h-100 d-flex justify-center align-items-center" id="">
    <v-row class="">
      <v-col
        cols="12"
        sm="8"
        offset-sm="2"
        md="6"
        offset-md="3"
        lg="4"
        offset-lg="4"
        class="d-flex flex-column justify-center"
      >
        <div class="logo d-flex justify-center">
          <img :src="require('@/assets/stock/design/logo.png')" />
        </div>
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
          class="px-10 py-12 elavation- black--text white rounded"
        >
          <p class="text-h3">Login</p>
          <p>
            Lorem ipsum dolor sit amet consectetur, adipisicing elit. Aut qui magnm
            perferendis,
          </p>
          <div v-if="errors">
            <p class="red--text">{{ errors }}</p>
          </div>
          <div class="d-flex justify-space-between">
            <v-text-field
              v-model="user.email"
              :rules="emailRules"
              class=""
              label="Email"
              color="black"
              required
            ></v-text-field>

            <span class="ml-8"></span>
          </div>

          <div class="d-flex justify-space-between mb-10">
            <v-text-field
              class=""
              v-model="user.password"
              color="black"
              :type="passwordVisibility.type"
              label="Mots de passe"
              required
            ></v-text-field>
            <span class="mt-5 ml-2">
              <v-icon color="black" @click="managePasswordVisibility()">
                {{ passwordVisibility.icon }}
              </v-icon>
            </span>
          </div>

          <div class="text-center">
            <v-btn
              :disabled="!valid"
              color=""
              :loading="loading"
              class="mr-4 main white--text"
              @click="signIn"
              width="100%"
            >
              Connexion
            </v-btn>
          </div>

          <div class="mt-5 text-center">
            <!-- <router-link to="/forget-password">Forget password</router-link> -->
          </div>
        </v-form>
        <p class="text-center my-2">
          New Student ?
          <router-link class="bold" to="/admission-form">Apply now</router-link>
        </p>
      </v-col>
    </v-row>

    <div class="targue d-none">
      <div>
        <h1>Doctor Mavix</h1>
        <span>All right reserve</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data: () => ({
    errors: "",
    loading: false,
    valid: true,
    user: {
      email: "",
      password: "",
    },

    emailRules: [
      (v) => !!v || "Email is required",
      (v) => /.+@.+\..+/.test(v) || "email must be valid",
    ],

    passwordVisibility: {
      icon: "mdi-eye",
      type: "password",
    },
  }),

  methods: {
    ...mapActions(["login"]),
    async signIn() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        try {
          await this.login(this.user);
          console.log("here");
          location.reload();
        } catch (error) {
          this.errors = "Invalid login information";
          this.loading = false;
        }
      }
    },
    //   manage password visibility
    managePasswordVisibility() {
      if (this.passwordVisibility.type == "password") {
        this.passwordVisibility.icon = "mdi-eye-off";
        this.passwordVisibility.type = "text";
      } else {
        this.passwordVisibility.icon = "mdi-eye";
        this.passwordVisibility.type = "password";
      }
    },
  },
};
</script>
