<script>
import { mapGetters, mapActions } from "vuex";

export default {
  methods: {
    ...mapActions(["addMarketDepartmentModal", "addMarketCategorieModal"]),
    goTo(link) {
      this.$router.push(link);
    },
    isActive(...names) {
      for (let i = 0; i < names.length; i++) {
