import axios from "axios";

// axios.defaults.baseURL = "http://localhost:3000/api/v1";
// axios.defaults.baseURL = "http://192.168.43.239:3000/api/v1";

axios.defaults.baseURL = 'https://elearning-mavix-back.herokuapp.com/api/v1/'
let token = "";
if (localStorage.getItem("e-admin")) {
	token = localStorage.getItem("e-admin");
	axios.defaults.headers.common["admin-token"] = token;
} else if (localStorage.getItem("e-student")) {
	token = localStorage.getItem("e-student");
	axios.defaults.headers.common["student-token"] = token;
}

axios.defaults.headers.common["Content-Type"] = "application/json";
