import axios from 'axios'

const state ={
    Facultys :[],
    Faculty :{}
   
}

const getters = {
    FacultysState : (state)=>state.Facultys,
    FacultyState : (state)=>state.Faculty
    
}

const mutations ={
    
    GET_FACULTYS(state,data){
        state.Facultys = data
    },  
    GET_FACULTY(state,data){
        state.Faculty = data
    },  
    
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
    getFacultys({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
                .get('/faculty')
                    .then(res=>{
                        commit('GET_FACULTYS',res.data)
                        resolve(res.data)
                    })
                    .catch(err=>{
                        reject(err)
                    })
                })
    },
    getFacultysList({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
                .get('/faculty/list')
                    .then(res=>{
                        commit('GET_FACULTYS',res.data)
                        resolve(res.data)
                    })
                    .catch(err=>{
                        reject(err)
                    })
                })
    },
    getFaculty({commit},id){
       return new Promise((resolve,reject)=>{
            axios
                .get('/faculty/'+id)
                    .then(res=>{
                        commit('GET_FACULTY',res.data)
                        resolve(res.data)
                    })
                    .catch(err=>{
                        reject(err)
                    })
       }) 
        
    },
    
    addFaculty({commit},faculty){
    
        return new Promise((resolve,reject)=>{
            axios 
                .post('/faculty',faculty)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        // console.log(error);   
                        reject(error)
                    })
        })

    },
    editFaculty({commit},faculty){
        return new Promise((resolve,reject)=>{
            axios 
                .put('/faculty/'+faculty._id,faculty.data)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    updateFacultyMainProfessors({commit},faculty){
        return new Promise((resolve,reject)=>{
            axios 
                .put('/faculty/main-professor/'+faculty._id,{
                    "mainProfessors":faculty.mainProfessors
                })
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    
    deleteFaculty({commit},_id){
        return new Promise((resolve,reject)=>{
            axios 
                .delete('/faculty/'+_id)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    
    

    
    
}

export default {
    state,
    getters,
    mutations,
    actions
}