        </router-link>
        <router-link class="" to="/admin/course">
          <v-list-item class="menu-list" :class="isActive('AdminCourses,AdminCourse')">
            <v-list-item-icon>
              <v-icon class="">mdi-book-open-page-variant</v-icon>
            </v-list-item-icon>

            <v-list-item-title class="">Courses</v-list-item-title>
          </v-list-item>
        </router-link>
        <router-link class="" to="/admin/school-times-table">
          <v-list-item class="menu-list" :class="isActive('AdminSchoolTimesTable')">
            <v-list-item-icon>
              <v-icon class="">mdi-timer</v-icon>
            </v-list-item-icon>

            <v-list-item-title class="">School Times Table</v-list-item-title>
          </v-list-item>
        </router-link>
        <router-link class="" to="/admin/admission-applications">
          <v-list-item
            class="menu-list"
