        <v-list-group :value="false" class="" prepend-icon="mdi-account-multiple">
          <template v-slot:activator>
            <v-list-item-title class="">Students</v-list-item-title>
          </template>

          <v-list-group
            v-for="i in 7"
            :key="i"
            :value="false"
            @click="goTo(`/admin/student/${i * 100}`)"
            :class="isSubActive('AdminStudents', `${i * 100}`)"
            no-action
            sub-group
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>{{ `${i * 100}` }} Level</v-list-item-title>
              </v-list-item-content>
            </template>
          </v-list-group>
        </v-list-group>
        <router-link
          v-if="
            AdminProfilState.function == 'Director' ||
            AdminProfilState.function == 'Main Professor'
          "
          class=""
          to="/admin/professor"
        >
          <v-list-item class="menu-list" :class="isActive('AdminProfessors')">
            <v-list-item-icon>
              <v-icon class="">mdi-account-box-multiple</v-icon>
