        <router-link class="" to="/admin/contact">
          <v-list-item class="menu-list" :class="isActive('AdminContactList')">
            <v-list-item-icon>
              <v-icon class="">mdi-gmail</v-icon>
            </v-list-item-icon>

            <v-list-item-title class="">Contact</v-list-item-title>
          </v-list-item>
        </router-link>
        <router-link class="" to="/admin/setting">
          <v-list-item class="menu-list" :class="isActive('AdminSetting')">
            <v-list-item-icon>
              <v-icon class="">mdi-cog</v-icon>
            </v-list-item-icon>

            <v-list-item-title class="">Setting</v-list-item-title>
          </v-list-item>
        </router-link>
      </v-list>
    </v-card>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";

export default {
  methods: {
    ...mapActions(["addMarketDepartmentModal", "addMarketCategorieModal"]),
    goTo(link) {
      this.$router.push(link);
    },
    isActive(name) {
      name = name.split(",");
      for (let i = 0; i < name.length; i++) {
        if (this.$route.name == name[i]) return "active";
      }
      return "";
    },
    isSubActive(name, level) {
      if (this.$route.name == name && level == this.$route.params.level)
        return "sub-active";
      return "";
    },
  },
  computed: {
    ...mapGetters(["AdminProfilState", "Collapse"]),
  },
  data: () => ({}),
};
</script>

<style>
.sidenaves.menu {
  letter-spacing: 0.1rem;
  font-size: 1.3rem;
}

.sidenaves.menu .icon {
  color: rgb(172, 171, 171) !important;
  transition: all ease-in 0.3s;
}
.sidenaves.menu a,
.sidenaves.menu div,
.sidenaves.menu i {
  color: rgb(172, 171, 171) !important;
  transition: all ease-in 0.3s;
}

.sidenaves.menu .menu-list:hover {
  background-color: rgba(0, 162, 255, 0.801);
}
.sidenaves.menu div,
.sidenaves.menu a {
  color: white !important;
}
.sidenaves.menu i {
  color: white !important;
}
.sidenaves.menu .active div,
.sidenaves.menu .active a,
.sidenaves.menu .active i {
  color: white !important;
}
.sidenaves.menu .active {
  background-color: rgb(0, 162, 255);
  margin: 0 0.1rem;
}
.sidenaves.menu .sub-active * {
  color: rgb(0, 162, 255) !important;
  /* margin: 0 0.1rem; */
}
</style>
