<template>
  <div>
    <div class="d-md-flex">
      <v-tabs
        v-model="tab"
        class="mb-3"
        color="blue"
        background-color="white"
        left
        icons-and-text
      >
        <v-tabs-slider></v-tabs-slider>

        <v-tab v-if="article" href="#article">
          {{ document }}
          <v-icon>mdi-email-newsletter</v-icon>
        </v-tab>

        <v-tab v-if="article" href="#update-article">
          Update {{ document }}
          <v-icon>mdi-file-document-edit-outline</v-icon>
        </v-tab>

        <v-tab v-if="!article" href="#update-article">
          Create {{ document }}
          <v-icon>mdi-file-document-edit-outline</v-icon>
        </v-tab>
        <v-tab href="#preview-article">
          Preview
          <v-icon>mdi-view-agenda-outline</v-icon>
        </v-tab>
      </v-tabs>
      <div class="d-flex justify-center align-center mr-md-4">
        <v-btn
          :loading="loading"
          class="main rounded px-3 py-3 white--text"
          @click="SaveArticle"
          >Save
        </v-btn>
      </div>
    </div>
    <v-tabs-items v-model="tab">
      <v-tab-item :value="'article'">
        <v-col cols="12" class="article">
          <article v-html="article"></article>
        </v-col>
      </v-tab-item>
      <v-tab-item :value="'preview-article'">
        <v-col cols="12" class="article">
          <article v-html="body"></article>
        </v-col>
      </v-tab-item>
      <v-tab-item :value="'update-article'">
        <v-col cols="12" class="">
          <div class="red--text text-center" v-if="errors">
            {{ errors }}
          </div>
          <quill-editor
            ref="editor"
            v-model="body"
            class="mt-5"
            :options="editorOption"
          />
        </v-col>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";

import "quill/dist/quill.snow.css";

import axios from "axios";

import hljs from "highlight.js";
// import "highlight.js/styles/dracula.css";
import "highlight.js/styles/atom-one-dark.css";

export default {
  components: {
    quillEditor,
  },
  props: {
    article: {},
    link: {},
    document: {
      default: "article",
    },
    online: {
      default: true,
    },
    timeLimite: {
      default: false,
    },
  },
  methods: {
    Initialize() {
      this.body = this.article;
    },
    async SaveArticle() {
      this.errors = "";
      let timeLimite = undefined;
      const online = this.online ?? undefined;
      if (this.timeLimite != false && this.timeLimite != undefined) {
        if (
          !this.timeLimite.date.start ||
          !this.timeLimite.date.end ||
          !this.timeLimite.time.start ||
          !this.timeLimite.time.end
        )
          return (this.errors = "The time limite is not valide");
        const start = new Date(this.timeLimite.date.start);
        const end = new Date(this.timeLimite.date.end);
        if (start.getTime() > end.getTime())
          return (this.errors = "The time limite is not valide");
        timeLimite = this.timeLimite;
      }

      if (this.body == "" || this.body == undefined)
        return (this.errors = "Can not save empty body");
      this.loading = true;
      try {
        await axios.put(`${this.link}`, { article: this.body, timeLimite, online });
        this.loading = false;
        this.message = "Chapiter article save ";
        this.snackbar = true;

        this.$emit("refresh");
        this.tab = "article";
      } catch (err) {
        this.loading = false;
        this.message = "failed to save this article";
        this.snackbar = true;
        this.errors = "failed to save this article";
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      tab: null,
      loading: false,
      errors: "",
      snackbar: false,
      message: "",
      body: "",
      editorOption: {
        // Some Quill options...
        theme: "snow",

        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            ["blockquote", "code-block"],
            [{ header: 1 }, { header: 2 }],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }],
            [{ indent: "-1" }, { indent: "+1" }],
            [{ direction: "rtl" }],
            [{ size: ["small", false, "large", "huge"] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ font: [] }],
            [{ color: [] }, { background: [] }],
            [{ align: [] }],
            ["clean"],
            ["link", "image", "video"],
          ],
          syntax: {
            highlight: (text) => hljs.highlightAuto(text).value,
          },
        },
      },
    };
  },
};
</script>

<style></style>
