<template>
  <div class="bg-tkp h-100">
    <!-- {{ CourseState.other_informations.permission }} -->
    <div class=" " id="admin-profil" v-if="loader == false && CourseState.name">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            Dashbord /
            <router-link to="/admin/course" class="main--text">Course</router-link>
          </h3>
          <div class="d-flex flex-column flex-md-row">
            <router-link
              class="px-3 py-3 mr-md-3 my-3 elevation-1 white rounded-pill secondary--text"
              :to="`/admin/visit-course/${$route.params.id}`"
              >Visite this course</router-link
            >
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
            <a
              href=""
              v-if="AdminProfilState.function == 'Director'"
              @click.prevent="Edit(CourseState)"
              class="px-3 py-3 my-3 white elevation-1 rounded-pill blue-grey--text"
              >Edit Course</a
            >
            <a
              href=""
              v-if="AdminProfilState.function == 'Director'"
              @click.prevent="Delete(CourseState._id)"
              class="px-3 py-3 ml-md-3 my-3 red elevation-1 rounded-pill white--text"
              >Delete Course</a
            >
          </div>
        </div>

        <div class="mt-5" v-if="CourseState.name">
          <v-container class="white px-1 px-md-5 py-5 rounded" fluid>
            <div class="border-b pb-2">
              <h1 class="text-h4">
                {{ CourseState.name }} -
                <span class="main--text">{{ CourseState.code }}</span>
              </h1>
              <p class="text-sm my-2">{{ CourseState.level }} level</p>

              <div class="mt-5 border-t py-3">
                <div class="mb-8">
                  <p class="bold">Department(s) :</p>
                  <ul>
                    <li
                      v-for="department in CourseState.other_informations.departments"
                      :key="department._id"
                    >
                      {{ department.name }}
                    </li>
                  </ul>
                </div>
                <p>
                  <span class="bold mr-3 d-block">Professor: </span>
                </p>

                <div v-if="CourseState.professor">
                  <v-list-item>
                    <v-list-item-avatar>
                      <v-img
                        :src="CourseState.other_informations.professor.profilPicture"
                        alt=""
                      />
                    </v-list-item-avatar>
                    <v-list-item-title v-if="CourseState.other_informations"
                      ><span class="bold"
                        >{{ CourseState.other_informations.professor.grade }}
                      </span>
                      {{ CourseState.other_informations.professor.firstname }}
                      {{ CourseState.other_informations.professor.lastname }}
                      <a
                        href=""
                        @click.prevent="(professorDialog = true), getProfessor()"
                        class="ml-md-12 main--text"
                        v-if="isAuthorized()"
                        >Change Professor
                      </a>
                    </v-list-item-title>
                  </v-list-item>
                </div>

                <a
                  href=""
                  @click.prevent="(professorDialog = true), getProfessor()"
                  class="main--text"
                  v-else-if="isAuthorized()"
                >
                  Add Professor
                </a>
                <span class="text-sm" v-else>Not Assign</span>
              </div>
            </div>
            <div class="border-t py-2">
              <h3 class="text-h5 mb-2">Course Management</h3>
              <div class="mt-4">
                <router-link
                  class="main rounded elevation-1 px-2 py-2 white--text"
                  :to="`/admin/course/${CourseState._id}/transcript`"
                  >Transcript</router-link
                >
              </div>
            </div>
          </v-container>
        </div>
      </div>
    </div>
    <div v-else-if="loader == false && !CourseState.name">
      <can-not-find what="this Courses" />
    </div>
    <v-slide-x-transition>
      <v-dialog v-model="Modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="(Modal = false), (selected = [])"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h1>{{ ModalText.title }}</h1>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.name"
                  :rules="nameRules"
                  label="Name"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.code"
                  :rules="codeRules"
                  label="Code"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-select
                  :items="levels"
                  color="red darken-3"
                  :rules="levelRules"
                  v-model="ModalModel.level"
                  label="Level"
                  class=""
                  required
                ></v-select>
              </div>
              <div>
                <v-card
                  class="py-3 mt-3 mb-12 elevation-0"
                  v-if="
                    AdminProfilState.function == 'Director' ||
                    AdminProfilState.function == 'Main Professor' ||
                    AdminProfilState.function == 'Staff'
                  "
                  color="grey lighten-4"
                >
                  <v-toolbar flat class="rounded" color="transparent">
                    <p class="text-h5 mt-3">Departments</p>
                    <v-spacer></v-spacer>
                    <v-btn v-if="!allSelected" icon @click="addDepartment = true">
                      <v-icon color="blue">mdi-plus-circle-outline</v-icon>
                    </v-btn>
                  </v-toolbar>

                  <v-container class="py-0">
                    <v-row align="center" justify="start">
                      <v-col v-for="(selection, i) in selections" :key="i" class="shrink">
                        <v-chip
                          :disabled="loading"
                          close
                          @click:close="selected.splice(i, 1)"
                        >
                          <!-- <v-icon left v-text="selection"></v-icon> -->
                          {{ selection }}
                        </v-chip>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card>
              </div>

              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="validates"
                  width="100%"
                >
                  {{ ModalText.btn }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>

    <v-slide-y-transition>
      <v-dialog v-model="Dialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this course ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every exercice ,exams ... about this course will be deleted
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="bg-secondaire white--text" text @click="CancelDelete()">
              Cancel
            </v-btn>
            <v-btn
              class="red white--text"
              :loading="loading"
              text
              @click="ConfirmDelete(deleteId)"
            >
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>
    <v-slide-y-transition>
      <v-dialog max-width="45rem" v-model="addDepartment">
        <v-list class="px-md-12 px-3 py-md-12">
          <div class="d-flex justify-space-between">
            <span class="text-h5 black--text d-block my-2">Choose departments</span>
            <a href="" class="main--text text-h5" @click.prevent="addDepartment = false"
              >Ok</a
            >
          </div>
          <div class="mt-5" v-for="item in departments" :key="item">
            <div class="">
              <v-list-item
                v-if="!selected.includes(item)"
                :key="item"
                :disabled="loading"
                @click="selected.push(item)"
              >
                <!-- <v-list-item-avatar>
                  <v-icon :disabled="loading" v-text="item.icon"></v-icon>
                </v-list-item-avatar> -->
                <v-list-item-title v-text="item"></v-list-item-title>
              </v-list-item>
            </div>
          </div>
          <div v-if="allSelected" class="text-center">
            Every department is already selected
          </div>
        </v-list>
      </v-dialog>
    </v-slide-y-transition>

    <v-slide-y-transition>
      <v-dialog max-width="45rem" v-model="professorDialog">
        <v-list class="px-md-12 px-3 py-md-12">
          <div class="d-flex justify-space-between">
            <span class="text-h5 black--text d-block my-2">Choose Professors</span>
            <a href="" class="main--text text-h5" @click.prevent="professorDialog = false"
              >Cancel</a
            >
          </div>
          <circulate-load v-if="circulateLoader" />
          <div v-if="circulateLoader !== true && AdminsState">
            <div class="mt-5" v-for="item in AdminsState" :key="item._id">
              <div class="">
                <v-list-item
                  v-if="professor == undefined || professor._id != item._id"
                  :key="item._id"
                  :disabled="loading"
                  @click="(professor = item), (professorDialog = false)"
                >
                  <v-list-item-avatar>
                    <v-img aspect-ratio="1" :src="item.profilPicture" alt="" />
                  </v-list-item-avatar>
                  <v-list-item-title
                    >{{ item.firstname }} {{ item.lastname }}</v-list-item-title
                  >
                </v-list-item>
              </div>
            </div>
          </div>
        </v-list>
      </v-dialog>
    </v-slide-y-transition>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>

    <circulate-load v-if="loader" />

    <function ref="function" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Function from "../../components/universal/function.vue";

export default {
  components: {
    CanNotFind,
    CirculateLoad,
    Function,
  },
  computed: {
    ...mapGetters([
      "CourseState",
      "AdminProfilState",
      "DepartmentsState",
      "AdminsState",
      "ErrorBag",
    ]),
    allSelected() {
      return this.selected.length === this.departments.length;
    },
    categories() {
      const search = this.search.toLowerCase();

      if (!search) return this.deparmtents.names;

      return this.departments.filter((item) => {
        const text = item.toLowerCase();

        return text.indexOf(search) > -1;
      });
    },
    selections() {
      const selections = [];

      for (const selection of this.selected) {
        selections.push(selection);
      }

      return selections;
    },
  },
  methods: {
    ...mapActions([
      "getCourse",
      "updateCourse",
      "deleteCourse",
      "getDepartments",
      "getProfessors",
      "assignProfessor",
    ]),
    startLoading() {
      this.loading = true;
      this.loader = true;
    },
    stopLoading() {
      this.loading = false;
      this.loader = false;
    },
    async Refresh() {
      this.startLoading();
      try {
        await this.getCourse(this.$route.params.id);
        this.message = "Faculty refresh successfully";
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.snackbar = true;
      this.stopLoading();
    },
    setDepartment() {
      const departments = [];
      this.DepartmentsState.forEach((item) => {
        departments.push(item.name);
      });
      this.departments = departments;
    },
    async Initialize() {
      this.ModalText.title = "Add new Course";
      this.ModalText.btn = "Add";
      this.ModalModel = {};
      this.selected = [];
      this.startLoading();
      this.setDepartment();
      try {
        await this.getCourse(this.$route.params.id);
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.stopLoading();
    },

    async Edit(item) {
      item.other_informations.departments.forEach((department) => {
        this.selected.push(department.name);
      });
      this.Modal = true;
      this.ModalModel = item;
      this.ModalText.title = "Edit Course";
      this.ModalText.btn = "Update";
    },
    Delete(id) {
      this.Dialog = true;
      this.deleteId = id;
    },
    CancelDelete() {
      this.deleteId = "";
      this.Dialog = false;
    },
    async ConfirmDelete(id) {
      this.loading = true;
      try {
        await this.deleteCourse(id);
        this.message = "Course deleted successfully";
        this.snackbar = true;

        setTimeout(() => {
          this.$router.push("/admin/course");
        }, 1000);
      } catch (error) {
        this.message = "Failed to delete this course";
      }
      this.snackbar = true;
      this.stopLoading();
      this.CancelDelete();
    },
    getDepartmentName(id) {
      for (let i = 0; i < this.DepartmentsState.length; i++) {
        const deparment = this.DepartmentsState;
        if (deparment[i]._id == id) {
          return deparment[i].name;
        }
      }
    },
    getDepartmentId(names) {
      this.departments_id = [];
      names.forEach((name) => {
        this.DepartmentsState.forEach((item) => {
          if (item.name == name) {
            this.departments_id.push(item._id);
          }
        });
      });
    },
    async validates() {
      if (this.$refs.form.validate()) {
        if (this.selected.length < 1)
          return (this.errors = "Department is required to create a course ");
        this.loading = true;
        this.getDepartmentId(this.selected);
        const ModelAdd = {
          name: this.ModalModel.name,
          code: this.ModalModel.code,
          level: this.ModalModel.level,
          departments: this.departments_id,
        };

        if (this.ModalModel._id) {
          // update
          const ModalModelUpdate = {
            data: ModelAdd,
            _id: this.ModalModel._id,
          };
          try {
            await this.updateCourse(ModalModelUpdate);
            this.message = "Course updated successfully";
          } catch (error) {
            this.message = "Failed to edit this course";
          }
        } else {
          // add
          try {
            await this.addCourse(ModelAdd);
            this.message = "Course added successfully";
          } catch (error) {
            this.message = "Failed to add course";
          }
        }
        this.loading = false;
        this.Modal = false;
        this.snackbar = true;
        this.getDepartments();
        this.Initialize();
      }
    },
    isAuthorized() {
      if (this.AdminProfilState.function == "Director") return true;
      const permissions = this.CourseState.other_informations.permission;
      for (let i = 0; i < permissions.length; i++) {
        if (this.AdminProfilState._id == permissions[i]) return true;
      }
      return false;
    },
    getMainProfessorFromName(names) {
      let result = [];
      for (let c = 0; c < names.length; c++) {
        let name = names[c];
        for (let i = 0; i < this.CourseState.mainProfessors.list.length; i++) {
          let professor = this.CourseState.mainProfessors.list[i];
          if (professor.firstname + " " + professor.lastname == name) {
            result.push(professor._id);
            break;
          }
        }
      }
      return result;
    },

    async getProfessor() {
      this.professor = this.CourseState.other_informations.professor;
      this.circulateLoader = true;
      try {
        await this.getProfessors();
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.circulateLoader = false;
    },
  },
  mounted() {
    this.Initialize();
    this.getDepartments();
  },
  watch: {
    selected() {
      this.search = "";
    },
    async professor(professor) {
      let id = "";
      for (let i = 0; i < this.AdminsState.length; i++) {
        if (professor == this.AdminsState[i]) {
          id = professor._id;
          break;
        }
      }

      if (!id) return false;
      const prof = {
        _id: this.$route.params.id,
        data: {
          professor: id,
        },
      };
      this.loader = true;
      try {
        await this.assignProfessor(prof);
        this.message = `Course assign to  ${professor.firstname} ${professor.lastname}`;
        this.snackbar = true;
      } catch (error) {
        this.message = `Failed to assign this course  to  ${professor.firstname} ${professor.lastname}`;
      }
      this.loader = false;
      this.snackbar = true;
      this.Initialize();
    },
  },
  data() {
    return {
      deleteId: "",
      Dialog: "",
      professorDialog: false,
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      circulateLoader: false,
      valid: true,
      nameRules: [(v) => !!v || "name is required"],
      codeRules: [(v) => !!v || "course code  is required"],
      departmentRules: [(v) => !!v || "department is required"],
      levelRules: [(v) => !!v || "level is required"],
      professor: "",
      selected: [],
      chips: [],
      departments: [],
      departments_id: [],
      addDepartment: false,
      levels: ["100", "200", "300", "400", "500", "600", "700"],
      Modal: false,
      ModalText: {
        title: "Add new Faculty",
        btn: "Add",
      },
      ModalModel: {},
      search: "",
      headers: [
        { text: "Name", value: "name" },
        { text: "Accronyme", value: "acronym" },
        { text: "Description", value: "description" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
