import axios from 'axios'

const state ={
    Student :{}
   
}

const getters = {
    StudentProfilState : (state)=>state.Student
    
}

const mutations ={
    
    GET_STUDENT_PROFIL(state,data){
        state.Student = data
    },  
    
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
    getStudentProfil({commit}){
        return new Promise((resolve,reject) =>{
            axios
            .get('/student/profil')
                .then(res=>{
                    commit('GET_STUDENT_PROFIL',res.data)
                    resolve(res.data)
                })
                .catch(err=>{
                    reject(err)
                })
        })    
    
    },
}

export default {
    state,
    getters,
    mutations,
    actions
}