<template>
  <div>
    <div class="d-md-flex justify-space-between">
      <v-tabs
        v-model="tab"
        class="mb-3"
        color="blue"
        background-color="white"
        left
        icons-and-text
      >
        <v-tabs-slider></v-tabs-slider>

        <v-tab v-if="exercice" href="#update">
          Update {{ document }}
          <v-icon>mdi-file-document-edit-outline</v-icon>
        </v-tab>

        <v-tab v-if="!exercice" href="#update">
          Create {{ document }}
          <v-icon>mdi-file-document-edit-outline</v-icon>
        </v-tab>
        <v-tab href="#preview-exercice" @click.prevent="preview">
          Preview
          <v-icon>mdi-view-agenda-outline</v-icon>
        </v-tab>
      </v-tabs>
      <div class="mt-4 text-h5 mr-4">
        <span
          :class="Number(markCount) == Number(mark) ? 'green--text' : ''"
          class="mx-3 d-flex"
          ><span :class="Number(markCount) > Number(mark) ? 'red--text' : ''"
            >{{ markCount }}/</span
          >
          <span class="bold">{{ mark }}</span></span
        >
      </div>
      <div class="d-flex justify-center align-center mr-md-4">
        <div class="d-flex">
          <v-select
            :items="marks"
            v-model="mark"
            class="mr-3"
            :disabled="tab == 'update' ? false : true"
            :rules="[(v) => !!v || 'Total Mark is required']"
            label="Total Mark"
            dense
            outlined
          ></v-select>
          <v-btn v-if="tab == 'update'" icon class="main--text" @click="addQuiz"
            ><v-icon class="text-h5">mdi-plus-circle-outline</v-icon></v-btn
          >
          <v-btn
            :loading="loading"
            class="main rounded ml-3 px-3 py-3 my-0 white--text"
            @click="saveExercice"
            >Save
          </v-btn>
        </div>
      </div>
    </div>
    <v-tabs-items v-model="tab">
      <v-tab-item :value="'update'">
        <div class="container">
          <div class="text-center red--text py-3">
            {{ errors }}
          </div>
          <v-form ref="quizsForm" lazy-validation>
            <v-row v-for="(quiz, n) in Quizs" :key="n" class="border-t pt-4">
              <v-col cols="12" md="6">
                <v-text-field
                  :label="'Question ' + (n + 1)"
                  v-model="quiz.question"
                  dense
                  outlined
                  :rules="[(v) => !!v || 'Question is required']"
                ></v-text-field>
              </v-col>
              <v-col class="d-flex" cols="4" md="2">
                <v-text-field
                  label="Mark"
                  v-model="quiz.mark"
                  dense
                  outlined
                  :rules="markRules"
                ></v-text-field>
              </v-col>
              <v-col class="d-flex" cols="4" md="2">
                <v-text-field
                  label="Bad Answer"
                  v-model="quiz.badAnswerMark"
                  dense
                  outlined
                  :rules="badAnswerMarkRules"
                ></v-text-field>
              </v-col>
              <v-col class="" cols="2" md="1">
                <v-btn
                  icon
                  color="orange"
                  @click="quiz.answers.push({ choice: '', status: false })"
                  class="mt-1"
                  ><v-icon class="text-h5">mdi-comment-plus-outline</v-icon></v-btn
                >
              </v-col>
              <v-col class="" cols="2" md="1">
                <v-btn icon color="red" class="mt-1" @click="Quizs.splice(n, 1)"
                  ><v-icon class="text-h5">mdi-head-minus</v-icon></v-btn
                >
              </v-col>

              <v-col cols="12">
                <v-row v-for="(answer, i) in quiz.answers" :key="i">
                  <v-col cols="12" md="8">
                    <v-text-field
                      :label="'Choice ' + (i + 1)"
                      dense
                      v-model="answer.choice"
                      outlined
                      :rules="[(v) => !!v || 'Answer is required']"
                    ></v-text-field>
                  </v-col>
                  <v-col class="d-flex" cols="6" md="2">
                    <v-select
                      :items="[true, false]"
                      v-model="answer.status"
                      label="Is true"
                      dense
                      outlined
                    ></v-select>
                  </v-col>
                  <v-col class="" cols="4" md="1">
                    <v-btn
                      icon
                      color="red"
                      class="mt-1"
                      @click="quiz.answers.splice(i, 1)"
                      ><v-icon class="text-h5">mdi-delete</v-icon></v-btn
                    >
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-form>
        </div>
      </v-tab-item>

      <v-tab-item :value="'preview-exercice'">
        <div class="container" v-if="isValid">
          <div v-for="(quiz, q) in Quizs" :key="q">
            <div>
              <p class="mb-1">
                <span class="bold">Q-{{ q + 1 }}</span> : {{ quiz.question }}
                <span class="main--text">({{ quiz.mark }} pts)</span>
              </p>
              <p class="">
                <span class="bold">Bad answer :</span>
                <span class="red--text">- {{ quiz.badAnswerMark }} pts</span>
              </p>
              <div>
                <p class="bold">Answers</p>
                <div
                  class="d-flex justify-space-between"
                  v-for="(answer, a) in quiz.answers"
                  :key="a"
                >
                  <p>
                    <span class="bold">{{ a + 1 }}-)</span> {{ answer.choice }}
                  </p>
                  <p :class="answer.status ? 'green--text' : 'red--text'">
                    {{ answer.status }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-else class="container">Quiz must be valid before preview</div>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    document: {
      default: "Quiz",
    },
    exercice: {},
    link: {},
    online: {
      default: true,
    },
    important: {
      default: false,
    },
    day: {
      default: undefined,
    },
    duration: {
      required: true,
    },
    mark: {
      required: true,
    },
    answer: {
      default: "Text",
    },
  },
  computed: {
    markCount() {
      let result = 0;

      this.Quizs.forEach((quiz) => {
        result += Number(quiz.mark);
      });
      return Number.isNaN(result) ? 0 : result;
    },
  },
  methods: {
    Initialize() {
      this.Quizs = this.exercice ?? [
        {
          question: "",
          mark: null,
          badAnswerMark: "",
          answers: [
            {
              choice: "",
              status: false,
            },
          ],
        },
      ];
    },
    addQuiz() {
      this.Quizs.push({
        question: "",
        mark: null,
        badAnswerMark: "",
        answers: [
          {
            choice: "",
            status: false,
          },
        ],
      });
    },
    preview() {
      if (this.$refs.quizsForm.validate()) {
        this.isValid = true;
      } else this.isValid = false;
    },

    async saveExercice() {
      this.errors = "";
      if (this.$refs.quizsForm.validate()) {
        this.isValid = true;
        //multiple choice quiz validation
        this.Quizs.forEach((quiz) => {
          if (Number(quiz.mark) < Number(quiz.badAnswerMark)) {
            return (this.errors =
              "Some mark for bad answer is greater than the question mark");
          }
        });
        if (this.markCount != this.mark)
          return (this.errors =
            "The sum of the question mark doesn't equal to the total mark of this exercice ");
        if (this.errors) return;

        // composition options validation
        let day = undefined;
        const composition = this.document;
        const mark = this.mark ?? undefined;
        const duration = this.duration ?? undefined;
        const answer = this.answer;
        if (!Number.isInteger(Number(duration)))
          return (this.errors = "Duration is not valid");
        if (!mark) return (this.errors = "Total mark is required");
        const online = this.online ?? undefined;
        const important = this.important ?? false;
        if (this.day != false && this.day != undefined) {
          if (!this.day.date || !this.day.time)
            return (this.errors = "Your time or your date is not valid");
          const dayDate = new Date(`${this.day.date} ${this.day.time}`);
          const today = new Date();
          if (dayDate.getTime() < today.getTime())
            return (this.errors = "We have already passed that day");
          day = this.day;
        }

        try {
          await axios.post(this.link, {
            questions: this.Quizs,
            day,
            duration,
            online,
            important,
            mark,
            answer,
            composition,
          });

          this.$emit("refresh");
          this.tab = "exercice";
        } catch (error) {
          console.log(error);
          this.errors = "Something went wrong";
        }
        this.loading = false;
      } else this.isValid = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      tab: null,
      loading: false,
      errors: "",
      marks: ["100", "20", "10"],
      markRules: [
        (v) => !!v || "Mark is required",
        (v) => (!!v && Number.isInteger(Number(v))) || "Mark must be number",
      ],
      badAnswerMarkRules: [(v) => !!v || "Mark is required"],
      isValid: false,
      Quizs: [],
    };
  },
};
</script>

<style></style>
