<template>
  <div class="bg-tkp h-100">
    <v-container>
      <v-row>
        <v-col class="rounded" cols="12" md="3">
          <div class="idPicture">
            <v-img
              aspect-ratio="1"
              v-if="!isProfessor"
              class="rounded"
              :src="Transcript.student.idPicture"
            />
            <v-icon color="black" v-else class="avatar-svg">mdi-account-tie</v-icon>
          </div>
        </v-col>
        <v-col cols="12" md="3">
          <div v-if="!isProfessor">
            <h3>
              {{ Transcript.student.surname + " " + Transcript.student.othernames }}
            </h3>
            <p class="mb-2">{{ Transcript.student.registration_number }}</p>
            <p class="mb-0 main--text">
              {{ Transcript.student.degre }} Level .
              {{ Transcript.student.session }}
            </p>
            <div class="mt-2">
              <router-link
                v-if="!StudentProfilState"
                :to="`/admin/show-student/${Transcript.student._id}`"
                class="main white--text rounded px-2 py-1"
                >Show</router-link
              >
            </div>
          </div>
        </v-col>
        <v-col cols="12" md="6">
          <router-link :to="`/admin/visit-course/${Transcript.course._id}`" class="bold">
            <span class="d-inline black--text">{{ Transcript.course.name }}</span>
            -
            <span class="d-inline main--text">{{ Transcript.course.code }}</span>
          </router-link>
          <div>
            <div v-if="!Transcript.offline">
              <v-simple-table v-if="Transcript.transcript.generate" class="mt-5">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">Test 1</th>
                      <th class="text-left">Test 2</th>
                      <th class="text-left">Test 3</th>
                      <th class="text-left">Exam</th>
                      <th class="text-left">Average</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{{ Transcript.transcript.generate.t1 }}</td>
                      <td>{{ Transcript.transcript.generate.t2 }}</td>
                      <td>{{ Transcript.transcript.generate.t3 }}</td>
                      <td>{{ Transcript.transcript.generate.exam }}</td>
                      <td>{{ Transcript.transcript.generate.average }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </div>
            <can-not-find
              v-else
              :small="true"
              :message="`This collection of notes is not yet available`"
            />
          </div>
        </v-col>
      </v-row>
      <div v-if="!Transcript.offline">
        <div class="mt-12 py-8 white">
          <h3 class="text-h4 text-center">Exam Paper</h3>
          <exam-paper :ExamPaper="Transcript.transcript.other_informations.examPaper" />
        </div>
        <div
          v-for="(test, i) in Transcript.transcript.other_informations.testPapers"
          :key="i"
          class="mt-12 py-8 white"
        >
          <h3 class="text-h4 text-center">Test Paper {{ i + 1 }}</h3>
          <test-paper :TestPaper="test" />
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import ExamPaper from "../professor/ExamPaper.vue";
import TestPaper from "../professor/TestPaper.vue";
import { mapGetters } from "vuex";
import CanNotFind from "./CanNotFind.vue";
export default {
  components: { ExamPaper, TestPaper, CanNotFind },
  props: {
    Transcript: {
      required: true,
    },
    isProfessor: {
      default: false,
    },
    isStudent: {
      default: true,
    },
  },
  computed: {
    ...mapGetters(["StudentProfilState"]),
  },
};
</script>

<style>
.idPicture {
  /* height: 15rem !important; */
  width: 100%;
}
.idPicture img {
  /* height: 100% !important; */
  width: 100%;
}
</style>
