<template>
  <v-container class="px-md-12">
    <v-card>
      <v-card-title>
        <v-card-subtitle>
          <div class=""></div>
        </v-card-subtitle>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :loading="loading"
        :loading-text="'chargement...'"
        :headers="headers"
        :items="Students"
        :search="search"
      >
        <template v-slot:item.avatar="{ item }">
          <div v-if="item.other_informations" class="my-3">
            <v-avatar v-if="item.other_informations.student" size="3rem">
              <v-img :src="item.other_informations.student.idPicture" alt="alt" />
            </v-avatar>
          </div>
        </template>
        <template v-slot:item.fullname="{ item }">
          <div v-if="item.other_informations" class="my-3">
            <div v-if="item.other_informations.student">
              {{ item.other_informations.student.surname }}
              {{ item.other_informations.student.othernames }}
            </div>
          </div>
        </template>

        <template v-slot:item.average="{ item }">
          {{ item.average }}
        </template>
        <template v-slot:item.action="{ item }">
          <td v-if="item.other_informations">
            <router-link
              v-if="item.other_informations.student"
              :to="`/admin/show-student/${item.other_informations.student._id}/transcript/${GenerateTranscript.course}`"
              class="rounded-circle white--text px-2 py-3 mr-3"
              ><v-icon class="green--text">mdi-arrow-right</v-icon>
            </router-link>
          </td>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
export default {
  props: {
    GenerateTranscript: {
      required: true,
    },
  },
  computed: {
    Students() {
      if (this.GenerateTranscript) {
        return this.GenerateTranscript.transcript ?? [];
      }
      return [];
    },
  },
  methods: {},
  beforeMount() {},
  data() {
    return {
      testNumber: 0,
      valid: true,
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      search: "",
      headers: [
        {
          text: "",
          value: "avatar",
        },
        {
          text: "Fullname",
          value: "fullname",
        },
        {
          text: "Test 1",
          value: "t1",
        },
        {
          text: "Test 2",
          value: "t2",
        },
        {
          text: "Test 3",
          value: "t3",
        },
        {
          text: "Exam",
          value: "exam",
        },
        {
          text: "Average (/20)",
          value: "average",
        },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
