<template>
  <div class="h-100 bg-tkp">
    <div v-if="loader == false">
      <div v-if="!levelIsValid">
        <can-not-find :what="`this level (${$route.params.level}) `" />
      </div>
      <div v-if="DepartmentsState.list && levelIsValid">
        <div class="container">
          <h3 class="mb-3">Students</h3>
          <div class="d-flex justify-space-between my-4">
            <div class="d-flex">
              <v-select
                dense
                class=""
                :items="DepartmentsState.list"
                @click="Initialize"
                @change="fetchStudent"
                v-model="department"
                label="Department"
                outlined
              ></v-select>
              <v-select
                dense
                class="mx-3"
                :items="AccademicYears"
                @click="Initialize"
                @change="fetchStudent"
                v-model="session"
                label="Accademic Years"
                outlined
              ></v-select>
              <v-btn class="" @click="fetchStudent">Refresh</v-btn>
              <v-btn class="main d-none white--text rounded" @click.prevent="">
                Add</v-btn
              >
            </div>
          </div>
        </div>
      </div>
      <v-container v-if="levelIsValid">
        <message v-if="!department" :text="`Department is required`" />
        <message v-if="!session" :text="`The accademic year is required`" />
        <div v-if="StudentsState && department && session" class="">
          <div class="border-grey elevation-1 py-5 rounded mb-6 container">
            <v-row class="tkp">
              <v-col cols="12" class="" md="3">
                <div
                  class="container rounded border-grey d-flex justify-center align- flex-column"
                >
                  <v-btn class="elevation-1">Generate Transcript</v-btn>
                  <v-switch class="ml-2" inset :label="`Online`"></v-switch>
                </div>
              </v-col>
            </v-row>
          </div>
          <v-card>
            <v-card-title>
              {{ $route.params.level }} Level
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :loading-text="'chargement...'"
              :headers="headers"
              :items="StudentsState"
              :search="search"
              :loading="loading"
            >
              <template v-slot:item.idPicture="{ item }">
                <td class="py-4">
                  <v-avatar>
                    <v-img :src="item.idPicture" />
                  </v-avatar>
                </td>
              </template>

              <template v-slot:item.action="{ item }">
                <td>
                  <router-link :to="`/admin/show-student/${item._id}`"
                    ><v-btn color="green" icon>
                      <v-icon>mdi-arrow-right</v-icon></v-btn
                    ></router-link
                  >
                </td>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </v-container>
    </div>
    <circulate-load v-else-if="(loader = true)" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import circulateLoad from "../../components/universal/circulateLoad.vue";
import Message from "../../components/universal/Message.vue";

export default {
  components: { circulateLoad, CanNotFind, Message },
  computed: {
    ...mapGetters([
      "DepartmentsState",
      "ErrorBag",
      "StudentsState",
      "AccademicYears",
      "AdminProfilState",
      "Levels",
    ]),
    levelIsValid() {
      if (!this.inArray(this.Levels, this.$route.params.level)) return false;
      return true;
    },
  },
  mounted() {
    this.Initialize();
  },
  methods: {
    ...mapActions(["getDepartmentsList", "getStudents"]),
    async getDepartments() {
      const interval = setInterval(() => {
        this.getDepartmentsList();
        if (this.DepartmentsState.list) {
          clearInterval(interval);
        }
      }, 500);
    },
    async Initialize() {
      this.getDepartments();
    },
    async fetchStudent() {
      if (!this.levelIsValid || !this.department || !this.session) return;
      this.loader = true;
      const department = this.getDptId(this.department);
      const session = this.session;
      try {
        this.getStudents({
          level: this.$route.params.level,
          department,
          session,
        });
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loader = false;
    },
    getDptId(name) {
      for (let i = 0; i < this.DepartmentsState.departments.length; i++) {
        const department = this.DepartmentsState.departments[i];
        if (department.name == name) return department._id;
      }
    },
    inArray(array, item) {
      console.log(item);
      for (let c = 0; c < array.length; c++) {
        if (item == array[c]) return item;
      }
      return false;
    },
  },
  data() {
    return {
      department: "er",
      loader: false,
      loading: false,
      session: "null",
      search: "",
      headers: [
        {
          text: "",
          value: "idPicture",
        },
        {
          text: "Registration Number",
          value: "registration_number",
        },
        {
          text: "Surname",
          value: "surname",
        },

        { text: "Other names", value: "othernames" },
        { text: "Email", value: "email" },
        { text: "", value: "email" },
        { text: "Activate", value: "verify" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
