<template>
  <v-container class="px-md-12">
    <v-card>
      <v-card-title>
        <v-card-subtitle>
          <div class="">
            <div>
              <span class="main--text px-1 py-2 bold"> Exam :</span>
              <span class=""> {{ availableExam }}</span>
            </div>
            <div class="mt-2">
              <span class="main--text px-1 py-2 bold">Available Tests :</span>
              <span class=""> {{ availableTest }}</span>
            </div>
          </div>
        </v-card-subtitle>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="search"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :loading="loading"
        :loading-text="'chargement...'"
        :headers="headers"
        :items="Students"
        :search="search"
      >
        <template v-slot:item.student="{ item }">
          {{ item.student }}
        </template>
        <template v-slot:item.average="{ item }">
          {{ item.average }}
        </template>
        <template v-slot:item.action="{ item }">
          <td>
            <router-link
              :to="`/professor/course/${Transcript.course._id}/transcript/${item.student}`"
              class="rounded-circle white--text px-2 py-3 mr-3"
              ><v-icon class="green--text">mdi-arrow-right</v-icon>
            </router-link>
          </td>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
export default {
  props: {
    GenerateTranscript: {
      required: false,
    },
    Transcript: {
      required: false,
    },
  },
  computed: {
    Students() {
      if (this.GenerateTranscript) {
        console.log("here");
        return this.GenerateTranscript;
      }
      const students = this.Transcript.students;
      let results = [];
      this.Initialize();
      for (let c = 0; c < students.length; c++) {
        const student = students[c];
        let testMark = [];
        let testMark20 = [];
        let importantTest = [];
        let importantTest20 = [];
        for (let i = 0; i < student.tests.length; i++) {
          const test = student.tests[i];
          const totalMark = Number(test.test.totalMark);

          let extraMark = 0;
          //add extra mark to mark
          if (test.test.extra) {
            extraMark = test.test.extra.mark ?? 0;
          }
          //compose test
          if (test.paper) {
            let mark = Number(test.paper.mark);

            mark = this.maxMark(mark, totalMark, extraMark);
            if (totalMark) {
              mark = this.twentyMark(test.paper.mark, totalMark);
            }

            if (test.test.important == true) {
              //recuperation des tests importants noter
              importantTest.push({
                mark: this.maxMark(Number(test.paper.mark), totalMark, Number(extraMark)),
                mark20: mark,
                totalMark: test.test.totalMark,
              });
              importantTest20.push(mark);
            } else {
              testMark20.push(mark);
              testMark.push({
                mark: Number(test.paper.mark) + Number(extraMark),
                mark20: mark,
                totalMark: test.test.totalMark,
              });
            }
          }
          //not compose test
          else {
            //important test
            if (test.test.important == true) {
              importantTest.push({
                mark: 0 + Number(extraMark),
                mark20: 0 + this.twentyMark(extraMark, totalMark),
              });
              importantTest20.push(this.twentyMark(extraMark, totalMark));
            }
          }
        }

        // sort
        testMark20 = this.getReverseArray(testMark20);
        let testMarkResult = [];
        const difference = this.testNumber - importantTest.length;
        if (difference > 0) {
          for (let j = 0; j < difference; j++) {
            if (testMark20[j]) {
              //sort object array for simple test
              for (let a = 0; a < testMark.length; a++) {
                if (testMark20[j] == testMark[a].mark20) {
                  testMarkResult.push(testMark[a]);
                  break;
                }
              }
            } else
              testMarkResult.push({
                mark: 0,
                mark20: 0,
              });
          }
        } else if (difference < 0) {
          let resultImportantResult20 = [];
          let resultImportantResult = [];
          //sort object array for important test
          importantTest20 = this.getReverseArray(importantTest20);

          for (let i = 0; i < this.testNumber; i++) {
            resultImportantResult20.push(importantTest20[i]);
            for (let a = 0; a < importantTest20.length; a++) {
              if (importantTest20[i] == importantTest[a].mark20) {
                resultImportantResult.push(importantTest[a]);
                break;
              }
            }
          }
          importantTest20 = resultImportantResult20;
          importantTest = resultImportantResult;
        }

        testMark = importantTest.concat(testMarkResult);
        testMark20 = importantTest20.concat(testMark20);

        //Average
        let testAverage = 0;
        for (let j = 0; j < this.testNumber; j++) {
          const mark = testMark20[j] ? Number(testMark20[j]) : 0;
          testAverage += mark;
        }
        testAverage /= Number(this.testNumber);

        let average = 0;
        const exam = student.exam;
        let examMark = 0;
        if (exam) {
          const totalMarkExam = student.exam.other_informations.totalMark;
          if (totalMarkExam) {
            examMark = (exam.mark * 20) / totalMarkExam;
          }
          average = (Number(testAverage) + Number(examMark)) / 2;
        } else {
          if (this.examIsAvailable()) {
            average = testAverage / 2;
          } else examMark = undefined;
        }
        average = Math.round(average * 100) / 100;
        results.push({
          t1: testMark[0]
            ? `${testMark[0].mark} ${testMark[0].totalMark ? "/" : ""} ${
                testMark[0].totalMark ?? ""
              }`
            : "-",
          t2: testMark[1]
            ? `${testMark[1].mark}  ${testMark[1].totalMark ? "/" : ""} ${
                testMark[1].totalMark ?? ""
              }`
            : "-",
          t3: testMark[2]
            ? `${testMark[2].mark}  ${testMark[2].totalMark ? "/" : ""} ${
                testMark[2].totalMark ?? ""
              }`
            : "-",
          exam: examMark
            ? `${student.exam.mark} / ${student.exam.other_informations.totalMark}`
            : "-",
          average: average,
          student: student._id,
          number: c + 1,
        });
      }

      return results;
    },
    availableExam() {
      if (this.examIsAvailable()) {
        return "available";
      }
      return "unavailable";
    },

    availableTest() {
      if (this.Transcript.course.other_informations) {
        const tests = this.Transcript.course.other_informations.tests;
        if (tests) return tests;
      }
      return 0;
    },
  },
  methods: {
    async Initialize() {
      this.testNumber = this.Transcript.course.testNumber;
    },
    examIsAvailable() {
      if (this.Transcript.course.other_informations) {
        const exam = this.Transcript.course.other_informations.exam;
        if (exam) return true;
      }

      return false;
    },
    maxMark(mark, totalMark, extraMark) {
      if (Number(totalMark) > Number(mark) + Number(extraMark)) {
        return Number(mark) + Number(extraMark);
      } else {
        return totalMark;
      }
    },
    twentyMark(mark, totalMark) {
      return (mark * 20) / totalMark;
    },
    getReverseArray(array) {
      return array.sort(function (a, b) {
        return b - a;
      });
    },
    getTranscript() {
      return this.Students;
    },
  },
  beforeMount() {
    this.Initialize();
  },
  data() {
    return {
      testNumber: 0,
      valid: true,
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      search: "",
      headers: [
        {
          text: "Student",
          value: "number",
        },
        {
          text: "Test 1",
          value: "t1",
        },
        {
          text: "Test 2",
          value: "t2",
        },
        {
          text: "Test 3",
          value: "t3",
        },
        {
          text: "Exam",
          value: "exam",
        },
        {
          text: "Average (/20)",
          value: "average",
        },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
