import axios from 'axios'

const state ={
    Courses :[],
    Course :{},
    ProfessorCourses :[],
    ProfessorCourse :{},
    StudentCourses :[],
    StudentCourse :{},
    Tests :[],
    Test :{},
    Exam :{},
    ExamPapers :{},
    ExamPaper :{},
    TestPapers :{},
    TestPaper :{},
    Transcript :{},
}

const getters = {
    CoursesState : (state)=>state.Courses,
    CourseState : (state)=>state.Course,
    
    ProfessorCoursesState : (state)=>state.ProfessorCourses,
    ProfessorCourseState : (state)=>state.ProfessorCourse,
    
    StudentCoursesState : (state)=>state.StudentCourses,
    StudentCourseState : (state)=>state.StudentCourse,
    
    CourseTestsState : (state)=>state.Tests,
    CourseTestState : (state)=>state.Test,

    CourseExamState: (state) => state.Exam,
    
    ExamPapersState : (state)=>state.ExamPapers,
    ExamPaperState : (state)=>state.ExamPaper,

    TestPapersState : (state)=>state.TestPapers,
    TestPaperState: (state) => state.TestPaper,
    
    TranscriptState : (state)=>state.Transcript,
}

const mutations ={
    
    GET_COURSES(state,data){
        state.Courses = data
    },    
    GET_COURSE(state,data){
        state.Course = data
    },  

    GET_PROFESSOR_COURSES(state,data){
        state.ProfessorCourses = data
    },  
    GET_PROFESSOR_COURSE(state,data){
        state.ProfessorCourse = data
    },

    GET_STUDENT_COURSES(state,data){
        state.StudentCourses = data
    },  
    GET_STUDENT_COURSE(state,data){
        state.StudentCourse = data
    },  
    
    GET_TESTS(state,data){
        state.Tests = data
    },
    GET_TEST(state,data){
        state.Test = data
    },

    GET_TEST_PAPERS(state,data){
        state.TestPapers = data
    },
    GET_TEST_PAPER(state,data){
        state.TestPaper = data
    },
    
    GET_EXAM(state,data){
        state.Exam = data
    },
    GET_EXAM_PAPERS(state,data){
        state.ExamPapers = data
    },
    GET_EXAM_PAPER(state,data){
        state.ExamPaper = data
    },
    GET_TRANSCRIPT(state, data) {
        state.Transcript = {}
        state.Transcript = data
    },

    
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
  
    getCourses({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/course')
                .then(res=>{
                    commit('GET_COURSES',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
  
    getCoursesList({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/course/get/list')
                .then(res=>{
                    commit('GET_COURSES',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getCourse({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/course/'+id)
                .then(res=>{
                    commit('GET_COURSE',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
    addCourse({commit},course){
       
        return new Promise((resolve,reject)=>{
            axios 
                .post('/course/register',course)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        reject(error)
                    })
        })

    },
    updateCourse({commit},course){
       

        return new Promise((resolve,reject)=>{
            axios 
                .put('/course/'+course._id,course.data)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    assignProfessor({commit},professor){
       

        return new Promise((resolve,reject)=>{
            axios 
                .put('/course/'+professor._id+'/professor',professor.data)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    
    deleteCourse({commit},_id){
        return new Promise((resolve,reject)=>{
            axios 
                .delete('/course/'+_id)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },


    getProfessorCourses({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/professor/course')
                .then(res=>{
                    commit('GET_PROFESSOR_COURSES',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getProfesssorCourse({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/professor/course/'+id)
                .then(res=>{
                    commit('GET_PROFESSOR_COURSE',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
    updateCourseGoal({commit},course){
        
        return new Promise((resolve,reject)=>{
            axios
            .put('/professor/course/'+course.id+'/goal',course.data)
                .then(res=>{
                    commit('LOGIN',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
    numberIsAvailable({commit},course){
        
        return new Promise((resolve,reject)=>{
            axios
            .post('/professor/course/'+course.id+'/chapiterNumberExist',course.chapiterNumber)
                .then(res=>{
                    commit('LOGIN',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    
    addCourseChapiter({commit},course){
        
        return new Promise((resolve,reject)=>{
            axios
            .post('/professor/course/'+course.id+'/chapiter',course.chapiter)
                .then(res=>{
                    commit('LOGIN',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    updateCourseChapiter({commit},course){
        
        return new Promise((resolve,reject)=>{
            axios
            .put('/professor/course/'+course.id+'/chapiter/'+course.chapiterId,course.chapiter)
                .then(res=>{
                    commit('LOGIN',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    deleteCourseChapiter({commit},course){
        
        return new Promise((resolve,reject)=>{
            axios
            .delete('/professor/course/'+course.id+'/chapiter/'+course.chapiterId)
                .then(res=>{
                    commit('LOGIN',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },



    getStudentCourses({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/student/course')
                .then(res=>{
                    commit('GET_STUDENT_COURSES',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getStudentCourse({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/student/course/'+id)
                .then(res=>{
                    commit('GET_STUDENT_COURSE',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    getCourseTests({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/course/${id}/test`)
                .then(res=>{
                    commit('GET_TESTS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getCourseExam({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/course/${id}/exam`)
                .then(res=>{
                    commit('GET_EXAM',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getExam({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/course/exam/${id}`)
                .then(res=>{
                    commit('GET_EXAM',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getExamPapers({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/course/${id}/exam`)
                .then(res=>{
                    commit('GET_EXAM_PAPERS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getExamPaper({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/course/${schema.courseId}/exam-paper/${schema.examPaperId}`)
                .then(res=>{
                    commit('GET_EXAM_PAPER',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    noteExam({commit }, schema) {
        return new Promise((resolve,reject)=>{
            axios
                .post(`/professor/course/${schema.courseId}/exam-paper/${schema.examPaperId}/note`, {
                mark :schema.mark
            })
                .then(res=>{
                    resolve(res.data)
                    if(!commit) return
                },err=>{
                    reject(err);
                })
        })   
    },

    getTest({commit},id){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/course/test/${id}`)
                .then(res=>{
                    commit('GET_TEST',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getTestPapers({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/course/${schema.courseId}/test/${schema.testId}`)
                .then(res=>{
                    commit('GET_TEST_PAPERS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    getTestPaper({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/course/${schema.courseId}/test-paper/${schema.testPaperId}`)
                .then(res=>{
                    commit('GET_TEST_PAPER',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
    noteTest({commit }, schema) {
        return new Promise((resolve,reject)=>{
            axios
                .post(`/professor/course/${schema.courseId}/test-paper/${schema.testPaperId}/note`, {
                mark :schema.mark
            })
                .then(res=>{
                    resolve(res.data)
                    if(!commit) return
                },err=>{
                    reject(err);
                })
        })   
    },
    
    professorGetTranscript({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/professor/course/${schema.id}/transcript/${schema.session}`)
                .then(res=>{
                    commit('GET_TRANSCRIPT',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    adminGetTranscript({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/admin/course/${schema.id}/transcript/${schema.session}`)
                .then(res=>{
                    commit('GET_TRANSCRIPT',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },


    adminGetStudentTranscript({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/admin/show-student/${schema.studentId}/transcript/${schema.courseId}`)
                .then(res=>{
                    commit('GET_TRANSCRIPT',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },

    studentCollectNotes({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
            .get(`/student/course/${schema.id}/collect-notes`)
                .then(res=>{
                    commit('GET_TRANSCRIPT',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },


   
}

export default {
    state,
    getters,
    mutations,
    actions
}