import axios from 'axios'

const state ={
   
}

const getters = {
    
    
}

const mutations ={
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
    
    login({ commit }, schema) {
        console.log(schema.email);
        return new Promise((resolve,reject)=>{
            axios 
                .post('/login',schema)
                    .then(res=>{
                        commit('LOGIN')
                        const user = res.data.user 
                        const token = res.data.token 
                        if (user.registration_number) {
                            localStorage.setItem("e-student",token);
                        }
                        else if (user.function) {
                            localStorage.setItem("e-admin",token);
                            if(user.function == "Professor")
                            localStorage.setItem("professor", "professor");
                        }
                        resolve(res.data)
                    }
                    ,error=>{
                        reject(error)
                    })
        })
    },
    logout() {
        localStorage.setItem("e-admin", "");
        localStorage.setItem("professor", "");
        localStorage.setItem("e-student", "");
        location.reload()
      },
    
}

export default {
    state,
    getters,
    mutations,
    actions
}