<template>
  <div>
    <div v-for="(quiz, q) in correctionPaper" :key="q">
      <div>
        <p class="mb-1">
          <span class="bold">Q-{{ q + 1 }}</span> : {{ quiz.question }}
          <span class="main--text">({{ quiz.mark }} / {{ quiz.totalMark }} pts)</span>
        </p>

        <div>
          <p class="bold">Answers</p>
          <div
            class="d-flex justify-space-between"
            v-for="(answer, a) in quiz.availableChoices"
            :key="a"
          >
            <p class="d-flex">
              <v-checkbox
                class="my-0 py-0"
                v-model="answer.choose"
                :label="answer.choice"
                color="blue"
                disabled
                hide-details
              ></v-checkbox>
              <span class="ml-12" :class="answer.status ? 'green--text' : 'red--text'">
                <span v-if="answer.status == true && answer.choose == true">
                  {{ ` ( + ${quiz.goodAnswerMark} ) ` }}
                </span>
                <span v-else-if="answer.status == false && answer.choose == true">
                  {{ ` ( - ${quiz.goodAnswerMark} ) ` }}
                </span>
              </span>
            </p>
            <p>
              <span class="ml-12" :class="answer.status ? 'green--text' : 'red--text'"
                >{{ answer.status }}
              </span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    Answers: {
      required: true,
    },
    Correction: {
      required: true,
    },
  },
  mounted() {
    this.Initialize();
  },
  methods: {
    Initialize() {
      this.resultAlgorithme();
    },
    resultAlgorithme() {
      let Paper = [];
      let finalMark = 0;
      console.log(finalMark);
      for (let i = 0; i < this.Answers.length; i++) {
        const answer = this.Answers[i];
        const question = this.getQuestion(answer.question);

        let mark = 0;

        for (let c = 0; c < answer.choices.length; c++) {
          const choice = answer.choices[c];
          if (this.isTrue(question.answers, choice)) {
            mark += question.mark / this.totalTrue(question.answers);
          } else {
            mark -= question.badAnswerMark;
          }
        }
        if (mark < 0) mark = 0;
        finalMark += mark;

        //paper information
        let availableChoices = this.getAvailableChoices(question.answers, answer.choices);
        Paper.push({
          question: question.question,
          totalMark: question.mark,
          badAnswerMark: question.badAnswerMark,
          goodAnswerMark: question.mark / this.totalTrue(question.answers),
          mark: mark,
          availableChoices,
        });
        this.correctionPaper = Paper;
      }
      finalMark = Math.round(finalMark * 100) / 100;
    },
    getQuestion(question) {
      const questions = this.Correction;
      for (let i = 0; i < questions.length; i++) {
        if (questions[i].question == question) return questions[i];
      }
    },
    isTrue(choices, choice) {
      for (let i = 0; i < choices.length; i++) {
        if (choices[i].choice == choice && choices[i].status == true) return true;
      }
      return false;
    },

    totalTrue(choices) {
      let result = 0;
      for (let i = 0; i < choices.length; i++) {
        if (choices[i].status == true) result++;
      }
      return result;
    },
    getAvailableChoices(availableChoices, choicesChoose) {
      let choices = [];
      for (let c = 0; c < availableChoices.length; c++) {
        let choose = false;
        for (let i = 0; i < choicesChoose.length; i++) {
          if (choicesChoose[i] == availableChoices[c].choice) choose = true;
        }
        choices.push({
          choice: availableChoices[c].choice,
          choose,
          status: availableChoices[c].status,
        });
      }

      return choices;
    },
  },
  data() {
    return {
      correctionPaper: undefined,
    };
  },
};
</script>

