<template>
  <div class="bg-tkp h-100">
    <div v-if="StudentState.registration_number && loader == false">
      <div class="white px-1">
        <v-row>
          <v-col cols="6" md="3" class="d-flex align-center justify-center avatar">
            <div class="admissionIdPictureContainer">
              <v-img
                v-if="StudentState.idPicture"
                :src="`${StudentState.idPicture}`"
                class="rounded admissionIdPicture"
                height="100%"
                width="100%"
              >
                <template v-slot:placeholder>
                  <v-row class="fill-height ma-0" align="center" justify="center">
                    <v-progress-circular
                      indeterminate
                      color="grey lighten-5"
                    ></v-progress-circular>
                  </v-row>
                </template>
              </v-img>
              <v-icon color="black" v-else class="avatar-svg">mdi-account-tie</v-icon>
            </div>
          </v-col>
          <v-col cols="12" md="3">
            <p class="mb-2">
              <span class="bold">Registration number :</span>
              <span class="ml-1">{{ StudentState.registration_number }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Surname :</span>
              <span class="ml-1">{{ StudentState.surname }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Othernames :</span>
              <span class="ml-1">{{ StudentState.othernames }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Email :</span>
              <span class="ml-1">{{ StudentState.email }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Date Of birth :</span>
              <span class="ml-1">{{ dateManagement(StudentState.birthdayDate) }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Place of birth :</span>
              <span class="ml-1">{{ StudentState.birthdayPlace }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Nationality :</span>
              <span class="ml-1">{{ StudentState.nationality }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">City :</span>
              <span class="ml-1">{{ StudentState.city }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">State :</span>
              <span class="ml-1">{{ StudentState.state }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Home phone number :</span>
              <span class="ml-1">{{ StudentState.phone }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Home address :</span>
              <span class="ml-1">{{ StudentState.address }}</span>
            </p>
          </v-col>
          <v-col cols="12" md="6">
            <p class="mb-2 text-justify">
              <span class="bold">Id Card Or Passport Number:</span>
              <span class="ml-1">{{ StudentState.idCard }}</span>
            </p>
            <p class="mb-2 text-justify">
              <span class="bold">Knowledge of foreign languages : </span>
              <span class="ml-1" v-for="lang in StudentState.languages" :key="lang">
                {{ lang }}
              </span>
            </p>

            <div class="mt-4">
              <p class="bold main--text mb-2">Department</p>
              <p class="mb-1 bold">
                {{ getDepartmentName(StudentState.department) }}
              </p>
            </div>
            <p class="mb-2 text-justify">
              <span class="bold">Level:</span>
              <span class="ml-1">{{ StudentState.degre }}</span>
            </p>
            <div class="mt-3">
              <p class="bold red--text mb-1">Type of admission applied for :</p>
              <div class="d-flex">
                <v-checkbox
                  :input-value="isCheck('DIPLOMA', StudentState.admissionType)"
                  label="Diploma"
                  color="red"
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
                <v-checkbox
                  :input-value="isCheck('UNDERGRADUATE', StudentState.admissionType)"
                  label="Undergraduate"
                  color="orange"
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
                <v-checkbox
                  :input-value="isCheck('POST GRADUATE', StudentState.admissionType)"
                  label="Post graduate"
                  color="red"
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
              </div>
            </div>
            <div class="mt-4">
              <p class="bold secondaire mb-1">Mode of study :</p>
              <div class="d-flex">
                <v-checkbox
                  :input-value="isCheck('FULL TIME', StudentState.studyMode)"
                  label="Full time"
                  color="red "
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
                <v-checkbox
                  :input-value="isCheck('PART TIME', StudentState.studyMode)"
                  label="Part time"
                  color="orange"
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
                <v-checkbox
                  :input-value="
                    isCheck('OPEN AND DISTANCE LEARNING', StudentState.studyMode)
                  "
                  label="Open and distance learning"
                  color="green"
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
                <v-checkbox
                  :input-value="isCheck('ONLINE', StudentState.studyMode)"
                  label="online"
                  color="indigo"
                  class="mr-4 mt-0"
                  disabled
                ></v-checkbox>
              </div>
            </div>
          </v-col>
        </v-row>
      </div>
    </div>
    <can-not-find v-else-if="loader == false" :what="'this student'" />
    <circulate-load v-else />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
export default {
  components: { CanNotFind, CirculateLoad },
  computed: {
    ...mapGetters(["StudentState", "DepartmentsState"]),
  },
  methods: {
    ...mapActions(["getStudent", "getDepartments"]),
    async Initialize() {
      this.loader = true;
      try {
        await this.getStudent(this.$route.params.id);
        await this.getDepartments();
      } catch (error) {
        console.log(error);
      }
      this.loader = false;
    },
    dateManagement(dateObj) {
      var created_date = new Date(dateObj);

      var months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];
      var year = created_date.getFullYear();
      var month = months[created_date.getMonth()];
      var date = created_date.getDate();

      return date + " " + month + " " + year; // final date with time, you can use this according your requirement
    },
    isCheck(val1, val2) {
      if (val2) {
        val2 = val2.trim();
      }
      if (val1 == val2) return true;
      return false;
    },
    getDepartmentName(id) {
      if (this.DepartmentsState) {
        for (let i = 0; i < this.DepartmentsState.length; i++) {
          const department = this.DepartmentsState;
          if (department[i]._id == id) {
            return department[i].name;
          }
        }
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      loader: false,
    };
  },
};
</script>

<style></style>
