<template>
  <div class="">
    <div class="h-100" id="admin-profil">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">Dashbord / <span class="main--text">Professors</span></h3>
          <div>
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 d-block d-md-inline my-3 py-3 mr-md-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
            <a
              href=""
              v-if="
                AdminProfilState.function == 'Director' ||
                AdminProfilState.function == 'Main Professor'
              "
              @click.prevent="Add()"
              class="px-3 d-block d-md-inline my-3 py-3 white elevation-1 rounded-pill blue-grey--text"
              >Add Professors</a
            >
          </div>
        </div>

        <div class="mt-5">
          <v-card>
            <v-card-title>
              Professors
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :headers="headers"
              :items="AdminsState"
              :search="search"
              :loading="loading"
            >
              <template v-slot:item.profilPicture="{ item }">
                <td class="py-4">
                  <v-avatar>
                    <v-img :src="item.profilPicture" />
                  </v-avatar>
                </td>
              </template>

              <template v-slot:item.action="{ item }">
                <td>
                  <a
                    v-if="
                      AdminProfilState.function == 'Director' ||
                      AdminProfilState.function == 'Main Professor'
                    "
                    href=""
                    @click.prevent="Edit(item)"
                    class="rounded-circle px-2 py-3 mr-3"
                    ><v-icon class="blue--text">mdi-circle-edit-outline</v-icon>
                  </a>
                  <a
                    v-if="
                      AdminProfilState.function == 'Director' ||
                      AdminProfilState.function == 'Main Professor'
                    "
                    href=""
                    @click.prevent="Delete(item._id)"
                    class="rounded-pill white--text px-2 py-3"
                  >
                    <v-icon color="red">mdi-trash-can-outline</v-icon>
                  </a>
                </td>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </div>
    </div>
    <v-slide-x-transition>
      <v-dialog v-model="Modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="Modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h1>{{ ModalText.title }}</h1>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.firstname"
                  :rules="fieldRules"
                  label="FIrst Name"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.lastname"
                  :rules="fieldRules"
                  label="Last name "
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  :rules="emailRules"
                  label="Email"
                  v-model="ModalModel.email"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  :rules="fieldRules"
                  label="Grade"
                  v-model="ModalModel.grade"
                  required
                ></v-text-field>
              </div>
              <div>
                <v-select
                  :items="functions"
                  color="blue "
                  :rules="fieldRules"
                  v-model="ModalModel.function"
                  label="Function"
                  class=""
                  required
                ></v-select>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.password"
                  :type="passwordVisibility.type"
                  :rules="passwordRules"
                  label="Mots de passe"
                  required
                ></v-text-field>
                <span class="mt-5 ml-2">
                  <v-icon @click="managePasswordVisibility(passwordVisibility)">
                    {{ passwordVisibility.icon }}
                  </v-icon>
                </span>
              </div>

              <div class="d-flex justify-space-between">
                <v-text-field
                  :rules="passwordConfirmationRules"
                  v-model="ModalModel.password_confirmation"
                  :type="confirmPasswordVisibility.type"
                  label="Confirmer le mots de passe"
                  required
                ></v-text-field>
                <span class="mt-5 ml-2">
                  <v-icon @click="managePasswordVisibility(confirmPasswordVisibility)">
                    {{ confirmPasswordVisibility.icon }}
                  </v-icon>
                </span>
              </div>

              <div>
                <v-file-input
                  label="Profil picture"
                  show-size
                  accept="image/png, image/jpeg, image/bmp"
                  v-model="ModalModel.profilPicture"
                  truncate-length="1"
                ></v-file-input>
              </div>
              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!valid"
                  color=""
                  class="mr-4 white--text main"
                  @click="validates"
                  width="100%"
                >
                  {{ ModalText.btn }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>

    <v-slide-y-transition>
      <v-dialog v-model="Dialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this admin ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            You can't come back to your decision
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="bg-secondaire white--text" text @click="CancelDelete()">
              Cancel
            </v-btn>
            <v-btn class="red white--text" text @click="ConfirmDelete(deleteId)">
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["AdminsState", "FacultysState", "AdminProfilState", "ErrorBag"]),
  },
  methods: {
    ...mapActions([
      "getProfessors",
      "addAdmin",
      "editAdmin",
      "deleteAdmin",
      "getFacultys",
    ]),
    managePasswordVisibility(current) {
      if (current.type == "password") {
        current.icon = "mdi-eye-off";
        current.type = "text";
      } else {
        current.icon = "mdi-eye";
        current.type = "password";
      }
    },

    async Refresh() {
      this.loading = true;
      try {
        await this.getProfessors();
        this.message = "Professors refresh successfully";
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.loading = false;
      this.snackbar = true;
    },

    Add() {
      this.Modal = true;
      this.ModalText.title = "Add new Professor";
      this.ModalText.btn = "Add";
      this.ModalModel = {};
    },
    Edit(item) {
      this.Modal = true;
      this.ModalModel = item;
      this.ModalModel.password = "";
      this.ModalText.title = "Edit Professor";
      this.ModalText.btn = "Update";
    },
    Delete(id) {
      this.Dialog = true;
      this.deleteId = id;
    },
    CancelDelete() {
      this.deleteId = "";
      this.Dialog = false;
    },
    async ConfirmDelete(id) {
      try {
        await this.deleteAdmin(id);
        this.message = "Professors deleted successfully";
      } catch (error) {
        this.message = "Failed to delete this professors";
      }
      this.CancelDelete();
      this.getProfessors();
    },

    async validates() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        const ModelAdd = {
          firstname: this.ModalModel.firstname,
          lastname: this.ModalModel.lastname,
          email: this.ModalModel.email,
          password: this.ModalModel.password,
          function: this.ModalModel.function,
          grade: this.ModalModel.grade,
          profilPicture: this.ModalModel.profilPicture,
        };

        if (this.ModalModel._id) {
          // update
          const ModalModelUpdate = {
            data: ModelAdd,
            _id: this.ModalModel._id,
          };
          try {
            await this.editAdmin(ModalModelUpdate);
            this.message = "Professor edit successfully";
          } catch (error) {
            this.message = "Failed to edit this Professor";
          }
        } else {
          // add
          try {
            await this.addAdmin(ModelAdd);
            this.message = "Professor added successfully";
          } catch (error) {
            this.message = "Failed to add thus professor";
          }
        }
        this.Modal = false;
        this.Initialize();
      }
    },
    async Initialize() {
      this.loading = true;
      try {
        await this.getProfessors();
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loader = false;
      this.loading = false;
    },
  },
  mounted() {
    this.Initialize();
    this.getFacultys();
  },
  data() {
    return {
      deleteId: "",
      facultys: [],
      faculty_id: "",
      Dialog: "",
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: false,
      valid: true,
      nameRules: [(v) => !!v || "name is required"],
      fieldRules: [(v) => !!v || "this field  is required"],
      descriptionRules: [(v) => !!v || "description  is required"],
      facultyRules: [(v) => !!v || "faculty is required"],
      certificateRules: [(v) => !!v || "certificate is required"],
      emailRules: [
        (v) => !!v || "email is required",
        (v) => /.+@.+\..+/.test(v) || "invalid email ",
      ],
      passwordRules: [
        (v) => !!v || "password is required",
        (v) => (v && v.length >= 4) || "password must have 4 characteres at least",
      ],
      passwordConfirmationRules: [
        (v) => !!v || "Password confirmation is required",
        (v) => (v && v == this.ModalModel.password) || "Password must matched",
      ],
      passwordVisibility: {
        icon: "mdi-eye",
        type: "password",
      },
      confirmPasswordVisibility: {
        icon: "mdi-eye",
        type: "password",
      },
      Modal: false,
      ModalText: {
        title: "Add new Professor",
        btn: "Add",
      },
      ModalModel: {
        name: "",
        acronym: "",
        description: "",
        certificate: "",
        _id: "",
      },
      search: "",
      headers: [
        {
          text: "",
          value: "profilPicture",
        },
        {
          text: "First Name",
          value: "firstname",
        },
        { text: "Last name", value: "lastname" },
        { text: "Email", value: "email" },
        { text: "Grade", value: "grade" },
        { text: "Function", value: "function" },
        { text: "Action", value: "action" },
      ],
      functions: ["Professor"],
    };
  },
};
</script>

<style></style>
