<template>
  <div class="bg-tkp h-100">
    <div class=" " id="admin-profil" v-if="TestPapersState.papers && loader == false">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            Test /
            <router-link
              :to="`/professor/course/${TestPapersState.course._id}`"
              class="main--text"
              >{{ TestPapersState.course.name }}</router-link
            >
          </h3>
          <div class="d-flex flex-column flex-md-row">
            <a
              href=""
              @click.prevent="OpenMarkModel()"
              class="px-3 py-3 white mr-md-3 my-3 elevation-1 rounded-lg secondary--text"
            >
              Additional Mark
            </a>
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
          </div>
        </div>
      </div>
      <v-container class="px-md-12 py-12">
        <v-card>
          <v-card-title>
            Student Paper
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :loading="loading"
            :loading-text="'chargement...'"
            :headers="headers"
            :items="TestPapersState.papers"
            :search="search"
          >
            <template v-slot:item.mark="{ item }">
              {{ totalMark(Number(item.mark)) }}
            </template>
            <template v-slot:item.action="{ item }">
              <td>
                <router-link
                  :to="`/professor/course/${TestPapersState.course._id}/test-paper/${item._id}`"
                  class="rounded-circle white--text px-2 py-3 mr-3"
                  ><v-icon class="green--text">mdi-arrow-right</v-icon>
                </router-link>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-container>
    </div>
    <div v-else-if="loader == false && !TestPapersState._id">
      <can-not-find what="this Exam" />
    </div>
    <v-dialog
      v-model="dialog"
      scrollable
      max-width="45rem"
      transition="dialog-transition"
    >
      <v-card>
        <v-container class="py-12">
          <v-form ref="form" v-model="valid" lazy-validation>
            <v-select
              :items="[0, 1, 2, 3, 4, 5]"
              label="Add aditional mark"
              v-model="Extra.mark"
              :rules="[(v) => !!v || Number(v) >= 0 || `Mark is required`]"
              outlined
            ></v-select>
            <v-textarea
              v-if="Extra.mark > 0"
              :rules="[(v) => !!v || `The justification is required`]"
              v-model="Extra.justification"
              outlined
              name="input-7-4"
              label="Why do you want do add this mark"
              value=""
            ></v-textarea>
            <v-btn class="main white--text" @click="extraMark"> Submit </v-btn>
            <v-btn class="ml-3 secondary--text" @click="closeMarkModel()"> Cancel </v-btn>
          </v-form>
        </v-container>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
    <function ref="function" />
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Function from "../../components/universal/function.vue";

export default {
  components: {
    CanNotFind,
    Function,
    CirculateLoad,
  },
  computed: {
    ...mapGetters(["TestPapersState", "AdminProfilState", "ErrorBag"]),
  },
  methods: {
    ...mapActions(["getTestPapers"]),
    Refresh() {
      this.Initialize();
    },

    async Initialize() {
      this.loading = true;
      this.loader = true;
      try {
        await this.getTestPapers({
          courseId: this.$route.params.courseId,
          testId: this.$route.params.testId,
        });
        this.setAdditionalMark();
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loading = false;
      this.loader = false;
    },
    OpenMarkModel() {
      this.dialog = true;
    },

    closeMarkModel() {
      this.dialog = false;
      this.Extra = {};
    },
    async extraMark() {
      if (this.$refs.form.validate()) {
        if (this.Extra.mark > 5) {
          return (this.Extra = {});
        }
        if (Number(this.Extra.mark) == 0) this.Extra.justification = undefined;
        try {
          await axios.put(
            `/professor/course/${this.$route.params.courseId}/test/${this.$route.params.testId}/extra-mark`,
            this.Extra
          );
          this.Initialize();
          this.message = "Additional mark modify successfully";
        } catch (error) {
          this.message = "Failed to modify additional mark";
        }
        this.snackbar = true;
        this.closeMarkModel();
      }
    },
    setAdditionalMark() {
      if (this.TestPapersState.test) {
        if (this.TestPapersState.test.extra) {
          const extra = this.TestPapersState.test.extra;
          this.Extra = {
            mark: extra.mark,
            justification: extra.justification,
          };
          if (Number(extra.mark) > 0) {
            this.additionalMark = extra.mark;
            return (this.headers[1].text = `Mark (+ ${extra.mark})`);
          }
        }
      }
      this.additionalMark = 0;

      return (this.headers[1].text = `Mark`);
    },
    totalMark(mark) {
      if (
        Number(this.TestPapersState.test.totalMark) >
        Number(mark) + Number(this.additionalMark)
      ) {
        return Number(mark) + Number(this.additionalMark);
      } else {
        return this.TestPapersState.test.totalMark;
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  watch: {},
  data() {
    return {
      DeleteDialog: false,
      dialog: false,
      Modal: false,
      ModalText: {},
      ModalModel: {},
      Extra: {},
      additionalMark: 0,
      valid: true,
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      TestModal: false,
      circulateLoader: false,
      search: "",

      headers: [
        {
          text: "Paper",
          value: "_id",
        },
        {
          text: `Mark  `,
          value: "mark",
        },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
