<template>
  <div>
    <div class="container">
      <div class="mb-5">
        <div v-html="coursePeriod">
          <!-- <v-icon class="mr-2 ext-h5 main--text"> mdi-timer-sand</v-icon> -->
        </div>
      </div>
      <div v-if="CourseShortInfo.students">
        <h3 class="text-h5 text-center py-2 order-main rounded-pill elevation-1 mb-3">
          <span class="text-h5">{{ CourseShortInfo.students.total }}</span>
          <span
            class="mx-1"
            v-text="Number(CourseShortInfo.students.total) > 1 ? 'Students' : 'Student'"
          ></span>

          <v-icon class="ml-1 text-h4 main--text">mdi-account-multiple</v-icon>
        </h3>
      </div>
      <div v-if="CourseShortInfo.chapiters">
        <h3 class="text-h5 mb-3">
          Progress
          <v-icon class="ml-1 text-h5 main--text">mdi-progress-clock</v-icon>
        </h3>
        <v-progress-linear
          class="rounded-pill elevation-1"
          dark
          color="blue lighten-1"
          height="50"
          :value="progressValue"
          >{{
            `${CourseShortInfo.chapiters.completed}/${CourseShortInfo.chapiters.total}`
          }}</v-progress-linear
        >
      </div>
      <div class="rounded elevation-1 white py-4 px-2 mt-6">
        <h3 class="text-h5">Comming Event</h3>
        <div class="mt-5">
          <div class="">
            <p class="mb-2">
              <span class="bold">Next class :</span>
              <span> 8 June 2021 - <span class="main--text">14h30</span></span>
            </p>
          </div>
          <div v-if="CourseShortInfo.exam" class="mt-">
            <p class="mb-2" v-html="exam"></p>
          </div>
          <div v-if="CourseShortInfo.tests" class="mt-">
            <p class="mb-2">
              <span class="bold">Test(s):</span>
              <!-- <span> Not programed <span class="main--text"></span></span> -->
            </p>
            <ul>
              <li
                v-for="test in CourseShortInfo.tests"
                :key="test._id"
                v-html="getDateTime(test)"
              ></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  props: {
    id: {
      required: true,
    },
    isProfessor: {
      //   required: true,
    },
    isStudent: {
      //   required: true,
    },
  },
  computed: {
    ...mapGetters(["CourseShortInfo", "InCoursePeriod"]),
    coursePeriod() {
      if (this.CourseShortInfo.program) {
        if (this.CourseShortInfo.program.dates.length == 2) {
          let startDate = this.getRealDate(this.CourseShortInfo.program.dates[0]);
          let endDate = this.getRealDate(this.CourseShortInfo.program.dates[1]);

          return `<span class="bold">From </span>
                <span> ${startDate} </span>
                 <span class="bold"> to </span>
                <span> ${endDate} </span>`;
        }
      }
      return `This course is not programed`;
    },
    exam() {
      if (this.CourseShortInfo.exam) {
        if (this.CourseShortInfo.exam.dates.length == 2) {
          let startDate = this.getRealDate(this.CourseShortInfo.exam.dates[0]);
          let endDate = this.getRealDate(this.CourseShortInfo.exam.dates[1]);

          return `<span class="bold">Exam :</span>
                <span class="">From </span>
                <span class="main--text"> ${startDate} </span>
                <span class=""> to </span>
                <span class="main--text"> ${endDate} </span>`;
        }
      }
      // CourseShortInfo

      return ` <span class="bold">Exam :</span> Not programed`;
    },
    progressValue() {
      if (!this.CourseShortInfo.chapiters) return null;
      const progress = this.CourseShortInfo.chapiters;

      return Math.round((progress.completed * 100) / progress.total);
    },
  },
  methods: {
    ...mapActions(["getCourseShortInfo"]),
    async Initialize() {
      await this.getCourseShortInfo(this.id);
      if (this.CourseShortInfo.chapiters)
        if (this.CourseShortInfo.chapiters.completed == null)
          this.CourseShortInfo.chapiters.completed = 0;
    },
    getRealDate(argDate) {
      argDate = new Date(argDate);
      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];

      const days = [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saterday",
      ];
      const day = days[argDate.getDay()];
      const year = argDate.getFullYear();
      const month = months[argDate.getMonth()];
      const date = argDate.getDate();

      return day + " " + date + " " + month + " " + year;
    },
    getDateTime(test) {
      if (test.day) {
        const date = this.getRealDate(test.day.date);
        return `<span> ${date} - <span class="main--text"> ${test.day.time}</span></span>`;
      }

      //   return ` <span> 11 June 2021 - <span class="main--text">14h30</span></span>`;
    },
  },
  mounted() {
    this.Initialize();
  },
};
</script>

