<template>
  <div class="nav-app z-index-100" :class="Collapse ? 'collapse' : ''">
    <v-app-bar class="white pr-md-12" dense dark elevation="0">
      <v-toolbar-title class="d-none d-md-inline"
        ><v-btn color="black" @click="toggleCollapse()" icon>
          <v-icon>mdi-menu</v-icon></v-btn
        ></v-toolbar-title
      >
      <v-spacer></v-spacer>

      <span class="black--text mr-3">{{ Today() }} </span>
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <a
            href=""
            @click.prevent=""
            class="px-5 py-1 rounded-pill bg-mv"
            v-bind="attrs"
            v-on="on"
          >
            <div class="d-flex">
              <v-avatar color="white" size="30">
                <v-img :src="AdminProfilState.profilPicture" alt="" />
              </v-avatar>

              <div class="d-flex justify-center align-center">
                <span class="ml-2 main--text">{{
                  AdminProfilState.firstname + " " + AdminProfilState.lastname
                }}</span>
              </div>
            </div>
          </a>
        </template>
        <v-list class="z-index-100 white">
          <v-list-item class="white" v-if="!isProfessor" @click="modeProfessor">
            <a href="" @click.prevent="" class="black--text d-block">Mode Professor</a>
          </v-list-item>
          <v-list-item
            class="white"
            v-if="isProfessor && AdminProfilState.function != 'Professor'"
            @click="modeAdmin"
          >
            <a href="" @click.prevent="" class="black--text d-block">Mode Admin</a>
          </v-list-item>
        </v-list>
        <v-list-item class="white">
          <a href="" @click.prevent="logout()" class="black--text d-block">Logout</a>
        </v-list-item>
      </v-menu>

      <router-link
        v-if="isProfessor"
        to="/professor/notification"
        class="black--text px-2 bold"
      >
        <v-badge :content="2" :value="0" color="green" overlap>
          <v-icon color="black"> mdi-bell-outline </v-icon>
        </v-badge>
      </router-link>
      <router-link v-else to="/admin/notification" class="black--text px-2 bold">
        <v-badge :content="2" :value="0" color="green" overlap>
          <v-icon color="black"> mdi-bell-outline </v-icon>
        </v-badge>
      </router-link>
    </v-app-bar>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {},
  computed: {
    ...mapGetters(["AdminProfilState", "Collapse"]),

    isProfessor() {
      return localStorage.getItem("professor");
    },
  },

  methods: {
    ...mapActions(["getAdminProfil", "logout", "toggleCollapse"]),
    Today() {
      const created_date = new Date();

      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];

      const days = [
        "Sunday",
        "Monday",
        "Thuesday",
        "Wednesday",
        "Tueday",
        "Friday",
        "Saterday",
      ];
      const day = days[created_date.getDay()];
      const year = created_date.getFullYear();
      const month = months[created_date.getMonth()];
      const date = created_date.getDate();

      return day + " " + date + " " + month + " " + year; // final date with time, you can use this according your requirement
    },
    modeProfessor() {
      localStorage.setItem("professor", "adminid");
      location.reload();
    },
    modeAdmin() {
      localStorage.setItem("professor", "");
      location.reload();
    },
  },
  mounted() {
    this.getAdminProfil();
  },

  data() {
    return {
      items: [{ title: "Logout" }],
    };
  },
};
</script>

<style>
.logo-dash .logo-img img {
  width: 80px !important;
  height: 50px !important;
  margin-top: 0.3rem;
}

.logo-dash .afriford {
  margin-top: 0.5rem !important;
}
.logo-dash .university {
  margin-top: -0.5rem !important;
  font-size: 0.9rem;
  letter-spacing: 0.19rem;
}

.z-index-100 {
  z-index: 100 !important;
}
.z-index-100 * {
  z-index: 100 !important;
}
</style>
