import axios from 'axios'

const state ={
    Admins :[]
   
}

const getters = {
    AdminsState : (state)=>state.Admins
    
}

const mutations ={
    
    GET_ADMINS(state,data){
        state.Admins = data
    },  
    
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
    getAdmins({commit}){
        
        axios
            .get('/admin/team')
                .then(res=>{
                    commit('GET_ADMINS',res.data)
                })
                .catch(err=>{
                    console.log(err);
                })
    },
   
    getProfessors({commit}){
        
        return new Promise((resolve,reject)=>{
            axios
            .get('/admin/professors')
                .then(res=>{
                    commit('GET_ADMINS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err);
                })
        })
    },
    
    addAdmin({commit},admin){
        const form = new FormData()

        form.append("firstname",admin.firstname)
        form.append("lastname",admin.lastname)
        form.append("email",admin.email)
        form.append("function",admin.function)
        form.append("grade",admin.grade)
        form.append("password",admin.password)
        form.append("profilPicture",admin.profilPicture)
        return new Promise((resolve,reject)=>{
            axios 
                .post('/admin/register',form)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        // console.log(error);   
                        reject(error)
                    })
        })

    },
    editAdmin({commit},admin){
        const form = new FormData()

        form.append("firstname",admin.data.firstname)
        form.append("lastname",admin.data.lastname)
        form.append("email",admin.data.email)
        form.append("function",admin.data.function)
        form.append("grade",admin.data.grade)
        form.append("password",admin.data.password)
        console.log(typeof(admin.data.profilPicture));
        if(typeof(admin.data.profilPicture) !="string" && typeof(admin.data.profilPicture) !="undefined" ){
            form.append("profilPicture",admin.data.profilPicture)
        }

        return new Promise((resolve,reject)=>{
            axios 
                .put('/admin/team/'+admin._id,form)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    
    deleteAdmin({commit},_id){
        return new Promise((resolve,reject)=>{
            axios 
                .delete('/admin/team/'+_id)
                    .then(res=>{
                        commit('LOGIN')

                        resolve(res.data)
                    }
                    ,error=>{
                        
                        reject(error)
                    })
        })

    },
    
    

    
    
}

export default {
    state,
    getters,
    mutations,
    actions
}