<template>
  <div class="bg-tkp h-100">
    <div class=" " id="admin-profil" v-if="ExamPaperState.paper && loader == false">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5 main--text">
            <router-link
              :to="`/professor/course/${$route.params.courseId}/exam`"
              class="black--text"
            >
              Exam paper /
            </router-link>
            {{ ExamPaperState.paper._id }}
          </h3>
          <div class="d-flex flex-column flex-md-row">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-1 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >

            <v-menu
              v-model="markMenu"
              :close-on-content-click="false"
              :nudge-width="200"
              offset-x
            >
              <template v-slot:activator="{ on, attrs }">
                <a
                  href=""
                  v-bind="attrs"
                  v-on="on"
                  @click.prevent="startNote"
                  class="px-3 py-1 mr-md-3 my-3 elevation-1 rounded-lg white blue-grey--text"
                  >Note this paper</a
                >
              </template>

              <v-form ref="noteMarkForm">
                <v-card>
                  <v-card-title primary-title>
                    <v-text-field
                      name="name"
                      :rules="noteRules"
                      v-model="noteMark"
                      label="Mark"
                    ></v-text-field>
                  </v-card-title>

                  <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn text @click="(markMenu = false), (noteMark = null)">
                      Cancel
                    </v-btn>
                    <v-btn
                      color="primary"
                      text
                      :loading="loading"
                      @click="noteThisPaper()"
                    >
                      Submit
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-form>
            </v-menu>
          </div>
        </div>
      </div>
      <exam-paper :ExamPaper="ExamPaperState" />
    </div>
    <div v-else-if="loader == false && !ExamPaperState.paper">
      <can-not-find what="this exam paper" />
    </div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
    <function ref="function" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import ExamPaper from "../../components/professor/ExamPaper.vue";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Function from "../../components/universal/function.vue";

export default {
  components: {
    CanNotFind,
    Function,
    CirculateLoad,

    ExamPaper,
  },
  computed: {
    ...mapGetters(["ExamPaperState", "AdminProfilState", "ErrorBag"]),
  },
  methods: {
    ...mapActions(["getExamPaper", "noteExam"]),
    Refresh() {
      this.Initialize();
    },

    async Initialize() {
      this.loading = true;
      this.loader = true;
      try {
        await this.getExamPaper({
          courseId: this.$route.params.courseId,
          examPaperId: this.$route.params.examPaperId,
        });

        if (this.ExamPaperState.paper)
          this.noteMark = this.ExamPaperState.paper.mark ?? null;
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loading = false;
      this.loader = false;
    },
    async noteThisPaper() {
      if (this.$refs.noteMarkForm.validate()) {
        this.loading = true;
        try {
          await this.noteExam({
            courseId: this.$route.params.courseId,
            examPaperId: this.$route.params.examPaperId,
            mark: this.noteMark,
          });
          this.message = "Paper noted successfully";
        } catch (error) {
          this.message = "Failed to note this paper";
        }
        this.snackbar = true;
        this.loading = false;
        this.markMenu = false;
        this.Initialize();
      }
    },
    startNote() {
      if (this.ExamPaperState.paper)
        this.noteMark = this.ExamPaperState.paper.mark ?? null;
      this.markMenu = true;
    },
  },
  mounted() {
    this.Initialize();
  },
  watch: {},
  data() {
    return {
      DeleteDialog: false,
      Modal: false,
      ModalText: {},
      ModalModel: {},
      paperMark: {},
      noteMark: null,
      valid: true,
      snackbar: false,
      markMenu: false,
      message: "",
      errors: "",
      loading: false,
      noteRules: [
        (v) => !!v || "Mark is required",
        (v) =>
          (v && Number(v) <= Number(this.ExamPaperState.exam.totalMark)) ||
          `Mark must be less or equal to to ${this.ExamPaperState.exam.totalMark}`,
      ],
      loader: true,
      TestModal: false,
      circulateLoader: false,
    };
  },
};
</script>

<style>
.border-red {
  border: 1px solid red;
}
</style>
