<template>
  <div class="">
    <div class="h-100" id="admin-profil">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">Dashbord / <span class="main--text">Faculty</span></h3>
          <div>
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 d-block d-md-inline my-3 py-3 mr-md-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
            <a
              href=""
              @click.prevent="Add()"
              v-if="
                AdminProfilState.function == 'Director' ||
                AdminProfilState.function == 'Main Professor' ||
                AdminProfilState.function == 'Staff'
              "
              class="px-3 d-block d-md-inline my-3 py-3 white elevation-1 rounded-pill blue-grey--text"
              >Add Faculty</a
            >
          </div>
        </div>

        <div class="mt-5">
          <v-card>
            <v-card-title>
              Faculty
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :loading="loading"
              :loading-text="'chargement...'"
              :headers="headers"
              :items="FacultysState"
              :search="search"
            >
              <template v-slot:item.mainProfessor="{ item }">
                <td class="">{{ item.mainProfessors.length }}</td>
              </template>
              <template v-slot:item.action="{ item }">
                <td>
                  <router-link
                    :to="'faculty/' + item._id"
                    class="rounded-circle white--text px-2 py-3 mr-3"
                    ><v-icon class="green--text">mdi-arrow-right</v-icon>
                  </router-link>
                  <a
                    v-if="AdminProfilState.function == 'Director'"
                    href=""
                    @click.prevent="Edit(item)"
                    class="rounded-circle white--text px-2 py-3 mr-3"
                    ><v-icon class="blue--text">mdi-circle-edit-outline</v-icon>
                  </a>
                  <a
                    v-if="AdminProfilState.function == 'Director'"
                    href=""
                    @click.prevent="Delete(item._id)"
                    class="rounded-pill white--text px-2 py-3"
                  >
                    <v-icon color="red">mdi-trash-can-outline</v-icon>
                  </a>
                </td>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </div>
    </div>
    <v-slide-x-transition>
      <v-dialog v-model="Modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="Modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h1>{{ ModalText.title }}</h1>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.name"
                  :rules="nameRules"
                  label="Name"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.acronym"
                  :rules="acronymRules"
                  label="Acronym"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  :rules="descriptionRules"
                  label="Description"
                  v-model="ModalModel.description"
                  required
                ></v-text-field>
              </div>

              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="validates"
                  width="100%"
                >
                  {{ ModalText.btn }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>

    <v-slide-y-transition>
      <v-dialog v-model="Dialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this faculty ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every department,student and course about this faculty will be deleted
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="bg-secondaire white--text" text @click="CancelDelete()">
              Cancel
            </v-btn>
            <v-btn
              class="red white--text"
              :loading="loading"
              text
              @click="ConfirmDelete(deleteId)"
            >
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  components: {},
  computed: {
    ...mapGetters(["FacultysState", "AdminProfilState"]),
  },
  methods: {
    ...mapActions(["getFacultys", "addFaculty", "editFaculty", "deleteFaculty"]),
    async Refresh() {
      this.loading;
      try {
        await this.getFacultys();
        this.message = "Faculty refresh successfully";
      } catch (error) {
        this.message = this.errorMsg;
      }
      this.snackbar = true;
      this.Initialize();
    },
    async Initialize() {
      this.loading = true;
      try {
        await this.getFacultys();
      } catch (error) {
        this.message = this.errorMsg;
        this.snackbar = true;
      }
      this.loading = false;

      this.ModalText.title = "Add new Faculty";
      this.ModalText.btn = "Add";
      this.ModalModel = {};
    },
    Add() {
      this.Modal = true;
      this.Initialize();
    },
    Edit(item) {
      this.Modal = true;
      this.ModalModel = item;
      this.ModalText.title = "Edit Faculty";
      this.ModalText.btn = "Update";
    },
    Delete(id) {
      this.Dialog = true;
      this.deleteId = id;
    },
    CancelDelete() {
      this.deleteId = "";
      this.Dialog = false;
    },
    async ConfirmDelete(id) {
      this.loading = true;
      try {
        await this.deleteFaculty(id);
        this.message = "Faculty deleted successfully";
      } catch (error) {
        this.message = "Failed to delete this faculty";
      }
      this.loading = false;
      this.snackbar = true;
      this.CancelDelete();
      this.getFacultys();
    },
    async validates() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        if (this.ModalModel._id) {
          // update
          const ModalModelUpdate = {
            data: {
              name: this.ModalModel.name,
              acronym: this.ModalModel.acronym,
              description: this.ModalModel.description,
            },
            _id: this.ModalModel._id,
          };
          try {
            await this.editFaculty(ModalModelUpdate);
            this.message = "Faculty edit successfully";
          } catch (error) {
            this.message = "Failed to edit this faculty";
          }
        } else {
          //add
          try {
            await this.addFaculty(this.ModalModel);
            this.message = "Faculty added successfully";
          } catch (error) {
            this.message = "Failed to add faculty";
          }
        }
        this.loading = false;
        this.Modal = false;
        this.snackbar = true;
        this.getFacultys();
        this.Initialize();
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      deleteId: "",
      Dialog: "",
      errors: "",
      snackbar: false,
      errorMsg: "Something went wrong",
      message: "",
      loading: true,
      valid: true,
      nameRules: [(v) => !!v || "name is required"],
      acronymRules: [(v) => !!v || "acronym  is required"],
      descriptionRules: [(v) => !!v || "description  is required"],
      Modal: false,
      ModalText: {
        title: "Add new Faculty",
        btn: "Add",
      },
      ModalModel: {
        name: "",
        acronym: "",
        description: "",
        _id: "",
      },
      search: "",
      headers: [
        { text: "Name", value: "name" },
        { text: "Accronyme", value: "acronym" },
        { text: "Description", value: "description" },
        { text: "Main professor", value: "mainProfessor" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
