<template>
  <div class="bg-tkp h-100">
    <div class=" " id="admin-profil" v-if="ExamPapersState.papers && loader == false">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            Exam /
            <router-link
              :to="`/professor/course/${ExamPapersState.course._id}`"
              class="main--text"
              >{{ ExamPapersState.course.name }}</router-link
            >
          </h3>
          <div class="d-flex flex-column flex-md-row">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
          </div>
        </div>
      </div>
      <v-container class="px-md-12 py-12">
        <v-card>
          <v-card-title>
            Student Paper
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :loading="loading"
            :loading-text="'chargement...'"
            :headers="headers"
            :items="ExamPapersState.papers"
            :search="search"
          >
            <template v-slot:item.action="{ item }">
              <td>
                <router-link
                  :to="`/professor/course/${ExamPapersState.course._id}/exam-paper/${item._id}`"
                  class="rounded-circle white--text px-2 py-3 mr-3"
                  ><v-icon class="green--text">mdi-arrow-right</v-icon>
                </router-link>
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-container>
    </div>
    <div v-else-if="loader == false && !ExamPapersState._id">
      <can-not-find what="this Exam" />
    </div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
    <function ref="function" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Function from "../../components/universal/function.vue";

export default {
  components: {
    CanNotFind,
    Function,
    CirculateLoad,
  },
  computed: {
    ...mapGetters(["ExamPapersState", "AdminProfilState", "ErrorBag"]),
  },
  methods: {
    ...mapActions(["getExamPapers"]),
    Refresh() {
      this.Initialize();
    },

    async Initialize() {
      this.loading = true;
      this.loader = true;
      try {
        await this.getExamPapers(this.$route.params.id);
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loading = false;
      this.loader = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  watch: {},
  data() {
    return {
      DeleteDialog: false,
      Modal: false,
      ModalText: {},
      ModalModel: {},
      valid: true,
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      TestModal: false,
      circulateLoader: false,
      search: "",
      headers: [
        {
          text: "Paper",
          value: "_id",
        },
        {
          text: "Mark",
          value: "mark",
        },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
