<template>
  <div class="h-100 bg-tkp">
    <div v-if="CourseTestState._id && loader == false" class="px-md-12 py-md-5 px-1 py-2">
      <div class="white rounded h-100 py-5 px-2 px-md-12">
        <div class="text-center">
          <p class="main--text mt-2 mb-2">Exam</p>
          <p class="bold" :class="timeClass">
            <span class="text-h4" v-html="remainingTime.hours"></span>
            <span class="text-h4"> : </span>
            <span class="text-h4" v-html="remainingTime.minutes"></span>
            <span class="ml-1" v-html="remainingTime.seconds"></span>
          </p>
        </div>
        <div class="d-flex justify-space-between">
          <div>
            <h3 class="text-h5">{{ CourseTestState.other_informations.course.name }}</h3>
            <p class="bold mb-1">
              {{ CourseTestState.other_informations.course.level }} level
            </p>
            <p><span class="bold">Session : </span> 2021 - 2022</p>
          </div>
          <div>
            <p><span class="bold">Duration :</span> {{ CourseTestState.duration }} min</p>
            <a href="" @click.prevent="refresh" class="main--text">Refresh</a>
          </div>
        </div>
        <div class="text-center"></div>
        <div>
          <treated-exercice
            ref="treated"
            :document="'exam'"
            :mark="mark"
            :paper="paper"
            :link="`/student/test/${CourseTestState._id}`"
            :quiz="CourseTestState"
            :submit="submit"
          />
        </div>
      </div>
    </div>

    <div v-else-if="loader == false && CourseTestState.offLine">
      <off-line
        document="test"
        @refresh="Initialize"
        :day="CourseTestState.startDate ? CourseTestState.startDate : undefined"
        :programed="CourseTestState.startDate ? true : false"
        :duration="CourseTestState.duration"
      />
    </div>
    <div v-else-if="loader == false">
      <can-not-find what="this Exam" />
    </div>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
  </div>
</template>

<script>
// import axios from "axios";
import { mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import OffLine from "../../components/universal/OffLine.vue";
import TreatedExercice from "../../components/universal/treatedExercice.vue";

export default {
  components: { CanNotFind, CirculateLoad, TreatedExercice, OffLine },
  computed: {
    ...mapGetters(["CourseTestState", "ErrorBag"]),
    paper() {
      if (this.CourseTestState.other_informations.paper) {
        if (this.CourseTestState.other_informations.paper.answer)
          return this.CourseTestState.other_informations.paper.answer;
        else if (this.CourseTestState.other_informations.paper.answers)
          return this.CourseTestState.other_informations.paper.answers;
      }
      return [];
    },
    mark() {
      if (this.CourseTestState.other_informations.paper)
        return this.CourseTestState.other_informations.paper.mark;
      return undefined;
    },
    submit() {
      if (this.CourseTestState.other_informations.paper)
        return this.CourseTestState.other_informations.paper.submit;
      return undefined;
    },
  },

  mounted() {
    this.Initialize();
  },

  methods: {
    async Initialize() {
      this.loader = true;
      try {
        await this.getTest();

        this.getRemainingTime();
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.loader = false;
    },
    async getTest() {
      try {
        await this.$store.dispatch("getTest", this.$route.params.id);
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
    },
    async refresh() {
      this.loader = true;
      try {
        await this.$store.dispatch("getTest", this.$route.params.id);
        clearInterval(this.interval);
        this.getRemainingTime();
        this.message = "Refresh sucessfully";
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.snackbar = true;
      this.loader = false;
    },
    getRemainingTime() {
      if (!this.CourseTestState.day) return;
      const duration = this.CourseTestState.duration;
      const day = this.CourseTestState.day;

      const startTime = new Date(`${day.date} ${day.time}`);
      let endTime = new Date(`${startTime}`);
      endTime.setMinutes(endTime.getMinutes() + Number(duration));

      const secDiff = (today) => {
        var diff = (today.getTime() - endTime.getTime()) / 1000;
        return Math.abs(Math.round(diff));
      };

      const timeStatus = (remaining) => {
        const min = 60;
        if (remaining > 20 * min) this.timeClass = "black--text";
        else if (remaining > 5 * min) this.timeClass = "orange--text";
        else this.timeClass = "red--text";
      };
      this.interval = setInterval(() => {
        const today = new Date();
        const remaining = secDiff(today);
        const seconds = remaining % 60;
        const minutes = Math.abs(Math.floor(remaining / 60) % 60);
        const hours = Math.abs(Math.floor(remaining / 3600));
        this.remainingTime.minutes = minutes > 9 ? minutes : `0${minutes}`;
        this.remainingTime.seconds = seconds > 9 ? seconds : `0${seconds} `;
        this.remainingTime.hours = hours > 9 ? hours : `0${hours} `;
        timeStatus(remaining);
        if (remaining <= 0) {
          this.$refs.treated.resultAlgorithme();
          clearInterval(this.interval);
        }
      }, 1000);
    },
  },
  data() {
    return {
      loading: true,
      loader: false,
      snackbar: false,
      message: "",
      timeClass: "",
      interval: undefined,
      remainingTime: {
        minutes: null,
        seconds: null,
        hours: null,
      },
    };
  },
};
</script>

<style></style>
