<template>
  <div class="bg-tkp h-100">
    <div
      class=" "
      id="admin-profil"
      v-if="ProfessorCourseState.name && data.loader == false"
    >
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            Dashbord /
            <router-link to="/admin/course" class="main--text">Course</router-link>
          </h3>
          <div class="d-flex flex-column flex-md-row">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
          </div>
        </div>
      </div>
      <v-row class="mx-2 mx-md-3 mx-lg-5">
        <v-col cols="12" md="4" lg="3">
          <short-course-info :id="$route.params.id" />
        </v-col>
        <v-col cols="12" md="8" lg="9">
          <v-container class="">
            <div class="white px-md-12 py-12 px-3">
              <div>
                <h1 class="text-h3">{{ ProfessorCourseState.name }}</h1>
                <div v-if="ProfessorCourseState.description" class="mt-3">
                  <article
                    class="text-sm"
                    v-html="ProfessorCourseState.description"
                  ></article>
                  <p class="bold">Objectif(s) of this course:</p>

                  <ul>
                    <li v-for="(objectif, i) in ProfessorCourseState.objectifs" :key="i">
                      {{ objectif }}
                    </li>
                  </ul>
                </div>
                <div class="mt-8 d-flex">
                  <a
                    href=""
                    class="white--text elevation-1 rounded main px-3 py-2"
                    @click.prevent="AddDescription"
                    >About this course</a
                  >
                  <a
                    href=""
                    class="elevation-1 mx-3 rounded main--text px-3 py-2"
                    @click.prevent="AddTest"
                    >Add Test</a
                  >
                  <a
                    v-if="!CourseExamState._id"
                    href=""
                    class="elevation-1 rounded main--text px-3 py-2"
                    @click.prevent="AddExam"
                    >Add Exam</a
                  >
                  <router-link
                    :to="`/professor/course/${$route.params.id}/transcript`"
                    class="elevation-1 rounded main--text px-3 py-2"
                    >Transcript
                  </router-link>
                </div>
              </div>
              <div class="mt-10 pt-6 border-t">
                <div class="d-flex justify-space-between">
                  <h3 class="bold">Chapiter(s)</h3>
                  <v-btn
                    v-if="
                      ProfessorCourseState.haveManyChapiter == true ||
                      ProfessorCourseState.other_informations.chapiters.length < 1
                    "
                    @click="AddChapiter"
                    class="main--text text-h3"
                    icon
                    ><v-icon>mdi-plus-circle-outline</v-icon></v-btn
                  >
                </div>
                <div
                  v-if="ProfessorCourseState.other_informations.chapiters.length > 0"
                  class="mt-8"
                >
                  <v-row
                    v-for="chapiter in ProfessorCourseState.other_informations.chapiters"
                    :key="chapiter._id"
                    class=""
                  >
                    <v-col cols="10">
                      <router-link
                        :to="`/professor/course/${$route.params.id}/chapiter/${chapiter._id}`"
                        >Chapiter {{ chapiter.number }} : {{ chapiter.title }} ({{
                          chapiter.type
                        }})</router-link
                      >
                    </v-col>

                    <v-col class="d-flex justify-end" cols="2">
                      <div>
                        <v-btn color="orange" @click="editChapiter(chapiter)" icon>
                          <v-icon>mdi-square-edit-outline</v-icon></v-btn
                        >
                        <v-btn
                          color="red"
                          @click="
                            (chapiterDeleteId = chapiter._id), (DeleteDialog = true)
                          "
                          icon
                        >
                          <v-icon>mdi-delete</v-icon></v-btn
                        >
                      </div>
                    </v-col>
                  </v-row>
                </div>
                <div class="mt-8" v-else>
                  <p>When you will add chapiter, he will come here</p>
                </div>
              </div>
              <div class="mt-10 pt-6 border-t">
                <div class="d-flex justify-space-between">
                  <h3 class="bold">Exam</h3>
                  <v-btn
                    v-if="!CourseExamState._id"
                    @click="AddExam"
                    class="main--text text-h3"
                    icon
                    ><v-icon>mdi-plus-circle-outline</v-icon></v-btn
                  >
                </div>
                <div v-if="CourseExamState._id" class="mt-8">
                  <div class="d-flex justify-space-between">
                    <div>
                      <router-link
                        class="d-block"
                        :to="`/professor/course/${ProfessorCourseState._id}/exam`"
                        >Exam
                        <v-icon v-if="CourseExamState.online == true" color="green"
                          >mdi-circle-medium</v-icon
                        ></router-link
                      >
                    </div>

                    <div>
                      <v-btn color="orange" @click="editExam(CourseExamState)" icon>
                        <v-icon>mdi-square-edit-outline</v-icon></v-btn
                      >
                      <v-btn
                        color="red"
                        @click="
                          (compoModelDelete._id = CourseExamState._id),
                            (compoModelDelete.type = 'Exam'),
                            (CompositionDelete = true)
                        "
                        icon
                      >
                        <v-icon>mdi-delete</v-icon></v-btn
                      >
                    </div>
                  </div>
                </div>
                <div class="mt-8" v-else>
                  <p>When you will add exam, he will come here</p>
                </div>
              </div>
              <div class="mt-10 pt-6 border-t">
                <div class="d-flex justify-space-between">
                  <h3 class="bold">Test (s)</h3>
                  <v-btn @click="AddTest" class="main--text text-h3" icon
                    ><v-icon>mdi-plus-circle-outline</v-icon></v-btn
                  >
                </div>
                <div v-if="CourseTestsState.length > 0" class="mt-8">
                  <div
                    v-for="(test, i) in CourseTestsState"
                    :key="test._id"
                    class="d-flex justify-space-between"
                  >
                    <router-link
                      :to="`/professor/course/${ProfessorCourseState._id}/test/${test._id}`"
                      >Test {{ i + 1 }} ({{ test.answerType }})
                      <v-icon v-if="test.online == true" color="green"
                        >mdi-circle-medium</v-icon
                      ></router-link
                    >

                    <div>
                      <v-btn color="orange" @click="editTest(test)" icon>
                        <v-icon>mdi-square-edit-outline</v-icon></v-btn
                      >
                      <v-btn
                        color="red"
                        @click="
                          (compoModelDelete._id = test._id),
                            (compoModelDelete.type = 'Test'),
                            (CompositionDelete = true)
                        "
                        icon
                      >
                        <v-icon>mdi-delete</v-icon></v-btn
                      >
                    </div>
                  </div>
                </div>
                <div class="mt-8" v-else>
                  <p>When you will add test, he will come here</p>
                </div>
              </div>
            </div>
          </v-container>
        </v-col>
      </v-row>
    </div>
    <div v-else-if="data.loader == false && !ProfessorCourseState.name">
      <can-not-find what="this Courses" />
    </div>

    <v-slide-x-transition>
      <v-dialog v-model="data.modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="data.modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="descriptionForm"
              v-model="data.valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h3 class="mb-4 text-h5">{{ "Give details about your course" }}</h3>

              <div v-if="data.errors">
                <p class="red--text">{{ data.errors }}</p>
              </div>
              <div>
                <quill-editor
                  ref="editor"
                  v-model="courseDescription"
                  class="mt-5"
                  :options="QuillOption.default"
                />
              </div>
              <div>
                <h3 class="mb-6 text-h5">Objectif of this courses</h3>
                <v-combobox
                  v-model="objectifs"
                  :items="items"
                  chips
                  clearable
                  label="What is the objectifs of this course ?"
                  multiple
                  prepend-icon="mdi-filter-variant"
                  solo
                >
                  <template v-slot:selection="{ attrs, item, select, selected }">
                    <v-chip
                      v-bind="attrs"
                      :input-value="selected"
                      close
                      @click:close="remove(item)"
                    >
                      <span class="text-sm">{{ item }}</span>
                    </v-chip>
                  </template>
                </v-combobox>
              </div>
              <div class="text-center">
                <v-btn
                  :loading="data.loading"
                  :disabled="!data.valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="saveCourseDetail"
                  width="100%"
                >
                  {{ "Save" }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>
    <v-slide-x-transition>
      <v-dialog v-model="ChapiterModal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="ChapiterModal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="chapiterForm"
              v-model="data.valid"
              lazy-validation
              class="px-10 py-12 white rounded"
            >
              <h3 class="mb-4 text-h5">{{ "Give a detail of your course" }}</h3>

              <div v-if="data.errors">
                <p class="red--text">{{ data.errors }}</p>
              </div>
              <div v-if="ProfessorCourseState.other_informations">
                <v-radio-group
                  v-if="
                    ProfessorCourseState.other_informations.chapiters.length < 2 &&
                    chapiter._id
                  "
                  row
                  class=""
                  v-model="chapiter.isMany"
                  mandatory
                >
                  <v-radio label="More than one chapiter" :value="true"></v-radio>
                  <v-radio label="Only one chapiter" :value="false"></v-radio>
                </v-radio-group>
              </div>
              <div v-if="chapiter.isMany == true">
                <v-text-field
                  label="Chapiter number :"
                  :rules="numberRules"
                  ref="numberInput"
                  v-model="chapiter.number"
                  @keyup="verifyChapiterNumber"
                  loader-height="2"
                  :loading="chapiterNumberLoading"
                ></v-text-field>
              </div>
              <div>
                <v-text-field
                  label="Chapiter title :"
                  :rules="[(v) => !!v || 'The title is required']"
                  v-model="chapiter.title"
                ></v-text-field>
              </div>
              <div>
                <v-select
                  v-if="!chapiter._id"
                  :rules="[(v) => !!v || 'The type is required']"
                  :items="data.chapiterTypes"
                  v-model="chapiter.type"
                  outlined
                  label="Chapiter types"
                  hint="You can't modified the chapiter types after created it "
                  persistent-hint
                ></v-select>
              </div>
              <div class="text-center">
                <v-btn
                  :loading="data.loading"
                  :disabled="!data.valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="saveChapiterCourse"
                  width="100%"
                >
                  {{ ChapiterModalText.btn }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>
    <v-slide-y-transition>
      <v-dialog v-model="DeleteDialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this chapiter ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            The content , the exercice and other thing about this chapiter will be deleted
            too
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="bg-secondaire white--text"
              text
              @click="(chapiterDeleteId = ''), (DeleteDialog = false)"
            >
              Cancel
            </v-btn>
            <v-btn
              class="red white--text"
              text
              @click="ConfirmDeleteChapiter(chapiterDeleteId)"
            >
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>

    <!-- Add exam and test -->
    <v-slide-y-transition>
      <v-dialog v-model="TestModal" max-width="65rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="closeTestModal"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <create-test
              ref="createTest"
              :examExist="CourseExamState._id ? true : false"
              :composition="composition ? composition : undefined"
              :compositionType="compositionType"
              @refresh="successComposition"
            />
          </div>
        </div>
      </v-dialog>
    </v-slide-y-transition>

    <v-slide-y-transition>
      <v-dialog v-model="CompositionDelete" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this document ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            The content , the exercice and other thing about this chapiter will be deleted
            too
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="bg-secondaire white--text"
              text
              @click="(compoModelDelete = {}), (CompositionDelete = false)"
            >
              Cancel
            </v-btn>
            <v-btn
              class="red white--text"
              text
              @click="ConfirmDeleteComposition(compoModelDelete)"
            >
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>

    <circulate-load v-if="data.loader" />
    <function ref="function" />
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
import CreateTest from "../../components/professor/createTest.vue";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Function from "../../components/universal/function.vue";

import { quillEditor } from "vue-quill-editor";

import "quill/dist/quill.snow.css";
import ShortCourseInfo from "../../components/universal/shortCourseInfo.vue";

import { defaultData } from "../../utils/data";

export default {
  components: {
    CanNotFind,
    Function,
    CirculateLoad,
    CreateTest,
    quillEditor,
    ShortCourseInfo,
  },
  computed: {
    ...mapGetters([
      "ProfessorCourseState",
      "AdminProfilState",
      "CourseTestsState",
      "CourseExamState",
      "ErrorBag",
      "QuillOption",
      "isInCoursePeriod",
    ]),
  },
  methods: {
    ...mapActions([
      "getProfesssorCourse",
      "updateCourseGoal",
      "addCourseChapiter",
      "numberIsAvailable",
      "updateCourseChapiter",
      "deleteCourseChapiter",
      "getCourseTests",
      "getCourseExam",
    ]),
    Refresh() {
      this.Initialize();
    },

    async Initialize() {
      this.data.loading = true;
      this.data.loader = true;
      this.chapiter = {};
      this.ChapiterModal = false;
      this.DeleteDialog = false;
      this.data.modal = false;
      this.CompositionDelete = false;
      try {
        await this.getProfesssorCourse(this.$route.params.id);
        await this.getCourseExam(this.$route.params.id);
        await this.getCourseTests(this.$route.params.id);
      } catch (error) {
        this.$store.dispatch("setSnackbar", {
          message: this.ErrorBag.default,
          showing: true,
        });
      }
      this.data.loading = false;
      this.data.loader = false;
    },
    AddDescription() {
      this.data.modal = true;
      this.objectifs = this.ProfessorCourseState.objectifs ?? "";
      this.courseDescription = this.ProfessorCourseState.description ?? "";
    },

    remove(item) {
      this.objectifs.splice(this.objectifs.indexOf(item), 1);
      this.objectifs = [...this.objectifs];
    },
    async saveCourseDetail() {
      if (this.$refs.descriptionForm.validate()) {
        if (this.courseDescription.length < 1) {
          return (this.data.errors = "Details is required");
        }
        if (this.objectifs.length < 1) {
          return (this.data.errors = "Objectif is required");
        }
        this.data.errors = "";
        const Detail = {
          id: this.$route.params.id,
          data: {
            description: this.courseDescription,
            objectifs: this.objectifs,
          },
        };
        this.data.loading = true;
        try {
          await this.updateCourseGoal(Detail);
          this.$store.dispatch("setSnackbar", {
            message: "Course objectifs updated successfully",
            showing: true,
          });
        } catch (error) {
          this.$store.dispatch("setSnackbar", {
            message: "Failed to update Course objectifs",
            showing: true,
          });
        }
        this.data.modal = false;
        this.data.loading = false;
        this.Initialize();
      }
    },

    AddChapiter() {
      this.chapiter = {
        number: "",
        title: "",
        isMany: this.ProfessorCourseState.haveManyChapiter ?? true,
      };
      this.ChapiterModalText = {
        btn: "Add Chapiter",
      };
      this.ChapiterModal = true;
    },
    editChapiter(chapiter) {
      this.chapiter = {
        number: chapiter.number,
        title: chapiter.title,
        isMany: this.ProfessorCourseState.haveManyChapiter,
        _id: chapiter._id,
      };
      this.ChapiterModalText = {
        btn: "Edit Chapiter",
      };
      this.ChapiterModal = true;
    },
    async ConfirmDeleteChapiter(id) {
      this.data.loading = true;
      const course = {
        id: this.$route.params.id,
        chapiterId: id,
      };
      try {
        await this.deleteCourseChapiter(course);
        this.$store.dispatch("setSnackbar", {
          message: "Chapiter deleted successfully",
          showing: true,
        });
      } catch (error) {
        this.$store.dispatch("setSnackbar", {
          message: "Failed to delete this  chapiter",
          showing: true,
        });
      }
      this.data.loading = false;

      this.DeleteDialog = false;
      this.Initialize();
    },

    async saveChapiterCourse() {
      if (this.$refs.chapiterForm.validate()) {
        let course = {
          id: this.$route.params.id,
          chapiter: {
            type: this.chapiter.type ?? "Can't change",
            number: this.chapiter.number,
            title: this.chapiter.title,
            isMany: this.chapiter.isMany ?? false,
          },
          chapiterId: this.chapiter._id ?? "",
        };
        this.data.loading = true;
        let message;
        if (this.chapiter._id) {
          try {
            await this.updateCourseChapiter(course);
            message = "Chapiter updated successfully";
          } catch (error) {
            message = "Failed to update this  chapiter";
          }
        } else {
          try {
            await this.addCourseChapiter(course);
            message = "Chapiter created successfully";
          } catch (error) {
            message = "Failed to create chapiter";
          }
        }
        this.$store.dispatch("setSnackbar", {
          message: message,
          showing: true,
        });

        this.data.loading = false;
        this.Initialize();
      }
    },

    verifyChapiterNumber() {
      if (Number.isInteger(Number(this.chapiter.number))) {
        this.chapiterNumberLoading = true;
        this.data.errors = "";
        const course = {
          id: this.$route.params.id,
          chapiterNumber: {
            chapiterNumber: this.chapiter.number,
          },
        };
        this.numberIsAvailable(course).then(
          () => {
            (this.chapiterNumberLoading = false), (this.chapiterNumberExist = false);
          },
          (err) => {
            if (err == "Error: Request failed with status code 403") {
              this.chapiterNumberExist = true;
              this.$refs.numberInput.validate();
            } else if (err == "Error: Request failed with status code 400") {
              this.data.errors = "Chapiter number must be greater than 0";
            } else {
              this.data.errors =
                "Can not connect to server . Check your network and retry";
            }
            this.chapiterNumberLoading = false;
          }
        );
      }
    },

    successComposition() {
      this.$store.dispatch("setSnackbar", {
        message: `${this.compositionType} saved successfully`,
        showing: true,
      });
      this.closeTestModal();
      this.Initialize();
    },
    AddTest() {
      this.compositionType = "Test";
      this.TestModal = true;
    },
    AddExam() {
      this.TestModal = true;
      this.compositionType = "Exam";
    },
    closeTestModal() {
      this.TestModal = false;
      this.composition = undefined;
      this.$refs.createTest.Initialize();
    },
    editTest(test) {
      this.AddTest();
      this.composition = test;
    },
    editExam(exam) {
      this.AddExam();
      this.composition = exam;
    },
    async ConfirmDeleteComposition(model) {
      this.data.loading = true;

      try {
        await axios.delete(`/professor/composition/${model._id}/${model.type}`);

        this.$store.dispatch("setSnackbar", {
          message: "Chapiter deleted successfully",
          showing: true,
        });
      } catch (error) {
        this.$store.dispatch("setSnackbar", {
          message: "Failed to delete this  chapiter",
          showing: true,
        });
      }
      this.Initialize();
    },
  },
  mounted() {
    this.Initialize();
  },
  watch: {},
  data() {
    return {
      data: defaultData,
      DeleteDialog: false,
      compoModelDelete: {},
      CompositionDelete: false,
      ChapiterModal: false,
      ChapiterModalText: {},
      professorDialog: false,
      TestModal: false,
      chapiterNumberLoading: false,
      objectifs: [],
      items: [],
      courseDescription: "",
      numberRules: [
        (v) => !!v || "The chapiter number is required",
        (v) => (v && Number(v) > 0) || "This field must be  greater than 0",
        (v) => (v && Number.isInteger(Number(v))) || "This field must be  an integer",
        (v) => (v && this.chapiterNumberExist == false) || "This number already exist",
      ],
      chapiterRadios: "many",
      chapiter: {},
      chapiterNumberExist: false,
      chapiterDeleteId: "",
      compositionType: "",
      composition: undefined,
    };
  },
};
</script>

<style></style>
