<template>
  <div class="h-100 bg-tkp">
    <div v-if="ChapiterState.title">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">
            <router-link
              :to="'/professor/course/' + ChapiterState.other_informations.course._id"
              class="black--text"
            >
              {{ ChapiterState.other_informations.course.name }}</router-link
            >
          </h3>
          <div class="d-flex flex-column flex-md-row">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
          </div>
        </div>

        <v-row>
          <v-col cols="12" md="4">
            <v-container class="rounded white px-md-12 py-12">
              <div>
                <div class="d-flex justify-space-between">
                  <h1 class="text-h5">
                    <span>Chapiter {{ ChapiterState.number }} </span> :
                    {{ ChapiterState.title }}
                  </h1>
                  <div>
                    <v-icon v-if="ChapiterState.type == 'Video'" class="main--text"
                      >mdi-video-vintage</v-icon
                    >
                    <v-icon v-if="ChapiterState.type == 'Article'" class="main--text"
                      >mdi-email-newsletter</v-icon
                    >
                    <v-icon v-if="ChapiterState.type == 'Pdf'" class="main--text"
                      >mdi-file-pdf-box-outline</v-icon
                    >
                    <v-icon v-if="ChapiterState.type == 'Live'" class="main--text"
                      >mdi-video-wireless-outline</v-icon
                    >
                  </div>
                </div>
                <p class="mt-3 grey--text">
                  <span class="">Created by </span>
                  <span class="bold">
                    <span class="main--text">{{
                      ChapiterState.other_informations.created_by.grade
                    }}</span>
                    {{ ChapiterState.other_informations.created_by.firstname }}
                    {{ ChapiterState.other_informations.created_by.lastname }}
                  </span>
                </p>

                <div>
                  <div v-if="ChapiterState.description" class="mt-3">
                    <p class="bold">What about this chapiter ?</p>
                    <p class="text-sm" v-html="ChapiterState.description"></p>
                    <p class="bold">Objectif(s) of this chapiter:</p>
                    <ul>
                      <li v-for="(objectif, i) in ChapiterState.objectifs" :key="i">
                        {{ objectif }}
                      </li>
                    </ul>
                  </div>
                  <div class="mt-4" v-if="ChapiterState.type == 'Link'">
                    <p class="bold">
                      Course link :
                      <a :href="ChapiterState.link" class="main--text" target="_BLANK">
                        {{ ChapiterState.link }}
                      </a>
                    </p>
                  </div>
                </div>
                <div class="mt-8">
                  <a
                    href=""
                    class="white--text rounded bg-mv-2 px-3 py-2"
                    @click.prevent="AddDescription"
                    >About this Chapiter</a
                  >
                </div>
              </div>
            </v-container>
          </v-col>
          <v-col cols="12" md="8">
            <div>
              <v-tabs
                v-model="tab"
                class="rounded"
                color="white"
                background-color="blue"
                centered
                dark
                icons-and-text
              >
                <v-tabs-slider></v-tabs-slider>

                <v-tab v-if="ChapiterState.type != 'Link'" href="#studio"> Studio </v-tab>

                <v-tab href="#exercice"> Application Exercice </v-tab>
              </v-tabs>
              <v-tabs-items v-model="tab">
                <v-tab-item :value="'studio'">
                  <v-container class="white rounded px-md-12 py-6">
                    <h3 class="mb-8 text-center">
                      Studio <v-icon class="main--text">mdi-youtube-studio</v-icon>
                    </h3>
                    <div v-if="ChapiterState.type == 'Article'">
                      <create-article
                        @refresh="Initialize"
                        :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/article`"
                        :article="ChapiterState.article"
                      />
                    </div>
                    <div v-if="ChapiterState.type == 'Video'">
                      <upload-video
                        @refresh="Initialize"
                        :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/video`"
                        :file="ChapiterState"
                      />
                    </div>
                    <div v-if="ChapiterState.type == 'Pdf'">
                      <upload-pdf
                        @refresh="Initialize"
                        :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/pdf`"
                        :file="ChapiterState"
                      />
                    </div>
                    <div v-if="ChapiterState.type == 'Live'">
                      <live-program
                        :link="`${AdminProfilState._id}/${ChapiterState._id}`"
                      />
                    </div>
                  </v-container>
                </v-tab-item>
                <v-tab-item :value="'exercice'">
                  <v-container class="py-6 rounded white">
                    <h3 class="mb-8 text-center">
                      Application Exercice
                      <v-icon class="main--text">mdi-frequently-asked-questions</v-icon>
                    </h3>
                    <div>
                      <create-exercice
                        @refresh="Initialize"
                        :exercices="ChapiterState.other_informations.exercices"
                      />
                    </div>
                  </v-container>
                </v-tab-item>
              </v-tabs-items>
            </div>
          </v-col>
        </v-row>
      </div>
    </div>
    <div v-else-if="loader == false && !ChapiterState.name">
      <can-not-find what="this Chapiter" />
    </div>

    <v-slide-x-transition>
      <v-dialog v-model="Modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="Modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="descriptionForm"
              v-model="valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h3 class="mb-4 text-h5">{{ "Give a detail about this chapiter" }}</h3>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>
              <div>
                <quill-editor
                  ref="editor"
                  v-model="chapiter.description"
                  class="mt-5"
                  :options="QuillOption.default"
                />
              </div>
              <div>
                <h3 class="mb-6 text-h5">Objectif of this chapiter</h3>
                <v-combobox
                  v-model="chapiter.objectifs"
                  :items="items"
                  chips
                  clearable
                  label="What is the objectifs of this course ?"
                  multiple
                  prepend-icon="mdi-filter-variant"
                  solo
                >
                  <template v-slot:selection="{ attrs, item, select, selected }">
                    <v-chip
                      v-bind="attrs"
                      :input-value="selected"
                      close
                      @click:close="remove(item)"
                    >
                      <span class="text-sm">{{ item }}</span>
                    </v-chip>
                  </template>
                </v-combobox>
              </div>
              <div class="my-5" v-if="ChapiterState.type == 'Link'">
                <v-text-field
                  label="Link"
                  v-model="chapiter.link"
                  outlined
                  :rules="linkRules"
                ></v-text-field>
              </div>

              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="saveChapiterDetail"
                  width="100%"
                >
                  {{ "Save" }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CreateExercice from "../../components/professor/createExercice.vue";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import CreateArticle from "../../components/universal/createArticle.vue";
import UploadPdf from "../../components/universal/uploadPdf.vue";
import UploadVideo from "../../components/universal/uploadVideo.vue";

import { quillEditor } from "vue-quill-editor";

import "quill/dist/quill.snow.css";
import LiveProgram from "../../components/universal/LiveProgram.vue";

export default {
  components: {
    CanNotFind,
    CirculateLoad,
    UploadVideo,
    UploadPdf,
    CreateArticle,
    CreateExercice,
    quillEditor,
    LiveProgram,
  },
  computed: {
    ...mapGetters([
      "ChapiterState",
      "AdminProfilState",
      "ErrorBag",
      "QuillOption",
      "InCoursePeriod",
    ]),
  },
  methods: {
    ...mapActions(["getProfessorChapiter", "updateChapiterGoal", "getCourseShortInfo"]),
    async Refresh() {
      this.loader = true;
      try {
        await this.getProfessorChapiter(this.$route.params.chapiterId);
        this.message = "Chapiter refresh successfully";
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.loader = false;
      this.snackbar = true;
    },

    async Initialize() {
      this.loading = true;
      this.loader = true;
      this.chapiter = {};
      this.errors = "";
      this.Modal = false;
      try {
        await this.getProfessorChapiter(this.$route.params.chapiterId);
        await this.getCourseShortInfo(this.$route.params.courseId);
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.loading = false;
      this.loader = false;
    },
    remove(item) {
      this.chapiter.objectifs.splice(this.chapiter.objectifs.indexOf(item), 1);
      this.chapiter.objectifs = [...this.chapiter.objectifs];
    },
    AddDescription() {
      this.Modal = true;
      this.chapiter.objectifs = this.ChapiterState.objectifs ?? "";
      this.chapiter.description = this.ChapiterState.description ?? "";
    },
    async saveChapiterDetail() {
      if (this.$refs.descriptionForm.validate()) {
        if (this.chapiter.description.length < 1) {
          return (this.errors = "Description is required");
        }
        if (this.chapiter.objectifs.length < 1) {
          return (this.errors = "Objectif is required");
        }
        let formData = {
          description: this.chapiter.description,
          objectifs: this.chapiter.objectifs,
        };

        if (this.chapiter.link) formData.link = this.chapiter.link;
        if (this.chapiter.pdf) formData.append("pdf", this.chapiter.pdf);

        this.errors = "";
        const Detail = {
          chapiterId: this.$route.params.chapiterId,
          courseId: this.$route.params.courseId,
          data: formData,
        };

        this.loading = true;
        try {
          this.updateChapiterGoal(Detail);
          this.message = "Chapiter goal updated successfully";
          this.Initialize();
        } catch (error) {
          this.message = "Failed to update chapiter goal";
        }

        this.loading = false;
        this.snackbar = true;
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      Modal: false,
      valid: true,
      snackbar: false,
      message: "",
      items: [],
      errors: "",
      loading: false,
      loader: true,
      chapiter: {},

      linkRules: [(v) => !!v || "Link is required"],
      tab: null,
    };
  },
};
</script>

<style></style>
