<template>
  <div class="">
    <div v-if="quiz._id">
      <div v-if="quiz.timeLimite" class="mt-5 text-center">
        <span class="bold red--text">Ending Date :</span>
        {{ quiz.timeLimite.date.end }}
        {{ quiz.timeLimite.time.end }}
      </div>
      <div class="d-md-flex">
        <v-tabs
          v-model="tab"
          class="mb-3"
          color="blue"
          background-color="white"
          left
          icons-and-text
        >
          <v-tabs-slider></v-tabs-slider>

          <v-tab v-if="quiz" href="#quiz">
            quiz
            <v-icon>mdi-email-newsletter</v-icon>
          </v-tab>

          <v-tab v-if="quiz.answerType != 'Multiple Choice'" href="#answer">
            Answer
            <v-icon>mdi-file-document-edit-outline</v-icon>
          </v-tab>
        </v-tabs>
        <div v-if="tab == 'answer'" class="d-flex justify-center align-center mr-md-4">
          <v-btn
            class="mr-4"
            :class="showQ.status == true ? 'main--text ' : ''"
            @click="toggleShowQ"
            icon
            ><v-icon>{{ showQ.icon }}</v-icon></v-btn
          >
          <v-btn
            v-if="!AdminProfilState._id && quiz.answerType == 'Text'"
            :loading="loading"
            class="main rounded px-3 py-3 white--text"
            @click="SendAnswer()"
            >Send
          </v-btn>
        </div>
      </div>
      <v-tabs-items v-model="tab">
        <v-tab-item :value="'quiz'">
          <v-col
            cols="12"
            v-if="quiz.answerType == 'Pdf' || quiz.answerType == 'Text'"
            class="quiz"
          >
            <article class="" v-html="quiz.quiz ? quiz.quiz : quiz.question"></article>
            <!-- <article class="article" v-html="quiz.quiz"></article> -->
          </v-col>
          <v-col cols="12" v-else-if="quiz.answerType == 'Multiple Choice'" class="quiz">
            <v-card v-if="success == false" class="mx-auto elevation-0" max-width="900">
              <v-card-title class="title font-weight-regular justify-space-between">
                <span>Exercice</span>
                <span class="rounded elevation-1 px-3 py-2">
                  {{ step + 1 }}/{{ quiz.questions.length }}
                </span>
              </v-card-title>
              <v-window v-model="step">
                <v-window-item
                  v-for="(question, i) in quiz.questions"
                  :key="i"
                  :value="i"
                >
                  <v-card-text>
                    <div>
                      <div class="red--text text-center">{{ errors }}</div>
                      <span class="bold mr-3">Q {{ i + 1 }}-)</span>
                      {{ question.question }}
                    </div>
                    <div class="container ml-md-4">
                      <v-checkbox
                        class="my-0"
                        v-for="(answer, i) in question.answers"
                        :key="i"
                        v-model="selected"
                        :label="answer.choice"
                        :value="answer.choice"
                      ></v-checkbox>
                    </div>
                  </v-card-text>
                </v-window-item>
              </v-window>

              <v-divider></v-divider>
              <v-card-actions>
                <v-btn :disabled="step == 0" text @click="prev"> Back </v-btn>
                <v-spacer></v-spacer>
                <v-btn
                  v-if="step < quiz.questions.length - 1"
                  color="primary"
                  depressed
                  @click="next"
                >
                  Next
                </v-btn>
                <v-btn
                  @click="getResult"
                  :loading="loading"
                  v-if="step == quiz.questions.length - 1"
                  color="primary"
                  depressed
                >
                  Send
                </v-btn>
              </v-card-actions>
            </v-card>
            <circulate-load v-else-if="loading" />
            <div
              v-if="loading == false && success == true"
              class="d-flex justify-center align-center"
            >
              <div class="text-center py-12 my-12">
                <div>
                  <v-icon color="green" class="text-h2"
                    >mdi-clipboard-check-multiple-outline</v-icon
                  >
                </div>
                <h3 class="mt-4 text-h5 text-md-h4">{{ message }}</h3>
                <div v-if="quiz.answerType == 'Multiple Choice'" class="mt-3 text-h5">
                  <h3>Score : {{ mark }} /20</h3>
                </div>
              </div>
            </div>
          </v-col>
        </v-tab-item>
        <v-tab-item :value="'preview-quiz'">
          <v-col cols="12" class="quiz">
            <quiz v-html="body"></quiz>
          </v-col>
        </v-tab-item>
        <v-tab-item :value="'answer'">
          <div class="container article" v-if="showQ.status">
            <h3 class="mb-2">Quiz</h3>
            <article class="" v-html="quiz.quiz ? quiz.quiz : quiz.question"></article>
          </div>
          <div v-if="!AdminProfilState._id">
            <v-col cols="12" v-if="timeOut == false" class="">
              <div class="red--text text-center" v-if="errors">
                {{ errors }}
              </div>
              <div v-if="quiz.answerType == 'Text'">
                <quill-editor
                  v-if="success == false && submit != true"
                  ref="editor"
                  v-model="body"
                  class="mt-5"
                  :options="editorOption"
                />
              </div>
              <div v-else-if="quiz.answerType == 'Pdf'">
                <upload-pdf v-if="!submit" @refresh="successUpload" :link="link" />
              </div>
              <div
                class="d-flex justify-center align-center py-12"
                v-if="success == true"
              >
                <div class="text-center">
                  <v-icon color="green" class="text-h1"
                    >mdi-check-box-multiple-outline</v-icon
                  >
                  <h3 class="text-h5 my-3">{{ message }}</h3>
                </div>
              </div>
            </v-col>
            <time-exhausted v-else-if="timeOut" />
          </div>
        </v-tab-item>
      </v-tabs-items>
    </div>
    <div class="text-center my-6" v-else>Please select one exercice to continue</div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";

import "quill/dist/quill.snow.css";

import axios from "axios";
import { mapGetters } from "vuex";
import UploadPdf from "./uploadPdf.vue";
import timeExhausted from "./timeExhausted.vue";
import CirculateLoad from "./circulateLoad.vue";
export default {
  components: {
    quillEditor,
    UploadPdf,
    timeExhausted,
    CirculateLoad,
  },
  props: {
    quiz: {},
    link: {},
    document: {
      default: "Exercice",
    },
    paper: {
      default: () => {
        return [];
      },
    },
    mark: {
      default: 0,
    },
    submit: {
      default: false,
    },
    timeOut: {
      default: false,
    },
  },
  computed: {
    ...mapGetters(["ErrorBag", "AdminProfilState"]),
  },

  methods: {
    Initialize() {
      this.success = false;
      this.Answers = this.paper;
      this.selected = [];
      this.body = "";
      if (Number(this.mark) > 0) this.success = true;
      if (this.submit) {
        this.message = `You have already answer this ${this.document}`;
        this.success = true;
      }
      if (this.Answers.length > 0) this.getSelected();
    },
    async SendAnswer(mark = null) {
      this.errors = "";
      let data;
      if (mark) {
        data = {
          answers: this.Answers ?? undefined,
          mark,
          submit: true,
        };
      } else if (this.quiz.answerType == "Text") {
        if (this.body == "") return (this.errors = "Body is required");
        data = {
          answer: this.body,
          submit: true,
        };
      } else {
        if (!this.pdf) return (this.errors = "Pdf is required");
        data = new FormData();
        data.append("pdf", this.pdf);
      }
      this.loading = true;

      try {
        await axios.post(`${this.link}`, data);
        this.loading = false;
        this.success = true;
        this.message = "Answer send successfully";
        this.$emit("refresh");
      } catch (err) {
        if (err.response) {
          const status = err.response.status;
          if (status == Number(403)) {
            this.message = `You have already answer this ${this.document}`;
            this.success = true;
          } else {
            this.message = "failed to send this answer";
            this.errors = "failed to send this answer";
          }
        }
      }
      this.loading = false;
      this.snackbar = true;
    },
    toggleShowQ() {
      if (this.showQ.status == false) {
        this.showQ = {
          status: true,
          icon: "mdi-eye-off",
        };
      } else {
        this.showQ = {
          status: false,
          icon: "mdi-eye",
        };
      }
    },
    async save() {
      this.errors = "";
      const question = this.quiz.questions[this.step].question;
      for (let i = 0; i < this.Answers.length; i++) {
        if (question == this.Answers[i].question) {
          this.Answers[i].choices = this.selected;
          await axios.post(`${this.link}`, { answers: this.Answers });

          return;
        }
      }
      this.Answers.push({
        question: question,
        choices: this.selected,
      });
      this.selected = [];
      await axios.post(`${this.link}`, { answers: this.Answers });
    },
    next() {
      if (this.selected.length == 0)
        return (this.errors = "You need to chose one answer at least");
      this.save();
      this.step++;
      this.getSelected();
    },
    prev() {
      if (this.selected.length == 0)
        return (this.errors = "You need to chose one answer at least");
      this.save();
      this.step--;
      this.getSelected();
    },
    getSelected() {
      if (!this.quiz.questions) return;
      const question = this.quiz.questions[this.step].question;
      for (let i = 0; i < this.Answers.length; i++) {
        if (question == this.Answers[i].question) {
          this.selected = this.Answers[i].choices;
        }
      }
    },
    getResult() {
      if (this.selected.length == 0)
        return (this.errors = "You need to chose one answer at least");
      this.save();
      this.getSelected();
      this.resultAlgorithme();
    },
    resultAlgorithme() {
      let finalMark = 0;
      for (let i = 0; i < this.Answers.length; i++) {
        const answer = this.Answers[i];
        const question = this.getQuestion(answer.question);
        let mark = 0;
        for (let c = 0; c < answer.choices.length; c++) {
          const choice = answer.choices[c];
          if (this.isTrue(question.answers, choice)) {
            mark += question.mark / this.totalTrue(question.answers);
          } else {
            mark -= question.badAnswerMark;
          }
        }
        if (mark < 0) mark = 0;
        finalMark += mark;
      }
      finalMark = Math.round(finalMark * 100) / 100;
      this.mark = finalMark;
      this.success = true;
      this.SendAnswer(finalMark);
    },
    getQuestion(question) {
      const questions = this.quiz.questions;
      for (let i = 0; i < questions.length; i++) {
        if (questions[i].question == question) return questions[i];
      }
    },
    isTrue(choices, choice) {
      for (let i = 0; i < choices.length; i++) {
        if (choices[i].choice == choice && choices[i].status == true) return true;
      }
      return false;
    },

    totalTrue(choices) {
      let result = 0;
      for (let i = 0; i < choices.length; i++) {
        if (choices[i].status == true) result++;
      }
      return result;
    },
    retry() {
      this.Initialize();
      this.getSelected();
    },
    successUpload() {
      this.success = true;
      this.message = "Answer send successfully";
      this.submit = true;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      tab: null,
      loading: false,
      errors: "",
      success: true,
      snackbar: false,
      message: "",
      pdf: null,
      step: 0,
      selected: [],
      Answers: [],
      body: "",
      showQ: {
        status: false,
        icon: "mdi-eye",
      },
      editorOption: {
        // Some Quill options...
        theme: "snow",

        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            ["blockquote", "code-block"],
            [{ header: 1 }, { header: 2 }],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }],
            [{ indent: "-1" }, { indent: "+1" }],
            [{ direction: "rtl" }],
            [{ size: ["small", false, "large", "huge"] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ font: [] }],
            [{ color: [] }, { background: [] }],
            [{ align: [] }],
            ["clean"],
            ["link", "image", "video"],
          ],
        },
      },
    };
  },
};
</script>

<style>
.article p {
  margin-bottom: 0rem !important;
}
</style>
