import axios from 'axios'

const state ={
    Departments :[]
   
}

const getters = {
    DepartmentsState : (state)=>state.Departments
    
}

const mutations ={
    
    GET_DEPARTMENTS(state,data){
        state.Departments = data
    },  
    
    
    LOGIN(){
        
    }
 
    
    
}

const actions = {
    getDepartments({commit}){
        return new Promise((resolve, reject) => {
            axios
            .get('/department')
                .then(res=>{
                    commit('GET_DEPARTMENTS', res.data)
                    resolve(res.data)
                },err=>{
                    reject(err)
                })
       }) 
       
    },
    getDepartmentsList({commit}){
        return new Promise((resolve, reject) => {
            axios
            .get('/department/list')
                .then(res=>{
                    commit('GET_DEPARTMENTS',res.data)
                    resolve(res.data)
                },err=>{
                    reject(err)
                })
        })
    },
    
    addDepartment({commit},department){
    
        return new Promise((resolve,reject)=>{
            axios 
                .post('/department',department)
                    .then(res=>{
                        commit('LOGIN')
                        resolve(res.data)
                    }
                    ,err=>{
                        reject(err)
                    })
        })

    },
    editDepartment({commit},department){
        return new Promise((resolve,reject)=>{
            axios 
                .put('/department/'+department._id,department.data)
                    .then(res=>{
                        commit('LOGIN')
                        resolve(res.data)
                    }
                    ,err=>{
                        
                        reject(err)
                    })
        })

    },
    
    deleteDepartment({commit},_id){
        return new Promise((resolve,reject)=>{
            axios 
                .delete('/department/'+_id)
                    .then(res=>{
                        commit('LOGIN')
                        resolve(res.data)
                    }
                    ,err=>{
                        
                        reject(err)
                    })
        })

    },
    
    

    
    
}

export default {
    state,
    getters,
    mutations,
    actions
}