<template>
  <div v-if="link">
    <v-tabs
      v-model="tab"
      class="mb-3"
      color="blue"
      background-color="white"
      centered
      icons-and-text
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab v-if="file.pdf" href="#show-pdf">
        Show pdf
        <v-icon>mdi-file-pdf-box-outline</v-icon>
      </v-tab>

      <v-tab v-if="file.pdf" href="#change-pdf">
        Change Pdf
        <v-icon>mdi-file</v-icon>
      </v-tab>

      <v-tab v-if="!file.pdf" href="#change-pdf">
        Upload Pdf
        <v-icon>mdi-file</v-icon>
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item :value="'show-pdf'">
        <v-col cols="12" class="">
          <div class="mt-4 text-center" v-if="file.type == 'Pdf'">
            <p class="bold">
              Handout :
              <a :href="file.pdf" class="main--text" target="_BLANK">
                {{ "Pdf file" }}
              </a>
            </p>
          </div>
        </v-col>
      </v-tab-item>
      <v-tab-item :value="'change-pdf'">
        <v-col class="bg-tkp rounded uploadVideoContainer" cols="12">
          <div class="uploadVideo" v-if="!inProgress">
            <input type="file" id="" ref="dropzone" @change="uploadPdf" class="" />
            <div class="callToAction d-flex justify-center align-center">
              <div class="text-center">
                <div class="red--text mb-5">{{ errors }}</div>
                <v-icon class="text-h4">mdi-file</v-icon>
                <p class="mb-0 mt-4">Choose or Drag your pdf here</p>
              </div>
            </div>
          </div>
          <div v-else class="d-flex justify-center align-center">
            <div>
              <div class="d-flex justify-center align-center">
                <v-progress-circular
                  :rotate="-90"
                  :size="100"
                  :width="15"
                  :value="progress"
                  color="primary"
                >
                  {{ progress }}%
                </v-progress-circular>
              </div>
              <div class="black--text text-center mt-2 main--text">
                {{ progressMessage }}
              </div>
            </div>
          </div>
        </v-col>
      </v-tab-item>
    </v-tabs-items>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: {
    link: {
      // default: `/professor/chapiter/${this.$route.params.chapiterId}/pdf`,
    },
    file: {
      default: () => {
        return {};
      },
    },
  },
  methods: {
    async uploadPdf() {
      const pdf = this.$refs.dropzone.files[0];
      // this.$refs.dropzone.files[0] = undefined;
      const formData = new FormData();
      if (pdf.type != "application/pdf") return (this.errors = "Only  pdf are supported");
      formData.append("pdf", pdf);
      this.inProgress = true;
      this.progressMessage = "In progress";
      try {
        await axios.put(this.link, formData, {
          onUploadProgress: (e) =>
            (this.progress = Math.round((e.loaded * 100) / e.total)),
        });
      } catch (err) {
        if (err.response) {
          console.log(err.response.status);
          if (err.response.status == 403) {
            this.message = "You have already upload this answer ";
            this.progressMessage = "You have already upload this answer";
            setTimeout(() => {
              this.inProgress = false;
              this.progress = 0;
            }, 1500);
          } else {
            this.message = "Failed to upload this pdf";
            this.errors = "Failed to upload this pdf";
            this.inProgress = false;
            this.progress = 0;
          }
          this.snackbar = true;
        }
        // this.$refs.dropzone.files[0] = null;
        return;
      }
      this.progressMessage = "Done";
      setTimeout(() => {
        this.$refs.dropzone.files[0] = null;
        this.inProgress = false;
        this.message = "pdf uploaded successfully";
        this.snackbar = true;
        this.tab = "show-pdf";
        this.$emit("refresh");
      }, 1000);
    },
  },
  data() {
    return {
      tab: null,
      snackbar: false,
      message: "",

      errors: "",
      chapiter: {},
      progress: 0,
      inProgress: false,
      progressMessage: "",
    };
  },
};
</script>

<style></style>
