<template>
  <div class="m-0 relative" id="auth">
    <v-row>
      <v-col cols="12" sm="8" class="auth d-flex justify-center align-center">
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
          class="px-10 py-12 white--text transparent-bg rounded ml-sm-10"
        >
          <h1>Mavix University</h1>
          <p>Student Account Activation</p>
          <div v-if="errors">
            <p class="red--text">{{ errors }}</p>
          </div>
          <div class="d-flex justify-space-between">
            <v-text-field
              v-model="user.registration_number"
              :rules="registrationRules"
              class=""
              label="Registration Number"
              dark
              color="white"
              required
            ></v-text-field>

            <span class="ml-8"></span>
          </div>
          <div class="d-flex justify-space-between">
            <v-text-field
              v-model="user.email"
              :rules="emailRules"
              class=""
              label="Email"
              dark
              color="white"
              required
            ></v-text-field>

            <span class="ml-8"></span>
          </div>

          <div class="d-flex justify-space-between mb-3">
            <v-text-field
              class=""
              dark
              v-model="user.password"
              color="white"
              :type="passwordVisibility.type"
              label="Password"
              :rules="passwordRules"
              required
            ></v-text-field>
            <span class="mt-5 ml-2">
              <v-icon color="white" @click="managePasswordVisibility(passwordVisibility)">
                {{ passwordVisibility.icon }}
              </v-icon>
            </span>
          </div>
          <div class="d-flex justify-space-between mb-10">
            <v-text-field
              class=""
              dark
              v-model="user.confirmPassword"
              color="white"
              :type="confirmPasswordVisibility.type"
              label="Confirm password"
              required
              :rules="passwordConfirmationRules"
            ></v-text-field>
            <span class="mt-5 ml-2">
              <v-icon
                color="white"
                @click="managePasswordVisibility(confirmPasswordVisibility)"
              >
                {{ confirmPasswordVisibility.icon }}
              </v-icon>
            </span>
          </div>

          <div class="text-center">
            <v-btn
              :disabled="!valid"
              color=""
              :loading="loading"
              class="mr-4 bg-primaire white--text"
              @click="login"
              width="100%"
            >
              Activate
            </v-btn>
          </div>
        </v-form>
      </v-col>
    </v-row>

    <div class="targue">
      <div>
        <h1>Doctor Mavix</h1>
        <span>All right reserve</span>
      </div>
    </div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      errors: "",
      loading: false,
      response: "",
      valid: true,
      message: "",
      snackbar: false,
      emailRules: [
        (v) => !!v || "Email is required",
        (v) => /.+@.+\..+/.test(v) || "Invalid email ",
      ],
      registrationRules: [
        (v) => !!v || "Registration number is required",
        (v) => /.+\/.+\/.+\//.test(v) || "Invalid registration number ",
      ],

      passwordRules: [
        (v) => !!v || "Password is required",
        (v) => (v && v.length >= 4) || "Password must have 4 character at lest",
      ],
      passwordConfirmationRules: [
        (v) => !!v || "Confirm password is required",
        (v) => (v && v == this.user.password) || "Password don't matched",
      ],
      passwordVisibility: {
        icon: "mdi-eye",
        type: "password",
      },
      confirmPasswordVisibility: {
        icon: "mdi-eye",
        type: "password",
      },
      user: {},
    };
  },

  methods: {
    async login() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        const user = {
          registration_number: this.user.registration_number,
          email: this.user.email,
          password: this.user.password,
        };
        try {
          await axios.post(`/student/activation/${this.$route.params.token}`, user);
          this.user.email = "";
          this.user.password = "";
          this.message = "Account activated successfully";
          this.snackbar = true;
          setTimeout(() => {
            // location.reload();
            this.$router.push("/student/login");
          }, 2000);
        } catch (error) {
          this.errors = "Invalid student information";
        }

        this.loading = false;
      }
    },

    //   manage password visibility
    managePasswordVisibility(current) {
      if (current.type == "password") {
        current.icon = "mdi-eye-off";
        current.type = "text";
      } else {
        current.icon = "mdi-eye";
        current.type = "password";
      }
    },
  },
};
</script>

<style>
/* 
.bg-3 {
  background: rgba(0, 4, 12, 0.856) !important;
} */
</style>
