<template>
  <div class="nav-app z-index-100" :class="Collapse ? 'collapse' : ''">
    <v-app-bar class="white pr-md-12" dense dark elevation="0">
      <v-toolbar-title class="d-none d-md-inline"
        ><v-btn color="black" @click="toggleCollapse()" icon>
          <v-icon>mdi-menu</v-icon></v-btn
        ></v-toolbar-title
      >
      <v-spacer></v-spacer>

      <span class="black--text mr-3">{{ Today() }} </span>
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <a
            href=""
            @click.prevent=""
            class="px-5 py-1 rounded-pill bg-mv"
            v-bind="attrs"
            v-on="on"
          >
            <div class="d-flex">
              <v-avatar color="white" size="30">
                <!-- <img src="../../assets/stock/student/student1.jpg" alt="" /> -->
                <v-img aspect-ratio="1" :src="StudentProfilState.idPicture" alt="" />
              </v-avatar>

              <div class="d-flex justify-center align-center">
                <span class="ml-2 main--text">{{
                  StudentProfilState.surname + " " + StudentProfilState.othernames
                }}</span>
              </div>
            </div>
          </a>
        </template>
        <v-list class="z-index-100 white">
          <v-list-item class="white">
            <a href="" @click.prevent="studentLogout()" class="black--text d-block"
              >Logout</a
            >
          </v-list-item>
        </v-list>
      </v-menu>

      <router-link to="/" class="black--text px-2 bold">
        <v-badge :content="2" :value="0" color="green" overlap>
          <v-icon color="black"> mdi-bell-outline </v-icon>
        </v-badge>
      </router-link>
    </v-app-bar>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  components: {},
  computed: {
    ...mapGetters(["StudentProfilState", "Collapse"]),

    isProfessor() {
      return localStorage.getItem("professor");
    },
  },

  methods: {
    ...mapActions(["getStudentProfil", "toggleCollapse", "logout"]),
    Today() {
      const created_date = new Date();

      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];

      const days = [
        "Sunday",
        "Monday",
        "Thuesday",
        "Wednesday",
        "Tueday",
        "Friday",
        "Saterday",
      ];
      const day = days[created_date.getDay()];
      const year = created_date.getFullYear();
      const month = months[created_date.getMonth()];
      const date = created_date.getDate();

      return day + " " + date + " " + month + " " + year; // final date with time, you can use this according your requirement
    },
    async studentLogout() {
      await this.logout();
    },
  },
  mounted() {
    this.getStudentProfil().then(() => {});
  },

  data() {
    return {
      items: [{ title: "Logout" }],
    };
  },
};
</script>

