<template>
  <div class="live-course bg-tkp">
    <div class="py-2 white px-5 rounded d-flex justify-space-between">
      <div class="red--text bold">
        Live
        <v-icon>mdi-video-wireless-outline</v-icon>
      </div>
      <div class="black--text"><v-icon color="red">mdi-record</v-icon> 13:10</div>
      <div>
        <v-btn icon> <v-icon>mdi-message</v-icon></v-btn>
        <!-- <v-btn icon> <v-icon>mdi-info</v-icon></v-btn> -->
        <v-btn icon> <v-icon>mdi-menu</v-icon></v-btn>
      </div>
    </div>
    <div>
      <v-row class="py-0 my-0 px-3">
        <v-col cols="12" class="" md="7" lg="8">
          <v-container>
            <h3>
              <v-avatar><v-icon>mdi-account</v-icon></v-avatar> HTML & CSS chapiter 3
            </h3>
            <div class="mainVideo">
              <video muted ref="mainVideo" class="py-0 my-0 rounded-lg" src=""></video>
              <div class="controls">
                <v-row class="text-center">
                  <v-col cols="4"></v-col>
                  <v-col cols="4">
                    <div>
                      <v-btn color="" @click="shareScreen" class="mx-3" icon
                        ><v-icon>mdi-monitor-screenshot</v-icon></v-btn
                      >
                      <v-btn color="white" class="mx-3 red" icon
                        ><v-icon>mdi-play</v-icon></v-btn
                      >
                      <v-btn color="" @click="shareWebcam" class="mx-3" icon
                        ><v-icon>mdi-video</v-icon></v-btn
                      >
                    </div>
                  </v-col>
                  <v-col cols="4"></v-col>
                </v-row>
              </div>
            </div>
          </v-container>
        </v-col>
        <v-col class="side" cols="12" md="5" lg="4">
          <v-row ref="subVideos">
            <v-col v-for="(stream, i) in subVideos" :key="i" cols="12" md="6" lg="6">
              <sub-video :stream="stream" />
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import SubVideo from "./SubVideo.vue";

export default {
  components: { SubVideo },
  sockets: {
    async userConnected(user) {
      console.log("user : ", user);
      await this.connectToNewUser(user);
    },
  },
  props: {
    isProfessor: {
      default: false,
    },
    userId: {
      required: true,
    },
  },
  computed: {
    ...mapGetters(["AdminProfilState", "StudentProfilState"]),
  },
  methods: {
    async shareScreen() {
      const constraints = { video: {} };
      try {
        const mediaDevice = navigator.mediaDevices || navigator.webkitGetUserMedia;
        const stream = await mediaDevice.getDisplayMedia(constraints);
        //verify if is professor or user

        if (this.AdminProfilState && this.isProfessor) {
          console.log("here");
          this.stopMainVideo();
          this.profStream = stream;
          this.stream = stream;
        } else {
          this.subVideos.push(stream);
          //   this.$sockets.emit("live");
          this.stream = stream;

          //   this.subVideos.push(stream);
        }
        await this.joinLiveCourse();
        this.$peer.on("call", (call) => {
          call.answer(stream);
        });
      } catch (error) {
        console.log(error);
      }
    },
    async shareWebcam() {
      const mediaDevice = navigator.mediaDevices || navigator.webkitGetUserMedia;

      const constraints = {
        audio: true,
        video: {
          width: {
            min: 1280,
            ideal: 1920,
            max: 2560,
          },
          height: {
            min: 720,
            ideal: 1080,
            max: 1440,
          },
        },
      };
      try {
        const stream = await mediaDevice.getUserMedia(constraints);

        this.profStream = stream;
        await this.joinLiveCourse();
        await this.$peer.on("call", (call) => {
          call.answer(stream);
        });
      } catch (error) {
        console.log(error);
      }
    },
    stopMainVideo() {
      this.$refs.mainVideo.pause();
      this.$refs.mainVideo.srcObject = null;
    },
    async connectToNewUser(user) {
      const stream = this.stream;
      const call = await this.$peer.call(user, stream);
      call.on("stream", (userVideoStream) => {
        this.subVideos.push(userVideoStream);
      });
      call.on("close", () => {
        console.log("close");
      });
    },
    async joinLiveCourse() {
      try {
        // const peer = await this.$peer.connect("");
        // console.log(peer);
        console.log("try to join");
        await this.$peer.on("open", async (id) => {
          console.log("here", id);
          const schema = {
            liveId: this.$route.params.liveId,
            userId: id,
          };
          await this.$socket.emit("joinLiveCourse", schema);
        });
      } catch (error) {
        console.log(error);
      }
    },
  },
  data() {
    return {
      profStream: null,
      subVideos: [],
      stream: null,
    };
  },
  watch: {
    profStream() {
      if (this.$refs.mainVideo) {
        this.$refs.mainVideo.srcObject = this.profStream;
        this.$refs.mainVideo.addEventListener("loadedmetadata", () => {
          this.$refs.mainVideo.play();
        });
      }
    },
  },
};
</script>

<style>
.live-course {
  width: 100vw;
  height: 100vh;
  /* background: rgba(0, 0, 0, 0.877); */
  /* color: white; */
  position: fixed;
}
.live-course .mainVideo {
  height: 70vh;
  width: 100%;
}
.live-course video {
  width: 100% !important;
  height: 100% !important;
}

.live-course .side {
  overflow-y: scroll;
  height: 90vh;
}
.live-course .subVideo {
  position: relative;
}
.live-course .subVideo .controls {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  color: aliceblue;
}
</style>
