<template>
  <div>
    <div>
      <v-row>
        <v-col cols="12" md="6">
          <v-row>
            <v-col class="d-flex" cols="12" md="5">
              <v-select
                :items="applicationAnswerType"
                label="Exercice Answer Type"
                v-model="AnswerType"
                dense
                outlined
              ></v-select>
            </v-col>
            <v-col v-if="examExist == false" class="d-flex" cols="12" md="5">
              <v-select
                :items="['Exam', 'Test']"
                label="Composition type"
                v-model="compositionModel"
                dense
                outlined
              ></v-select>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" md="6">
          <div class="d-md-flex justify-end">
            <v-col class="d-flex align-center" cols="12" md="4" lg="3">
              <v-switch
                v-model="isOnline"
                color="green"
                inset
                class="my-0 py-0"
                :label="`Online`"
              ></v-switch>
            </v-col>
            <v-col class="d-flex align-center" cols="12" md="4" lg="3">
              <v-switch
                v-model="isImportant"
                color="red"
                inset
                class="my-0 py-0"
                :label="`Important`"
              ></v-switch>
            </v-col>
          </div>
        </v-col>
      </v-row>
      <v-row>
        <v-col v-if="compositionModel == 'Test'" cols="12" md="6">
          <v-menu
            v-model="menu.Date"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date"
                label="Date"
                prepend-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="date" @input="menu.Date = false"></v-date-picker>
          </v-menu>
        </v-col>
        <v-col v-if="compositionModel == 'Test'" cols="12" md="6">
          <v-menu
            ref="startTime"
            v-model="menu.startTime"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="time"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="time"
                label="Start Time"
                prepend-icon="mdi-clock-time-ten-outline"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-time-picker
              v-if="menu.startTime"
              v-model="time"
              full-width
              @click:minute="$refs.startTime.save(time)"
            ></v-time-picker>
          </v-menu>
        </v-col>

        <v-col cols="12" md="6">
          <v-text-field
            label="Duration"
            :rules="durationRules"
            v-model="duration"
            prepend-icon="mdi-timelapse"
            placeholder=""
            dense
            hint="in minute"
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="6">
          <v-select
            :items="['10', '20', '100']"
            v-model="totalMark"
            dense
            prepend-inner-icon="mdi-scoreboard-outline"
            label="Total mark"
          ></v-select>
        </v-col>
      </v-row>
      <create-composition
        v-if="AnswerType == 'Text' || AnswerType == 'Pdf'"
        ref="createComposition"
        :online="isOnline"
        :important="isImportant"
        :quiz="composition ? composition.quiz : undefined"
        :duration="duration"
        :mark="totalMark"
        :answer="AnswerType"
        :day="compositionModel == 'Test' ? { date, time } : undefined"
        @refresh="successCreated"
        :link="
          composition
            ? `/professor/course/${$route.params.id}/composition/${composition._id}`
            : `/professor/course/${$route.params.id}/composition`
        "
        :document="compositionModel"
      />
      <create-multi-choice-composition
        v-if="AnswerType == 'Multiple Choice'"
        :link="
          composition
            ? `/professor/course/${$route.params.id}/composition/${composition._id}`
            : `/professor/course/${$route.params.id}/composition`
        "
        :document="compositionModel"
        ref="createComposition"
        :online="isOnline"
        :important="isImportant"
        :quiz="composition ? composition.questions : undefined"
        :duration="duration"
        :mark="totalMark"
        :day="compositionModel == 'Test' ? { date, time } : undefined"
        :answer="AnswerType"
        :exercice="composition ? composition.questions : undefined"
        @refresh="successCreated"
      />
    </div>

    <v-slide-y-transition>
      <v-dialog v-model="DeleteDialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this exercice ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every thing about this exercice will be deleted too
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="bg-secondaire white--text"
              text
              @click="(exerciceDeleteId = ''), (DeleteDialog = false)"
            >
              Cancel
            </v-btn>
            <v-btn class="red white--text" text @click="remove(exerciceDeleteId)">
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import createComposition from "../professor/createComposition.vue";
import axios from "axios";
import CreateMultiChoiceComposition from "./createMultiChoiceComposition.vue";
export default {
  components: { createComposition, CreateMultiChoiceComposition },
  props: {
    exercices: {},
    compositionType: {
      required: true,
    },
    composition: {},
    examExist: {
      default: false,
    },
  },
  mounted() {
    this.Initialize();
  },
  methods: {
    Initialize() {
      this.compositionModel = this.compositionType;
      this.date = "";
      this.time = "";
      this.totalMark = "";
      this.duration = "";
      this.AnswerType = "Text";
      if (this.composition) {
        this.AnswerType = this.composition.answerType ?? "Text";
        this.totalMark = `${this.composition.totalMark}` ?? "20";
        this.duration = this.composition.duration ?? "";
        this.isOnline = this.composition.online ?? true;
        this.isImportant = this.composition.important ?? false;
        if (this.composition.day) {
          this.date = this.composition.day.date ?? "";
          this.time = this.composition.day.time ?? "";
        }
      }
    },
    successCreated() {
      this.tab = "show";
      this.$emit("refresh");
      this.Initialize();
      if (this.$refs.createComposition) this.$refs.createComposition.Initialize();
    },
    edit(exercice) {
      this.AnswerType = exercice.answerType;
      this.isOnline = exercice.online;

      this.exerciceUpdate = exercice;
      this.isCreated = false;
      this.tab = "update";
    },
    async remove(id) {
      try {
        await axios.delete(`/professor/exercice/${id}`);
        this.message = "Exercice deleted successfully";
      } catch (error) {
        this.message = "Failed to delete this exercice";
      }
      this.DeleteDialog = false;
      this.exerciceDeleteId = "";
      this.snackbar = true;
      this.$emit("refresh");
    },
  },
  watch: {
    compositionType(val) {
      this.compositionModel = val;
    },
    composition() {
      this.Initialize();
    },
  },
  data() {
    return {
      snackbar: false,
      message: "",
      exerciceDeleteId: "",
      DeleteDialog: false,
      errors: "",
      chapiter: {},
      progress: 0,
      inProgress: false,
      progressMessage: "",
      applicationAnswerType: ["Text", "Pdf", "Multiple Choice"],
      AnswerType: "Text",
      totalMark: "20",
      isOnline: true,
      isImportant: false,
      exerciceUpdate: {},
      isCreated: true,
      date: "",
      time: "",
      menu: {
        endDate: false,
        endTime: false,
        startDate: false,
        startTime: false,
      },
      duration: "",
      durationRules: [
        (v) => !!v || "Duration is required",
        (v) => (v && Number.isInteger(Number(v))) || "Duration must be a whole number",
      ],
      compositionModel: "",
    };
  },
};
</script>

<style></style>
