<template>
  <div class="h-100 bg-tkp">
    <div v-if="ChapiterState.title">
      <v-row class="px-md-12 py-md-5 px-2 py-2">
        <v-col cols="12" md="4">
          <div class="d-md-flex justify-space-between">
            <h3 class="text-h5 mb-3">
              <router-link
                :to="'/admin/visit-course/' + ChapiterState.other_informations.course._id"
                class="black--text"
              >
                {{ ChapiterState.other_informations.course.name }}</router-link
              >
            </h3>
            <div class="d-none flex-column flex-md-row">
              <a
                href=""
                @click.prevent="Refresh()"
                class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
                >Refrech</a
              >
            </div>
          </div>
          <v-container class="rounded white px-md-12 py-12">
            <div>
              <div class="d-flex justify-space-between">
                <h1 class="text-h5">
                  <span>Chapiter {{ ChapiterState.number }} </span> :
                  {{ ChapiterState.title }}
                </h1>
                <div>
                  <v-icon v-if="ChapiterState.type == 'Video'" class="main--text"
                    >mdi-video-vintage</v-icon
                  >
                  <v-icon v-if="ChapiterState.type == 'Article'" class="main--text"
                    >mdi-email-newsletter</v-icon
                  >
                  <v-icon v-if="ChapiterState.type == 'Pdf'" class="main--text"
                    >mdi-file-pdf-box-outline</v-icon
                  >
                  <v-icon v-if="ChapiterState.type == 'Live'" class="main--text"
                    >mdi-video-wireless-outline</v-icon
                  >
                </div>
              </div>
              <p class="mt-3 grey--text">
                <span class="">Created by </span>
                <span class="bold">
                  <span class="main--text">{{
                    ChapiterState.other_informations.created_by.grade
                  }}</span>
                  {{ ChapiterState.other_informations.created_by.firstname }}
                  {{ ChapiterState.other_informations.created_by.lastname }}
                </span>
              </p>

              <div>
                <div v-if="ChapiterState.description" class="mt-3">
                  <p class="bold">What about this chapiter ?</p>
                  <article class="text-sm" v-html="ChapiterState.description"></article>
                  <p class="bold">Objectif(s) of this chapiter:</p>
                  <ul>
                    <li v-for="(objectif, i) in ChapiterState.objectifs" :key="i">
                      {{ objectif }}
                    </li>
                  </ul>
                </div>
                <div class="mt-4" v-if="ChapiterState.type == 'Link'">
                  <p class="bold">
                    Course link :
                    <a :href="ChapiterState.link" class="main--text" target="_BLANK">
                      {{ ChapiterState.link }}
                    </a>
                  </p>
                </div>
              </div>
            </div>
          </v-container>
        </v-col>

        <v-col cols="12" md="8">
          <v-tabs
            v-model="tab"
            class="rounded mt-12"
            color="white"
            background-color="transparent-bg"
            centered
            dark
            icons-and-text
          >
            <v-tabs-slider></v-tabs-slider>

            <v-tab v-if="ChapiterState.type != 'Link'" href="#leson"> Lesson </v-tab>

            <v-tab href="#exercice"> Application Exercice </v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item :value="'leson'">
              <v-container class="rounded px-md-12 py-6">
                <h3 class="mb-8 text-center">
                  Lesson
                  <v-icon class="main--text ml-1">mdi-book-open-page-variant</v-icon>
                </h3>
                <div>
                  <show-lesson :file="ChapiterState" />
                </div>
              </v-container>
            </v-tab-item>
            <v-tab-item :value="'exercice'">
              <v-container class="py-6 rounded white">
                <h3 class="mb-8 text-center">
                  Application Exercice
                  <v-icon class="main--text">mdi-frequently-asked-questions</v-icon>
                </h3>
                <div>
                  <show-exercice
                    @refresh="Initialize"
                    :exercices="ChapiterState.other_informations.exercices"
                  />
                </div>
              </v-container>
            </v-tab-item>
          </v-tabs-items>
        </v-col>
      </v-row>
    </div>
    <div v-else-if="loader == false && !ChapiterState.name">
      <can-not-find what="this Chapiter" />
    </div>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import ShowExercice from "../../components/universal/showExercice.vue";
import ShowLesson from "../../components/universal/showLesson.vue";

export default {
  components: {
    CanNotFind,
    CirculateLoad,
    ShowLesson,
    ShowExercice,
  },
  computed: {
    ...mapGetters(["ChapiterState", "AdminProfilState"]),
  },
  methods: {
    ...mapActions(["getProfessorChapiter", "updateChapiterGoal"]),
    Refresh() {
      this.Initialize();

      this.message = "Faculty refresh successfully";
      this.snackbar = true;
    },

    Initialize() {
      this.loading = true;
      this.loader = true;
      this.chapiter = {};
      this.errors = "";
      this.Modal = false;
      this.getProfessorChapiter(this.$route.params.id).then(
        () => {
          this.loading = false;
          this.loader = false;
        },
        () => {
          this.loading = false;
          this.loader = false;
        }
      );
    },
    remove(item) {
      this.chapiter.objectifs.splice(this.chapiter.objectifs.indexOf(item), 1);
      this.chapiter.objectifs = [...this.chapiter.objectifs];
    },

    saveChapiterDetail() {
      if (this.$refs.descriptionForm.validate()) {
        if (this.chapiter.objectifs.length < 1) {
          return (this.errors = "Objectif is required");
        }
        const formData = new FormData();
        formData.append("description", this.chapiter.description);
        formData.append("objectifs", this.chapiter.objectifs);
        if (this.chapiter.link) formData.append("link", this.chapiter.link);
        if (this.chapiter.pdf) formData.append("pdf", this.chapiter.pdf);

        this.errors = "";
        const Detail = {
          id: this.$route.params.id,
          data: formData,
        };

        this.loading = true;

        this.updateChapiterGoal(Detail).then(
          () => {
            this.loading = false;
            this.message = "Chapiter goal updated successfully";
            this.snackbar = true;
            this.Initialize();
          },
          () => {
            this.loading = false;
            this.message = "Failed to update chapiter goal";
            this.snackbar = true;
          }
        );
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      Modal: false,
      ModalText: {},
      ModalModel: {},
      valid: true,
      snackbar: false,
      message: "",
      items: [],
      errors: "",
      loading: false,
      loader: true,
      chapiter: {},

      linkRules: [(v) => !!v || "Link is required"],
      tab: null,
    };
  },
};
</script>

<style></style>
