<template>
  <div>
    <v-tabs
      v-model="tab"
      class="mb-3"
      color="blue"
      background-color="white"
      centered
      icons-and-text
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab @click="(exerciceUpdate = {}), (isCreated = true)" href="#show">
        Show Exercice
        <v-icon>mdi-head-question-outline</v-icon>
      </v-tab>

      <v-tab @click="AnswerType = 'Text'" v-if="isCreated" href="#create">
        Create Exercice
        <v-icon>mdi-cloud-question</v-icon>
      </v-tab>

      <v-tab v-if="!isCreated" href="#update">
        Update Exercice
        <v-icon>mdi-crosshairs-question</v-icon>
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item :value="'show'">
        <div class="container">
          <div
            class="my-2 d-flex justify-space-between"
            v-for="(exercice, i) in exercices"
            :key="exercice._id"
          >
            <router-link class="" to="">
              <div>
                Exercice {{ i + 1 }} ({{ exercice.answerType }})
                <v-icon v-if="exercice.online == true" color="green"
                  >mdi-circle-medium</v-icon
                >
              </div>
            </router-link>
            <div>
              <v-btn color="orange" @click="edit(exercice)" icon>
                <v-icon>mdi-file-edit</v-icon></v-btn
              >
              <v-btn
                @click="(exerciceDeleteId = exercice._id), (DeleteDialog = true)"
                color="red"
                icon
              >
                <v-icon>mdi-delete</v-icon></v-btn
              >
            </div>
          </div>
        </div>
      </v-tab-item>
      <v-tab-item :value="'create'">
        <div>
          <v-row>
            <v-col class="d-flex" cols="12" md="3">
              <v-select
                :items="applicationAnswerType"
                label="Exercice Answer Type"
                v-model="AnswerType"
                dense
                outlined
              ></v-select>
            </v-col>
            <v-col class="d-flex" cols="12" md="3">
              <v-select
                :items="['no', 'yes']"
                label="Time Limite"
                v-model="timeLimite"
                dense
                outlined
              ></v-select>
            </v-col>
            <v-col class="d-flex align-center" cols="12" md="3">
              <v-switch
                v-model="isOnline"
                color="green"
                inset
                class="my-0 py-0"
                :label="`Online`"
              ></v-switch>
            </v-col>
          </v-row>
          <v-row v-if="timeLimite == 'yes'">
            <v-col cols="12" md="6">
              <v-menu
                v-model="menu.startDate"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date.start"
                    label="Starting date"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date.start"
                  @input="menu.startDate = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu
                ref="startTime"
                v-model="menu.startTime"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="time.start"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="time.start"
                    label="Start Time"
                    prepend-icon="mdi-clock-time-ten-outline"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="menu.startTime"
                  v-model="time.start"
                  full-width
                  @click:minute="$refs.startTime.save(time.start)"
                ></v-time-picker>
              </v-menu>
            </v-col>

            <v-col cols="12" md="6">
              <v-menu
                v-model="menu.endDate"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date.end"
                    label="Ending date"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date.end"
                  @input="menu.endDate = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu
                ref="endTime"
                v-model="menu.endTime"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="time.end"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="time.end"
                    label="End time"
                    prepend-icon="mdi-clock-time-four-outline"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="menu.endTime"
                  v-model="time.end"
                  full-width
                  @click:minute="$refs.endTime.save(time.end)"
                ></v-time-picker>
              </v-menu>
            </v-col>
          </v-row>
          <create-article
            v-if="AnswerType == 'Text' || AnswerType == 'Pdf'"
            ref="createArticle"
            :online="isOnline"
            :timeLimite="timeLimite != 'no' ? { date, time } : false"
            @refresh="successCreated"
            :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/exercice/${AnswerType}`"
            document="quiz"
          />

          <create-multi-choice-exercice
            :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/exercice/${AnswerType}`"
            v-if="AnswerType == 'Multiple Choice'"
            @refresh="successCreated"
          />
        </div>
      </v-tab-item>
      <v-tab-item :value="'update'">
        <div>
          <v-row>
            <v-col class="d-flex" cols="12" md="3">
              <v-select
                :items="applicationAnswerType"
                label="Exercice Answer Type"
                v-model="AnswerType"
                dense
                outlined
              ></v-select>
            </v-col>
            <v-col v-if="InCoursePeriod" class="d-flex" cols="12" md="3">
              <v-select
                :items="['no', 'yes']"
                label="Time Limite"
                v-model="timeLimite"
                dense
                outlined
              ></v-select>
            </v-col>
            <v-col class="d-flex align-center" cols="12" md="3">
              <v-switch
                v-model="isOnline"
                color="green"
                inset
                class="my-0 py-0"
                :label="`Online`"
              ></v-switch>
            </v-col>
          </v-row>
          <v-row v-if="timeLimite == 'yes'">
            <v-col cols="12" md="6">
              <v-menu
                v-model="menu.startDate"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date.start"
                    label="Starting date"
                    :rules="startDateRules"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date.start"
                  @input="menu.startDate = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <!-- {{ isInCoursePeriod(new Date(date.start)) }} -->
              <v-menu
                ref="startTime"
                v-model="menu.startTime"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="time.start"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="time.start"
                    label="Start Time"
                    prepend-icon="mdi-clock-time-ten-outline"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="menu.startTime"
                  v-model="time.start"
                  full-width
                  @click:minute="$refs.startTime.save(time.start)"
                ></v-time-picker>
              </v-menu>
            </v-col>

            <v-col cols="12" md="6">
              <v-menu
                v-model="menu.endDate"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date.end"
                    label="Ending date"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date.end"
                  @input="menu.endDate = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
            <v-col cols="12" md="6">
              <v-menu
                ref="endTime"
                v-model="menu.endTime"
                :close-on-content-click="false"
                :nudge-right="40"
                :return-value.sync="time.end"
                transition="scale-transition"
                offset-y
                max-width="290px"
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="time.end"
                    label="End time"
                    :rules="endDateRules"
                    prepend-icon="mdi-clock-time-four-outline"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-time-picker
                  v-if="menu.endTime"
                  v-model="time.end"
                  full-width
                  @click:minute="$refs.endTime.save(time.end)"
                ></v-time-picker>
              </v-menu>
            </v-col>
          </v-row>
          <create-article
            v-if="AnswerType == 'Text' || AnswerType == 'Pdf'"
            ref="createArticle"
            :online="isOnline"
            :timeLimite="timeLimite != 'no' ? { date, time } : false"
            @refresh="successCreated"
            :article="exerciceUpdate.question"
            :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/exercice/${AnswerType}/${exerciceUpdate._id}`"
            document="quiz"
          />
          <create-multi-choice-exercice
            :link="`/professor/course/${$route.params.courseId}/chapiter/${$route.params.chapiterId}/exercice/${AnswerType}/${exerciceUpdate._id}`"
            v-if="AnswerType == 'Multiple Choice'"
            :exercice="exerciceUpdate.questions"
            :totalMark="exerciceUpdate.totalMark"
            @refresh="successCreated"
          />
        </div>
      </v-tab-item>
    </v-tabs-items>

    <v-slide-y-transition>
      <v-dialog v-model="DeleteDialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this exercice ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every thing about this exercice will be deleted too
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              class="bg-secondaire white--text"
              text
              @click="(exerciceDeleteId = ''), (DeleteDialog = false)"
            >
              Cancel
            </v-btn>
            <v-btn class="red white--text" text @click="remove(exerciceDeleteId)">
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>
    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import createArticle from "../universal/createArticle.vue";
import axios from "axios";
import CreateMultiChoiceExercice from "./createMultiChoiceExercice.vue";
import { mapGetters } from "vuex";
export default {
  components: { createArticle, CreateMultiChoiceExercice },
  props: {
    exercices: {},
  },
  computed: {
    ...mapGetters(["InCoursePeriod", "isInCoursePeriod"]),
    startDateRules() {
      console.log(this.date.start);
      return [
        (v) =>
          (v && this.isInCoursePeriod(new Date(this.date.start))) ||
          "The date must be in class program period",
      ];
    },
  },
  watch: {
    timeLimite(val) {
      if (val == "no") {
        this.date = {};
        this.time = {};
      }
    },
  },
  methods: {
    successCreated() {
      this.tab = "show";
      this.$emit("refresh");
      this.timeLimite = "no";
      if (this.$refs.createArticle) this.$refs.createArticle.Initialize();
      this.message = "Exercice saved successfully";
      this.snackbar = true;
    },
    edit(exercice) {
      this.AnswerType = exercice.answerType;
      this.isOnline = exercice.online;
      if (exercice.timeLimite) {
        this.timeLimite = "yes";
        this.date = exercice.timeLimite.date;
        this.time = exercice.timeLimite.time;
      }
      this.exerciceUpdate = exercice;
      this.isCreated = false;
      this.tab = "update";
    },
    async remove(id) {
      try {
        await axios.delete(
          `/professor/course/${this.$route.params.courseId}/exercice/${id}`
        );
        this.message = "Exercice deleted successfully";
      } catch (error) {
        this.message = "Failed to delete this exercice";
      }
      this.DeleteDialog = false;
      this.exerciceDeleteId = "";
      this.snackbar = true;
      this.$emit("refresh");
    },
  },
  data() {
    return {
      tab: null,
      snackbar: false,
      message: "",
      exerciceDeleteId: "",
      DeleteDialog: false,

      endDateRules: [
        (v) => (v && v.length <= 10) || "Name must be less than 10 characters",
      ],
      errors: "",
      chapiter: {},
      progress: 0,
      inProgress: false,
      progressMessage: "",
      applicationAnswerType: ["Text", "Pdf", "Multiple Choice"],
      AnswerType: "Text",
      timeLimite: "no",
      isOnline: true,
      exerciceUpdate: {},
      isCreated: true,
      date: {},
      time: {},
      menu: {
        endDate: false,
        endTime: false,
        startDate: false,
        startTime: false,
      },
    };
  },
};
</script>

<style></style>
