<template>
  <div class="bg-tkp h-100">
    <v-container v-if="Notifications && loader == false">
      <div class="white rounded px-3 py-3">
        <div class="d-flex justify-space-between">
          <h1 class="my-3">Notifications <v-icon color="red">mdi-bell</v-icon></h1>
          <div class="py-5">
            <a href="" @click.prevent="refresh()" class="main--text">Refresh</a>
          </div>
        </div>

        <div>
          <notification :Notifications="Notifications.notifications" />
        </div>
      </div>
    </v-container>
    <circulate-load v-else />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CirculateLoad from "../../components/universal/circulateLoad.vue";
import Notification from "../../components/universal/Notification.vue";

export default {
  components: { Notification, CirculateLoad },
  computed: {
    ...mapGetters(["Notifications"]),
  },
  methods: {
    ...mapActions(["professorGetNotifications"]),
    refresh() {
      this.Initialize();
    },
    async Initialize() {
      this.loader = true;
      try {
        await this.professorGetNotifications();
      } catch (error) {
        console.log(error);
      }
      this.loader = false;
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      loader: false,
    };
  },
};
</script>

