<template>
  <v-container class="">
    <div
      v-if="TestPaper.test.extra && Number(TestPaper.test.extra.mark) > 0"
      class="rounded py-5 elevation-1 px-md-12 px-4 white--text transparent-bg"
    >
      <h3>{{ `+ ${TestPaper.test.extra.mark} for every student ` }}</h3>
      <div class="mt-3">
        <p class="bold mb-2">Justification :</p>
        <div>
          {{ TestPaper.test.extra.justification }}
        </div>
      </div>
    </div>
    <div class="white px-md-12 py-12 px-4">
      <div class="d-md-flex justify-space-between">
        <div class="">
          <p>
            <span class="bold">Test Type : </span>
            {{ TestPaper.test.answerType }}
          </p>
          <p>
            <span class="bold">Duration: </span>
            {{ TestPaper.test.duration }} min
          </p>
          <p v-if="TestPaper.paper">
            <span class="bold">Submit: </span> {{ TestPaper.paper.submit }}
          </p>
        </div>

        <div v-if="TestPaper.paper">
          <div
            class="d-flex justify-center align-center order-md-2"
            v-if="TestPaper.paper.mark"
          >
            <v-avatar size="100" class="red--text text-h5 border-red">
              {{ TestPaper.paper.mark }} /
              {{ TestPaper.test.totalMark }}
            </v-avatar>
            <div
              class=""
              v-if="TestPaper.test.extra && Number(TestPaper.test.extra.mark) > 0"
            >
              <span class="mx-3 text-h5">==></span>
              <v-avatar size="100" class="red--text text-h5 border-red">
                {{ totalMark }}
                /
                {{ TestPaper.test.totalMark }}
              </v-avatar>
            </div>
          </div>
        </div>
        <div class="d-md-flex align-center" v-else>
          <div
            class="red--text border-red rounded bold px-5 py-3 d-flex justify-content-center align-items-center"
          >
            <span>not compose</span>
          </div>

          <div
            class=""
            v-if="TestPaper.test.extra && Number(TestPaper.test.extra.mark) > 0"
          >
            <span class="mx-3 text-h5"> +</span>
            <v-avatar size="100" class="red--text text-h5 border-red">
              {{ TestPaper.test.extra.mark }}
              /
              {{ TestPaper.test.totalMark }}
            </v-avatar>
          </div>
        </div>
      </div>
      <div v-if="TestPaper.paper">
        <h3 class="mb-4 main--text">Paper</h3>
        <correction-multichoice
          v-if="TestPaper.test.answerType == 'Multiple Choice'"
          :Answers="TestPaper.paper.answers"
          :Correction="TestPaper.test.questions"
        />
        <correction-simple-document
          v-else
          document="Test"
          :Answers="TestPaper.paper.answer"
          :Quiz="TestPaper.test.quiz"
          :Pdf="TestPaper.paper.pdf"
        />
      </div>
    </div>
  </v-container>
</template>

<script>
import CorrectionMultichoice from "./correctionMultichoice.vue";
import correctionSimpleDocument from "./correctionSimpleDocument.vue";
export default {
  components: { correctionSimpleDocument, CorrectionMultichoice },
  computed: {
    totalMark() {
      const mark = this.TestPaper.paper.mark;
      if (
        Number(this.TestPaper.test.totalMark) >
        Number(mark) + Number(this.TestPaper.test.extra.mark)
      ) {
        return Number(mark) + Number(this.TestPaper.test.extra.mark);
      } else {
        return this.TestPaper.test.totalMark;
      }
    },
  },
  props: {
    TestPaper: {
      required: true,
    },
  },
};
</script>
