<template>
  <div>
    <div class="d-md-flex">
      <v-tabs
        v-model="tab"
        class="mb-3"
        color="blue"
        background-color="white"
        left
        icons-and-text
      >
        <v-tabs-slider></v-tabs-slider>

        <v-tab v-if="quiz" href="#quiz">
          {{ document }}
          <v-icon>mdi-email-newsletter</v-icon>
        </v-tab>

        <v-tab v-if="quiz" href="#update-quiz">
          Update {{ document }}
          <v-icon>mdi-file-document-edit-outline</v-icon>
        </v-tab>

        <v-tab v-if="!quiz" href="#update-quiz">
          Create {{ document }}
          <v-icon>mdi-file-document-edit-outline</v-icon>
        </v-tab>
        <v-tab href="#preview-quiz">
          Preview
          <v-icon>mdi-view-agenda-outline</v-icon>
        </v-tab>
      </v-tabs>
      <div class="d-flex justify-center align-center mr-md-4">
        <v-btn
          :loading="loading"
          class="main rounded px-3 py-3 white--text"
          @click="SaveArticle"
          >Save
        </v-btn>
      </div>
    </div>
    <v-tabs-items v-model="tab">
      <v-tab-item :value="'quiz'">
        <v-col cols="12" class="article">
          <article v-html="quiz"></article>
        </v-col>
      </v-tab-item>
      <v-tab-item :value="'preview-quiz'">
        <v-col cols="12" class="article">
          <article v-html="body"></article>
        </v-col>
      </v-tab-item>
      <v-tab-item :value="'update-quiz'">
        <v-col cols="12" class="">
          <div class="red--text text-center" v-if="errors">
            {{ errors }}
          </div>
          <quill-editor
            ref="editor"
            v-model="body"
            class="mt-5"
            :options="editorOption"
          />
        </v-col>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";

import "quill/dist/quill.snow.css";

import axios from "axios";

import hljs from "highlight.js";

import "highlight.js/styles/atom-one-dark.css";

export default {
  components: {
    quillEditor,
  },
  props: {
    quiz: {},
    link: {},
    document: {
      default: "Test",
    },
    online: {
      default: true,
    },
    important: {
      default: false,
    },
    day: {
      default: undefined,
    },
    duration: {
      required: true,
    },
    mark: {
      required: true,
    },
    answer: {
      default: "Text",
    },
  },
  watch: {
    quiz() {
      this.Initialize();
    },
  },
  methods: {
    Initialize() {
      this.body = this.quiz;
    },
    async SaveArticle() {
      this.errors = "";
      // composition options validation
      let day = undefined;
      const composition = this.document;
      const mark = this.mark ?? undefined;
      const duration = this.duration ?? undefined;
      const answer = this.answer;
      if (!Number.isInteger(Number(duration)))
        return (this.errors = "Duration is not valid");
      if (!mark) return (this.errors = "Total mark is required");
      const online = this.online ?? undefined;
      const important = this.important ?? false;
      if (this.day != false && this.day != undefined) {
        if (!this.day.date || !this.day.time)
          return (this.errors = "Your time or your date is not valid");
        const dayDate = new Date(`${this.day.date} ${this.day.time}`);
        const today = new Date();
        if (dayDate.getTime() < today.getTime())
          return (this.errors = "We have already passed that day");
        day = this.day;
      }

      //quiz body validation
      if (this.body == "" || this.body == undefined)
        return (this.errors = "Can not save empty document");
      this.loading = true;
      try {
        await axios.post(`${this.link}`, {
          quiz: this.body,
          day,
          duration,
          online,
          important,
          mark,
          answer,
          composition,
        });
        this.loading = false;
        this.message = "Chapiter quiz save ";
        this.snackbar = true;

        this.$emit("refresh");
        this.tab = "quiz";
      } catch (err) {
        this.loading = false;
        this.message = "failed to save this quiz";
        this.snackbar = true;
        this.errors = "failed to save this quiz";
      }
    },
  },
  mounted() {
    this.Initialize();
  },
  data() {
    return {
      tab: null,
      loading: false,
      errors: "",
      snackbar: false,
      message: "",
      body: "",
      editorOption: {
        // Some Quill options...
        theme: "snow",

        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            ["blockquote", "code-block"],
            [{ header: 1 }, { header: 2 }],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }],
            [{ indent: "-1" }, { indent: "+1" }],
            [{ direction: "rtl" }],
            [{ size: ["small", false, "large", "huge"] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ font: [] }],
            [{ color: [] }, { background: [] }],
            [{ align: [] }],
            ["clean"],
            ["link", "image", "video"],
          ],
          syntax: {
            highlight: (text) => hljs.highlightAuto(text).value,
          },
        },
      },
    };
  },
};
</script>

<style>
.ql-container {
  min-height: 20vh !important;
}

pre {
  box-sizing: border-box;
  width: 100%;
  padding: 0;
  margin: 0;
  overflow: auto;
  overflow-y: hidden;
  font-size: 12px;
  line-height: 20px;
  /* background: #fcfbfb; */
  background: #202020;
  border: 1px solid rgb(221, 221, 221);
  padding: 10px;
  /* color: #333; */
  color: white;
}
</style>
