export const defaultData ={
    passwordRules: [
        (v) => !!v || "the password is required",
        (v) => (v && v.length >= 4) || "Password must have 4 characters at least",
      ],
     
      passwordVisibility: {
        icon: "mdi-eye",
        type: "password",
      },
      confirmPasswordVisibility: {
        icon: "mdi-eye",
        type: "password",
    },
    valid: true,
    modal: false,
    ModalModel: {},
    loading: false,
    loader: true,
    search: "",
    errors: "",
    chapiterTypes: ["Article", "Pdf", "Link", "Video", "Live"],
}

export const defaultMethod = {
   
    managePasswordVisibility(current) {
        if (current.type == "password") {
          current.icon = "mdi-eye-off";
          current.type = "text";
        } else {
          current.icon = "mdi-eye";
          current.type = "password";
        }
    },
    requiredRules(field) {
        return [(v) => !!v || `${field} is required`]
    }
}