import axios from 'axios'

const state ={
    Students :[],
    Student :{}
   
}

const getters = {
    StudentsState : (state)=>state.Students,
    StudentState : (state)=>state.Student
    
}

const mutations ={
    
    GET_STUDENTS(state,data){
        state.Students = data
    },  
    GET_STUDENT(state,data){
        state.Student = data
    },  
    
   
    
    
}

const actions = {
    getStudents({commit},schema){
        
        return new Promise((resolve,reject)=>{
            axios
                .get(`/admin/student/${schema.level}/${schema.department}/${schema.session}`)
                    .then(res=>{
                        commit('GET_STUDENTS',res.data)
                        resolve(res.data)
                    })
                    .catch(err=>{
                        reject(err)
                    })
                })
    },
    
    getStudent({commit},id){
       return new Promise((resolve,reject)=>{
            axios
                .get('/admin/student/'+id)
                    .then(res=>{
                        commit('GET_STUDENT',res.data)
                        resolve(res.data)
                    })
                    .catch(err=>{
                        reject(err)
                    })
       }) 
        
    },
    
}

export default {
    state,
    getters,
    mutations,
    actions
}