<template>
  <div class="bg-tkp h-100">
    <div class=" " id="admin-profil" v-if="loader == false && FacultyState.faculty">
      <div class="px-md-12 py-md-5 px-2 py-2">
        <div class="d-md-flex justify-space-between">
          <h3 class="text-h5">Dashbord / <span class="main--text">Faculty</span></h3>
          <div class="d-flex flex-column flex-md-row">
            <a
              href=""
              @click.prevent="Refresh()"
              class="px-3 py-3 mr-md-3 my-3 elevation-1 transparent-bg rounded-lg white--text"
              >Refrech</a
            >
            <a
              href=""
              v-if="AdminProfilState.function == 'Director'"
              @click.prevent="Edit()"
              class="px-3 py-3 my-3 white elevation-1 rounded-pill blue-grey--text"
              >Edit Faculty</a
            >
            <a
              href=""
              v-if="AdminProfilState.function == 'Director'"
              @click.prevent="Delete()"
              class="px-3 py-3 ml-md-3 my-3 red elevation-1 rounded-pill white--text"
              >Delete Faculty</a
            >
          </div>
        </div>

        <div class="mt-5" v-if="FacultyState.faculty.name">
          <v-container class="white px-1 px-md-5 py-5 rounded" fluid>
            <div class="border-b pb-2">
              <h1 class="text-h5">
                {{ FacultyState.faculty.name }}
                <span class="main--text">({{ FacultyState.faculty.acronym }})</span>
              </h1>
              <p>{{ FacultyState.faculty.description }}</p>
            </div>
            <div>
              <h3 class="">
                <span class="main--text"
                  >{{ FacultyState.faculty.mainProfessors.length }}
                </span>
                Main Professor
              </h3>
              <v-list-item
                class="py-3 mt-2 mb-8"
                v-for="mainProfessor in FacultyState.faculty.mainProfessors"
                :key="mainProfessor._id"
              >
                <v-list-item-avatar>
                  <v-img :src="mainProfessor.profilPicture" />
                </v-list-item-avatar>

                <v-list-item-content>
                  <v-list-item-title
                    >{{ mainProfessor.firstname }}
                    {{ mainProfessor.lastname }}</v-list-item-title
                  >

                  <v-list-item-subtitle
                    v-text="mainProfessor.email"
                  ></v-list-item-subtitle>
                </v-list-item-content>

                <v-list-item-action>
                  <v-btn icon>
                    <v-icon color="grey lighten-1">mdi-information</v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>

              <v-card
                class="mx-auto py-3 my-3 elevation-1"
                v-if="AdminProfilState.function == 'Director'"
              >
                <v-toolbar flat color="transparent">
                  <v-toolbar-title>Manage Main professor</v-toolbar-title>
                  <v-spacer></v-spacer>
                </v-toolbar>

                <v-container class="py-0">
                  <v-row align="center" justify="start">
                    <v-col
                      v-for="(selection, i) in selections"
                      :key="selection"
                      class="shrink"
                    >
                      <v-chip
                        :disabled="loading"
                        close
                        @click:close="selected.splice(i, 1)"
                      >
                        {{ selection }}
                      </v-chip>
                    </v-col>

                    <v-col v-if="!allSelected" cols="12">
                      <v-text-field
                        ref="search"
                        v-model="search"
                        full-width
                        hide-details
                        label="Search"
                        single-line
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>

                <v-divider v-if="!allSelected"></v-divider>

                <v-list>
                  <template v-for="item in FacultyState.mainProfessors.names">
                    <v-list-item
                      v-if="!selected.includes(item)"
                      :key="item"
                      :disabled="loading"
                      @click="selected.push(item)"
                    >
                      <v-list-item-avatar>
                        <v-icon>mdi-account</v-icon>
                      </v-list-item-avatar>
                      <v-list-item-title v-text="item"></v-list-item-title>
                    </v-list-item>
                  </template>
                </v-list>

                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <div>
                    <v-btn
                      color=""
                      :loading="loading"
                      class="main white--text"
                      @click="editFacultyMainProfessor"
                      >Update main professor</v-btn
                    >
                  </div>
                </v-card-actions>
              </v-card>
            </div>
          </v-container>
        </div>
      </div>
    </div>
    <div v-else-if="loader == false && !FacultyState.faculty">
      <can-not-find what="this Faculty" />
    </div>
    <v-slide-x-transition>
      <v-dialog v-model="Modal" max-width="50rem">
        <div class="modals white">
          <div class="d-flex justify-end">
            <a
              href=""
              @click.prevent="Modal = false"
              class="text-h4 mr-4 mt-1 cursor-pointer"
              >X</a
            >
          </div>
          <div class="px-3 px-md-12 py-3 py-md-8">
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="px-10 py-12 white rounded ml-sm-10"
            >
              <h1>{{ ModalText.title }}</h1>

              <div v-if="errors">
                <p class="red--text">{{ errors }}</p>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.name"
                  :rules="nameRules"
                  label="Name"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  v-model="ModalModel.acronym"
                  :rules="acronymRules"
                  label="Acronym"
                  required
                ></v-text-field>
              </div>
              <div class="d-flex justify-space-between">
                <v-text-field
                  :rules="descriptionRules"
                  label="Description"
                  v-model="ModalModel.description"
                  required
                ></v-text-field>
              </div>

              <div class="text-center">
                <v-btn
                  :loading="loading"
                  :disabled="!valid"
                  color=""
                  class="main mr-4 white--text"
                  @click="validates"
                  width="100%"
                >
                  {{ ModalText.btn }}
                </v-btn>
              </div>
            </v-form>
          </div>
        </div>
      </v-dialog>
    </v-slide-x-transition>

    <v-slide-y-transition>
      <v-dialog v-model="Dialog" max-width="50rem">
        <v-card class="modal-alert">
          <v-card-title class="headline red white--text">
            Are you sure to delete this faculty ?
          </v-card-title>

          <v-card-text class="black--text my-3">
            Every department,student and course about this faculty will be deleted
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="bg-secondaire white--text" text @click="CancelDelete()">
              Cancel
            </v-btn>
            <v-btn class="red white--text" text @click="ConfirmDelete(deleteId)">
              Delete
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-slide-y-transition>

    <v-snackbar v-model="snackbar">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
      </template>
    </v-snackbar>
    <circulate-load v-if="loader" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import CanNotFind from "../../components/universal/CanNotFind.vue";
import CirculateLoad from "../../components/universal/circulateLoad.vue";

export default {
  components: {
    CanNotFind,
    CirculateLoad,
  },
  computed: {
    ...mapGetters(["FacultyState", "AdminProfilState", "ErrorBag"]),
    allSelected() {
      return this.selected.length === this.FacultyState.mainProfessors.names.length;
    },
    categories() {
      const search = this.search.toLowerCase();

      if (!search) return this.FacultyState.mainProfessors.names;

      return this.FacultyState.mainProfessors.names.filter((item) => {
        const text = item.text.toLowerCase();

        return text.indexOf(search) > -1;
      });
    },
    selections() {
      const selections = [];
      for (const selection of this.selected) {
        selections.push(selection);
      }
      return selections;
    },
  },
  methods: {
    ...mapActions([
      "getFaculty",
      "editFaculty",
      "deleteFaculty",
      "updateFacultyMainProfessors",
    ]),
    async Refresh() {
      this.loader = true;
      try {
        await this.getFaculty(this.$route.params.id);
        this.message = "Faculty refresh successfully";
      } catch (error) {
        this.message = this.ErrorBag.default;
      }
      this.snackbar = true;
      this.loader = false;
    },
    async Initialize() {
      this.loading = true;
      this.loader = true;
      try {
        await this.getFaculty(this.$route.params.id);
      } catch (error) {
        this.message = this.ErrorBag.default;
        this.snackbar = true;
      }
      this.loading = false;
      this.loader = false;
      this.ModalText.title = "Add new Faculty";
      this.ModalText.btn = "Add";
      this.ModalModel = {};
      this.mainProfSelected();
    },

    Edit() {
      this.Modal = true;
      this.ModalModel = this.FacultyState.faculty;
      this.ModalText.title = "Edit Faculty";
      this.ModalText.btn = "Update";
    },
    Delete() {
      this.Dialog = true;
      this.deleteId = this.FacultyState._id;
    },
    CancelDelete() {
      this.deleteId = "";
      this.Dialog = false;
    },
    async ConfirmDelete(id) {
      try {
        await this.deleteFaculty(id);
        this.message = "Faculty deleted successfully";
      } catch (error) {
        this.message = "Failed to delete this faculty";
      }
      this.snackbar = true;
      this.CancelDelete();
      this.Initialize();
    },
    async validates() {
      if (this.$refs.form.validate()) {
        this.loading = true;

        if (this.ModalModel._id) {
          // update
          const ModalModelUpdate = {
            data: {
              name: this.ModalModel.name,
              acronym: this.ModalModel.acronym,
              description: this.ModalModel.description,
            },
            _id: this.ModalModel._id,
          };
          try {
            await this.editFaculty(ModalModelUpdate);
            this.message = "Faculty edit successfully";
          } catch (error) {
            this.message = "Failed to edit this faculty";
          }
        }
        this.loading = false;
        this.Modal = false;
        this.snackbar = true;
        this.Initialize();
      }
    },
    async editFacultyMainProfessor() {
      const mainProfessors = this.getMainProfessorFromName(this.selected);
      const faculty = {
        _id: this.$route.params.id,
        mainProfessors: mainProfessors,
      };
      this.loading = true;
      try {
        await this.updateFacultyMainProfessors(faculty);
        this.message = "Main professors updated successfully";
      } catch (error) {
        this.message = "Can not updated main professors , try later";
      }
      this.snackbar = true;
      this.Initialize();
    },
    getMainProfessorFromName(names) {
      let result = [];
      for (let c = 0; c < names.length; c++) {
        let name = names[c];

        for (let i = 0; i < this.FacultyState.mainProfessors.list.length; i++) {
          let professor = this.FacultyState.mainProfessors.list[i];
          if (professor.firstname + " " + professor.lastname == name) {
            result.push(professor._id);
            break;
          }
        }
      }
      return result;
    },

    remove(item) {
      this.chips.splice(this.chips.indexOf(item), 1);
      this.chips = [...this.chips];
    },
    mainProfSelected() {
      let result = [];
      for (let i = 0; i < this.FacultyState.faculty.mainProfessors.length; i++) {
        const prof = this.FacultyState.faculty.mainProfessors[i];
        result.push(prof.firstname + " " + prof.lastname);
      }

      this.selected = result;
    },
  },
  mounted() {
    this.Initialize();
  },
  watch: {
    selected() {
      this.search = "";
    },
  },
  data() {
    return {
      deleteId: "",
      Dialog: "",
      snackbar: false,
      message: "",
      errors: "",
      loading: false,
      loader: true,
      valid: true,
      nameRules: [(v) => !!v || "name is required"],
      acronymRules: [(v) => !!v || "acronym  is required"],
      descriptionRules: [(v) => !!v || "description  is required"],
      selected: [],
      chips: [],

      Modal: false,
      ModalText: {
        title: "Add new Faculty",
        btn: "Add",
      },
      ModalModel: {
        name: "",
        acronym: "",
        description: "",
        _id: "",
      },
      search: "",
      headers: [
        { text: "Name", value: "name" },
        { text: "Accronyme", value: "acronym" },
        { text: "Description", value: "description" },
        { text: "Main professor", value: "mainProfessor" },
        { text: "Action", value: "action" },
      ],
    };
  },
};
</script>

<style></style>
