<template>
  <div>
    <div class="text-center py-12">
      <v-icon class="text-h1 main--tex">mdi-incognito-off</v-icon>
      <p v-if="programed == false" class="text-h4 mt-12">
        This {{ document }} is not programed
      </p>
      <p v-else-if="day != undefined" class="text-h5 mt-12">
        <span v-html="message()"></span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    document: {
      required: true,
    },
    programed: {
      default: false,
    },
    day: {
      default: undefined,
    },
    duration: {
      default: 30,
    },
  },
  methods: {
    message() {
      //   const date = this.getDate(this.day, this.duration);

      let startDate = new Date(this.day);
      let duration = Number(this.duration); // in minute
      const months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];

      const days = [
        "Sunday",
        "Monday",
        "Thuesday",
        "Wednesday",
        "Tueday",
        "Friday",
        "Saterday",
      ];

      const day = days[startDate.getDay()];
      const year = startDate.getFullYear();
      const month = months[startDate.getMonth()];
      const date = startDate.getDate();

      let durationDate = new Date(startDate);
      durationDate = durationDate.getTime() + duration * 60000;
      durationDate = new Date(durationDate);
      const DocumentDate =
        day +
        " " +
        date +
        " " +
        month +
        " " +
        year +
        ` from <span class='main--text'> ${startDate.getHours()}:${
          startDate.getMinutes() < 10
            ? "0" + startDate.getMinutes()
            : startDate.getMinutes()
        } </span> to  <span class='main--text'>  ${durationDate.getHours()}:${
          durationDate.getMinutes() < 10
            ? "0" + durationDate.getMinutes()
            : durationDate.getMinutes()
        } </span>`;

      const today = new Date();
      let introduceMessage = "";
      const remaining = startDate.getTime() - today.getTime();
      if (durationDate.getTime() < today.getTime()) {
        introduceMessage = `This ${this.document}  is already passed the   `;

        if (remaining > 0) {
          setTimeout(() => {
            location.reload();
          }, remaining);
        }
      } else {
        introduceMessage = `This ${this.document}  will start the `;
      }
      return `${introduceMessage} ${DocumentDate}`;
    },
  },
};
</script>

