<template>
  <div class="white">
    <div class="h-100 bg-dash">
      <div class="container">
        <div>
          <div>
            <h3 class="">My profil</h3>
          </div>
          <!-- {{ StudentProfilState }} -->
          <v-row class="mt-">
            <v-col cols="12" md="3" class="rounded">
              <v-img
                class="admissionIdPictureContainer rounded"
                :src="StudentProfilState.idPicture"
                alt=""
              />
            </v-col>
            <v-col cols="12" md="8" class="white px-5 py-2 rounded ml-md-6">
              <v-row>
                <v-col class="border-r">
                  <div class="">
                    <p>
                      <span class="bold">Surname :</span> {{ StudentProfilState.surname }}
                    </p>
                    <p>
                      <span class="bold">Othernames :</span>
                      {{ StudentProfilState.othernames }}
                    </p>
                    <p>
                      <span class="bold">Registration Number:</span>
                      {{ StudentProfilState.registration_number }}
                    </p>
                    <p>
                      <span class="bold">Email:</span>
                      {{ StudentProfilState.email }}
                    </p>
                    <p>
                      <span class="bold">Departement :</span>
                      {{ StudentProfilState.other_informations.department.acronym }}
                    </p>
                    <p>
                      <span class="bold">Level :</span> {{ StudentProfilState.degre }}
                    </p>

                    <p>
                      <span class="bold">Address :</span> {{ StudentProfilState.address }}
                    </p>
                    <p>
                      <span class="bold">Phone :</span> {{ StudentProfilState.phone }}
                    </p>
                  </div>
                </v-col>
                <v-col cols="12" md="5" order="2" class=" ">
                  <p>
                    <span class="bold">Birthday:</span>
                    {{ StudentProfilState.birthdayDate }}
                  </p>
                  <p>
                    <span class="bold">Birthday place:</span>
                    {{ StudentProfilState.birthdayPlace }}
                  </p>
                  <p>
                    <span class="bold">Country :</span> {{ StudentProfilState.country }}
                  </p>
                  <p><span class="bold">State :</span> {{ StudentProfilState.state }}</p>
                  <p>
                    <span class="bold">Nationality :</span>
                    {{ StudentProfilState.nationality }}
                  </p>
                  <p>
                    <span class="bold">Additional Language :</span>
                    <span
                      v-for="language in StudentProfilState.languages"
                      :key="language"
                    >
                      {{ language }} ,</span
                    >
                  </p>
                </v-col>
              </v-row>
            </v-col>
          </v-row>

          <v-row class="mt-5">
            <v-col cols="12" md="4" class="white rounded">
              <p class="bold">Scolarite</p>
              <v-row>
                <v-col cols="4">
                  <div class="d-flex justify-center align-center">
                    <v-progress-circular
                      :rotate="270"
                      :size="100"
                      :width="15"
                      :value="vald"
                      color="orange"
                    >
                      {{ vald }}%
                    </v-progress-circular>
                  </div>
                </v-col>
                <v-col cols="7" order="1" offset-md="1">
                  <div class="mt-5">
                    <p>
                      <span class="orange rounded-circle mr-2 px-3 py-1"></span>Scolarite
                      payee
                    </p>
                    <p>
                      <span class="bg-dash rounded-circle mr-2 px-3 py-1"></span>Scolarite
                      restante
                    </p>
                  </div>
                </v-col>
              </v-row>
              <div class="mt-1">
                <p><span class="bold">Scolarite payee:</span> 360.000 FCFA</p>
                <p><span class="bold">Scolarite restante :</span> 40.000 FCFA</p>
              </div>
            </v-col>
          </v-row>
        </div>
      </div>
      <bulletin />
    </div>
  </div>
</template>

<script>
import Bulletin from "../../components/student/Bulletin.vue";

import { mapGetters } from "vuex";
export default {
  components: {
    Bulletin,
  },
  computed: {
    ...mapGetters(["StudentProfilState"]),
  },
  methods: {
    circleEffect() {
      let i = this.vald;
      this.vald = 0;
      let effect = setInterval(() => {
        this.vald += 12;

        if (i <= this.vald) {
          this.vald = i;
          clearInterval(effect);
        }
      }, 10);
    },
  },
  mounted() {
    this.circleEffect();
  },
  data() {
    return {
      vald: 90,
      PayeModal: false,
    };
  },
};
</script>

<style></style>
